<template>
  <div id='normalizationCheck'>
     <div class='nc-left'>
        <div class="nc-title">文书<a href="javascript:void(0)" style="cursor:text;">忽略所有</a></div>
        <ul>
          <li style="margin-top:10px;color:#999;font-size:12px;">
              <div>文书名称</div>
              <div>检查结果</div>
              <div>操作</div>
            </li>
        </ul>
        <ul style="max-height:670px;overflow-y: scroll">
            <!-- <li v-for="(item,index) in wsTypeList" :key="index" v-if="item.wsTypeIsNeeded === 'Y'"> -->
            <li v-for="(item,index) in wsTypeList" :key="index" v-if="item.wsTypeIsNeeded === 'Y'" style="color:#666">
              <div v-bind:title="item.wsType">{{item.wsType}}</div>
              <div><i class="icon iconfont" style="color:#23ca85;" v-if="item.wsIsUpload === 'Y'">&#xe6cd;</i><i class="icon iconfont" style="color:#f6c92e;" v-if="item.wsIsUpload === 'N'">&#xe6de;</i><span>{{item.wsIsUpload === 'N'?'未上传':'已上传'}}</span></div>
              <div style="color:#42a0f8;">忽略</div>
            </li>
            <div v-if="wsTypeList.length == 0" class="no-result">
                <img src="../assets/img/no_result.png" alt="">
                <p style="text-align: center;">该列表暂无数据！</p>
            </div>
        </ul>
     </div>
     <div class='nc-right'>
        <div class="nc-title">案卡<a href="">忽略所有</a></div>
     </div>
  </div>
</template>

<script>
import { getAjaxPromise } from '@/api/admin'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'normalizationCheck',
  mounted() {
    this.$nextTick(() => {
      // 获取检查文书规范性列表
      this.checkWsNormalization()
    })
  },
  data() {
    return {
      msg: '卷宗',
      caseNumber: this.caseObj.caseNumber,
      wsTypeList: [],
      currentPage1: 1,
      totalRow: 0
    }
  },
  created() {},
  methods: {
    handleCurrentChange(val) {
      this.getOcrList(val, this.searchKeyWord)
    },
    checkWsNormalization() {
      // this.caseObj.caseNumber = '42011420180035655'
      const param = {
        caseNumber: this.caseObj.caseNumber,
        caseFlowType: this.caseObj.caseFlowType
      }
      const checkWsNormalizationPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/ocrFileWs/checkWsNormalization',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      checkWsNormalizationPromise.then(res => {
        console.log(res.data.code)
        if (
          res.data.code === '200' &&
          res.data.data &&
          res.data.data.WsTypeList.length > 0
        ) {
          this.wsTypeList = res.data.data.WsTypeList
        } else if (res.data.code === '500') {
          this.$message({
            message: res.data.msg,
            type: 'error',
            duration: 2000
          })
        }
      })
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
#normalizationCheck {
  width: 100%;
  height: 100%;
  ::-webkit-scrollbar {
    width: 0px;
  }
  .nc-left {
    float: left;
    width: calc(50% - 12px);
    height: 100%;
    background-color: white;
    ul {
      width: calc(100% - 24px);
      list-style: none;
      margin: 0;
      padding: 0;
      padding-left: 24px;
      li {
        height: 32px;
        line-height: 32px;
        div {
          float: left;
          font-size: 12px;
          &:nth-child(1) {
            width: calc(50% - 70px);
            margin-right: 10px;
            max-width: calc(50% - 70px);
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          &:nth-child(2) {
            width: calc(50% - 70px);
            margin-right: 10px;
            max-width: calc(50% - 70px);
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            i {
              font-size: 14px;
              margin-right: 5px;
              vertical-align: middle;
            }
            span {
              vertical-align: middle;
            }
          }
          &:nth-child(3) {
            width: 120px;
          }
        }
      }
    }
  }
  .nc-right {
    float: right;
    width: calc(50% - 12px);
    height: 100%;
    background-color: white;
  }
  .nc-title {
    height: 48px;
    line-height: 48px;
    border-bottom: 1px solid #ddd;
    padding: 0 24px;
    color: #333;
    font-weight: 400;
    a {
      float: right;
      color: #42a0f8;
    }
  }
  .no-result {
    text-align: center;
    margin-top: 200px;
    img {
      margin-bottom: 10px;
    }
    p {
      margin: 0;
    }
  }
}
</style>



