<template>
  <div id="main-content-inner">
  <!--pdf预览组件（组件内部包括一个卷宗树形列表组件） begin-->
  <!--pdf预览组件 end-->
  <div id="left-main-wrap"><preview-pdf :caseObj="caseObj"></preview-pdf></div>
  <div id="right-main-wrap">
    <el-steps :active="active" finish-status="success" align-center id="steps-wrap">
        <el-step title="嫌疑人基本信息采集" description=""></el-step>
        <el-step title="案件基本信息采集" description=""></el-step>
        <el-step title="事实认定"></el-step>
        <el-step title="证据分析"></el-step>
        <el-step title="处理意见"></el-step>
        <el-step title="生成文书"></el-step>
    </el-steps>
    <div id="mian-form-wrap">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive" :caseObj="caseObj"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive" :caseObj="caseObj"></router-view>
    </div>
  </div>
  </div>
</template>

<script>
// import Router from 'vue-router'
import PreviewPdf from '@/components/PreviewPdf'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'caseHandle',
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.active = to.meta.index
    })
  },
  beforeRouteUpdate(to, from, next) {
    this.active = to.meta.index
    next()
  },
  components: {
    PreviewPdf
  },
  data() {
    return {
      active: 0
    }
  }
}
</script>

<style lang="scss">
#main-content-inner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

#right-main-wrap {
  width: 53.57906%;
  height: 100%;
  //   flex-basis: 44.875%;
  //嵌套
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#left-main-wrap {
  //   flex-basis: 44.875%;
  width: calc(100% - 53.57906% - 20px);
  height: 100%;
  background-color: #fff;
}

#steps-wrap {
  background-color: #fff;
  padding: 20px 0;
}

#mian-form-wrap {
  //background-color: #fff;
  flex-grow: 1;
  padding-top: 20px;
  position: relative;
}
.step-wrap {
  width: 100%;
  height: calc(100% - 20px);
  background-color: #fff;
  position: absolute;
  .step-header-wrap {
    height: 47px;
    border-bottom: 1px solid #ddd;
    line-height: 47px;
    h2 {
      margin: 0;
      font-size: 16px;
      font-weight: normal;
      text-indent: 20px;
      font-weight: bold;
    }
  }
  .step-body-wrap {
    height: calc(100% + 15px);
    //background-color: red;
    //height: calc(100% - 48px);
    padding: 15px;
    box-sizing: border-box;
    overflow: auto;
  }
  .step-body-without-tabs-wrap {
    height: calc(100% - 96px);
    padding: 15px;
    box-sizing: border-box;
    overflow: auto;
  }
  .step-footer-wrap {
    height: 64px;
    line-height: 64px;
    background-color: #fff;
    border-top: 1px solid #ddd;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    text-align: right;
    padding: 0 15px;
    box-sizing: border-box;
  }
}

.step-wrap {
  .h3-title {
    font-size: 14px;
    color: #606266;
  }
  .el-tabs__nav-wrap::after {
    height: 1px;
    background-color: #ddd;
  }
  .el-tabs--top {
    height: calc(100% - 62px);
    overflow: hidden;
    .el-tabs__header {
      margin-bottom: 0;
      padding-left: 15px;
    }
    .el-tabs__content {
      border-top: 1px solid #ddd;
      height: calc(100% - 47px);
      .el-tab-pane {
        height: 100%;
      }
    }
  }
  // tabs可以横向卷动时候，不要盖住增加按钮
  .el-tabs__nav-wrap {
    width: calc(100% - 100px);
    height: 47px;
    line-height: 47px;
  }
  // 增加 tab按钮
  .addBtn {
    position: absolute;
    right: 24px;
    top: 0;
    //width: 40px;
    span {
      padding: 14px;
      position: relative;
    }
  }
  //确定按钮定位
  .btn-wrap {
    position: absolute;
    right: 0px;
    top: 0;
  }

  // step3证据tabs
  .evidence-tabs-wrap {
    .el-tabs__nav-wrap {
      width: auto;
    }
    .el-tabs__content {
      // border: 1px solid #ddd;
      // border-bottom: 1px solid #ddd;
    }

    .el-tabs--left.el-tabs--card .el-tabs__item.is-left.is-active {
      background-color: #42a0f8;
      border: 1px solid #42a0f8;
      color: #fff;
    }

    .el-tabs__nav.is-left {
      width: 100px;
    }

    .el-tabs--left .el-tabs__item {
      margin-bottom: 12px;
    }

    .el-tabs--left .el-tabs__item.is-left {
      text-align: center;
    }

    .el-tabs--card {
      > .el-tabs__header {
        border-bottom: none;
      }
    }

    .el-tabs--left.el-tabs--card .el-tabs__item.is-left {
      border: 1px solid #ddd !important;
    }

    .el-tabs__nav.is-left {
      border: none;
    }

    //最后一个垂直的tab文字竖排
    .el-tabs__nav.is-left {
      > div.el-tabs__item {
        // &:nth-child(4) {
        height: auto;
        white-space: normal;
        line-height: 1;
        padding: 10px 15px;
        // }
      }
    }
    .el-tabs--left .el-tabs__item:nth-child(2) {
      padding-left: 0;
    }
    .el-tabs__item.is-top:last-child {
      padding-right: 0;
    }
  }

  .evidence-item-tabs-wrap {
    .el-tabs__nav-wrap {
      width: calc(100% - 100px);
      height: 47px;
      line-height: 47px;
    }

    .el-tabs__item {
    }
  }

  // 具体证据表单
  .evidence-item-content-wrap {
    padding: 24px 16px;
  }
}
</style>



