<template>
  <div class="uploader-file" :status="status" v-show="file.view !='show'">
    <slot
      :file="file"
      :list="list"
      :status="status"
      :paused="paused"
      :error="error"
      :average-speed="averageSpeed"
      :formated-average-speed="formatedAverageSpeed"
      :current-speed="currentSpeed"
      :is-complete="isComplete"
      :is-uploading="isUploading"
      :size="size"
      :formated-size="formatedSize"
      :uploaded-size="uploadedSize"
      :progress="progress"
      :progress-style="progressStyle"
      :progressing-class="progressingClass"
      :time-remaining="timeRemaining"
      :formated-time-remaining="formatedTimeRemaining"
      :type="type"
      :extension="extension"
      :file-category="fileCategory"
      >
      <div class="uploader-file-progress" :class="progressingClass" :style="progressStyle"></div>
      <div class="uploader-file-info">
        <div class="uploader-file-name">
          <svg class="icon file-uploader-icon" aria-hidden="true">
              <use v-bind:xlink:href="fileCategory"></use>
          </svg>
          {{file.name}}
        </div>
        <div class="uploader-file-size">{{getFileSize}}</div>
        <div class="uploader-file-status">
          <span v-show="status !== 'uploading'">{{statusText}}</span>
          <span v-show="status === 'uploading'">
            <span  class="yofc-file-name">{{progressStyle.progress}}</span>
            <!-- <em>{{formatedAverageSpeed}}</em> -->
          </span>
        </div>
        <div class="uploader-file-actions">
          <span class="uploader-file-remove" @click="remove(file)"></span>
          <span class="uploader-file-pause"  @click="pause"></span>
          <span class="uploader-file-resume" @click="resume">️</span>
          <!-- <span class="uploader-file-retry" @click="retry"></span> -->
        </div>
      </div>
    </slot>
  </div>
</template>

<script>
import Uploader from 'zhang-simple-uploader.js'
import { secondsToStr } from '../utils/filter.js'
const events = ['fileProgress', 'fileSuccess', 'fileComplete', 'fileError']
const COMPONENT_NAME = 'uploader-file'

export default {
  name: COMPONENT_NAME,
  props: {
    file: {
      type: Object,
      default() {
        return {}
      }
    },
    list: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      paused: false,
      error: false,
      averageSpeed: 0,
      currentSpeed: 0,
      isComplete: false,
      isUploading: false,
      size: 0,
      formatedSize: '',
      uploadedSize: 0,
      progress: 0,
      timeRemaining: 0,
      type: '',
      extension: '',
      progressingClass: ''
    }
  },
  computed: {
    fileCategory() {
      const iconMap = {
        image: 'icon-tupianwenjian',
        video: 'icon-shipinwenjian',
        audio: 'icon-yinpinwenjian',
        folder: 'icon-wenjianjia',
        doc: 'icon-wordwenjian',
        txt: 'icon-textwenjian',
        excel: 'icon-excelwenjian',
        pdf: 'icon-pdfwenjian',
        ppt: 'icon-pptwenjian',
        zip: 'icon-yasuobaowenjian'
      }
      const extension = this.extension
      const isFolder = this.file.isFolder
      let type = isFolder ? 'folder' : 'unknown'
      const categoryMap = this.file.uploader.opts.categoryMap
      const typeMap = categoryMap || {
        image: ['gif', 'jpg', 'jpeg', 'png', 'bmp', 'webp'],
        video: ['mp4', 'm3u8', 'rmvb', 'avi', 'swf', '3gp', 'mkv', 'flv'],
        audio: ['mp3', 'wav', 'wma', 'ogg', 'aac', 'flac'],
        doc: ['doc', 'docx'],
        txt: ['txt'],
        excel: ['xls', 'xlsx'],
        pdf: ['pdf', 'epub'],
        ppt: ['ppt', 'pptx'],
        zip: ['zip', 'rar', '7z']
      }
      Object.keys(typeMap).forEach(_type => {
        const extensions = typeMap[_type]
        if (extensions.indexOf(extension) > -1) {
          type = _type
        }
      })
      const icon = iconMap[type] || 'icon-qitawenjianhuoweizhiwenjian'
      return `#${icon}`
    },
    progressStyle() {
      const progress = Math.floor(this.progress * 99)
      const style = `translateX(${Math.floor(progress - 99)}%)`
      return {
        progress: `${progress}%`,
        webkitTransform: style,
        mozTransform: style,
        msTransform: style,
        transform: style
      }
    },
    formatedAverageSpeed() {
      return `${Uploader.utils.formatSize(this.averageSpeed)} / s`
    },
    status() {
      const isUploading = this.isUploading
      const isComplete = this.isComplete
      const isError = this.error
      const paused = this.paused
      if (isComplete) {
        this.$root.eventHub.$emit('removeItemFromYmFileList', this.file)
        return 'success'
      } else if (isError) {
        return 'error'
      } else if (isUploading) {
        return 'uploading'
      } else if (paused) {
        return 'paused'
      } else {
        return 'waiting'
      }
    },
    statusText() {
      const status = this.status
      return this.file.uploader.fileStatusText[status] || status
    },
    formatedTimeRemaining() {
      const timeRemaining = this.timeRemaining
      const file = this.file
      if (timeRemaining === Number.POSITIVE_INFINITY || timeRemaining === 0) {
        return ''
      }
      let parsedTimeRemaining = secondsToStr(timeRemaining)
      const parseTimeRemaining = file.uploader.opts.parseTimeRemaining
      if (parseTimeRemaining) {
        parsedTimeRemaining = parseTimeRemaining(
          timeRemaining,
          parsedTimeRemaining
        )
      }
      return parsedTimeRemaining
    },
    getFileSize() {
      let total = this.file.size
      if (this.file.files.length > 0) {
        total = 0
        this.file.files.forEach(file => {
          total += file.size
        })
      }
      let currentSize
      let size
      let unit
      if (total < 1024) {
        unit = 'Kb'
        size = total / 1024
        currentSize = (total * this.progress) / 1024
      } else {
        unit = 'Mb'
        size = total / (1024 * 1024)
        currentSize = (total * this.progress) / (1024 * 1024)
      }
      currentSize = currentSize.toFixed(1)
      size = size.toFixed(1)
      return `${currentSize} / ${size} ${unit} `
    }
  },
  watch: {
    status(newStatus, oldStatus) {
      if (oldStatus && newStatus === 'uploading' && oldStatus !== 'uploading') {
        this.tid = setTimeout(() => {
          this.progressingClass = 'uploader-file-progressing'
        }, 200)
      } else {
        clearTimeout(this.tid)
        this.progressingClass = ''
      }
    }
  },
  methods: {
    _actionCheck() {
      this.paused = this.file.paused
      this.error = this.file.error
      this.isUploading = this.file.isUploading()
    },
    pause() {
      this.file.pause()
      this._actionCheck()
      this._fileProgress()
    },
    resume() {
      this.file.resume()
      this._actionCheck()
    },
    remove(file) {
      const message = this.error
        ? '是否删除？'
        : '当前任务未完成，是否取消上传？'
      this.$confirm(message, '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        cancelButtonClass: 'cancel',
        confirmButtonClass: 'danger',
        center: true,
        showClose: false,
        customClass: 'arDialog'
      })
        .then(() => {
          this.resume()
          this.pause()
          this.file.cancel()
          this.$root.eventHub.$emit('removeItemFromYmFileList', this.file)
        })
        .catch(() => {})
    },
    retry() {
      this.file.retry()
      this._actionCheck()
    },
    fileEventsHandler(event, args) {
      const rootFile = args[0]
      const file = args[1]
      const target = this.list ? rootFile : file
      if (this.file === target) {
        if (this.list && event === 'fileSuccess') {
          return
        }
        this[`_${event}`].apply(this, args)
      }
    },
    _fileProgress() {
      this.progress = this.file.progress()
      this.averageSpeed = this.file.averageSpeed
      this.currentSpeed = this.file.currentSpeed
      this.timeRemaining = this.file.timeRemaining()
      this.uploadedSize = this.file.sizeUploaded()
      this._actionCheck()
    },
    _fileSuccess() {
      this._fileProgress()
      this.error = false
      this.isComplete = true
      this.isUploading = false
      this.file.cancel()
      // this.$nextTick(function() {
      //   this.remove(this.file)
      // })

      // this.$root.eventHub.$emit('fileUploadSuccess', this.file)
    },
    _fileComplete() {
      this._fileSuccess()
    },
    _fileError() {
      this._fileProgress()
      this.error = true
      this.isComplete = false
      this.isUploading = false
    }
  },
  mounted() {
    const staticProps = ['paused', 'error', 'averageSpeed', 'currentSpeed']
    const fnProps = [
      'isComplete',
      'isUploading',
      {
        key: 'size',
        fn: 'getSize'
      },
      {
        key: 'formatedSize',
        fn: 'getFormatSize'
      },
      {
        key: 'uploadedSize',
        fn: 'sizeUploaded'
      },
      'progress',
      'timeRemaining',
      {
        key: 'type',
        fn: 'getType'
      },
      {
        key: 'extension',
        fn: 'getExtension'
      }
    ]
    staticProps.forEach(prop => {
      this[prop] = this.file[prop]
    })
    fnProps.forEach(fnProp => {
      if (typeof fnProp === 'string') {
        this[fnProp] = this.file[fnProp]()
      } else {
        this[fnProp.key] = this.file[fnProp.fn]()
      }
    })

    const handlers = (this._handlers = {})
    const eventHandler = event => {
      handlers[event] = (...args) => {
        this.fileEventsHandler(event, args)
      }
      return handlers[event]
    }
    events.forEach(event => {
      this.file.uploader.on(event, eventHandler(event))
    })
  },
  destroyed() {
    events.forEach(event => {
      this.file.uploader.off(event, this._handlers[event])
    })
    this._handlers = null
  }
}
</script>

<style lang="scss">
.uploader-file {
  position: relative;
  height: 49px;
  line-height: 49px;
  overflow: hidden;
  border-bottom: 1px solid #e8e8e8;

  &[status='waiting'] .uploader-file-pause,
  &[status='uploading'] .uploader-file-pause {
    display: block;
  }
  &[status='paused'] .uploader-file-resume {
    display: block;
  }
  &[status='error'] .uploader-file-retry {
    display: block;
  }
  &[status='success'] .uploader-file-remove {
    display: block;
  }
  &[status='error'] .uploader-file-progress {
    background: #fff3f2;
  }

  .uploader-file-progress {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #e2f1fd;
    transform: translateX(-100%);
  }
  .uploader-file-progressing {
    transition: all 0.4s linear;
  }
  .uploader-file-info {
    position: relative;
    z-index: 1;
    height: 100%;
    overflow: hidden;
  }
  .uploader-file-info:hover {
    background-color: rgba(240, 240, 240, 0.2);
  }
  .uploader-file-info i,
  .uploader-file-info em {
    font-style: normal;
  }
  .uploader-file-name,
  .uploader-file-size,
  .uploader-file-meta,
  .uploader-file-status,
  .uploader-file-actions {
    float: left;
    position: relative;
    height: 100%;
  }
  .uploader-file-name {
    width: 200px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-indent: 10px;

    svg {
      width: 24px;
      height: 24px;
      position: relative;
      top: 2px;
    }
  }
  .uploader-file-size {
    width: 140px;
    text-indent: 10px;
  }
  .uploader-file-status {
    width: 80px;
    text-indent: 20px;
  }
  .uploader-file-actions {
    width: 50px;
    .uploader-file-pause {
      background-position-y: 0;
    }
    .uploader-file-resume {
      background-position-y: -17px;
    }
    .uploader-file-remove {
      background-position-y: -34px;
      display: inline-block;
    }
  }
  .uploader-file-actions > span {
    float: right;
    position: relative;
    display: none;
    width: 16px;
    height: 16px;
    margin-top: 16px;
    margin-right: 8px;
    cursor: pointer;
    background: url('data:image/png;base64,')
      no-repeat 0 0;
  }
  .uploader-file-actions > span:hover {
    background-position-x: -21px;
  }
  .yofc-file-name {
    white-space: nowrap;
    overflow: hidden;
  }
}
</style>



