import Vue from 'vue'
import Router from 'vue-router'
import Root from '@/pages/Root'
import Admin from '@/pages/Admin'
import CaseFileLibary from '@/pages/CaseFileLibrary'
import Dossier from '@/pages/Dossier'
import NormalizationCheck from '@/pages/NormalizationCheck'
// 案件办理模块
import CaseHandle from '@/modules/caseHandle/CaseHandle.vue'
import caseHandleRouterConfig from '@/modules/caseHandle/router'

import CaseList from '@/pages/CaseList'
import FilesRepot from '@/pages/FilesRepot'
import AutoRecognition from '@/pages/AutoRecognition'
import TweakUp from '@/pages/TweakUp'
import Doc from '../pages/Doc'
import {
  store as step3SharedStore
} from '@/utils/global-store'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/edossier_h5_case_manage/',
  routes: [{
    name: 'admin',
    path: '/',
    component: Admin,
    meta: {
      keepAlive: true
    },
    children: [{
      path: '/caseList',
      name: 'caseList',
      component: CaseList,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/filesRepot',
      name: 'filesRepot',
      component: FilesRepot,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/autoRecognition',
      name: 'autoRecognition',
      component: AutoRecognition,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/tweakUp',
      name: 'tweakUp',
      component: TweakUp,
      meta: {
        keepAlive: true
      }
    }
    ],
    redirect: '/caseList'
  },
  {
    name: 'root',
    path: '/root',
    component: Root,
    children: [
      // 案件文件库
      {
        path: '/caseFileLibary',
        name: 'caseFileLibary',
        meta: {
          keepAlive: true
        },
        component: CaseFileLibary
      },
      // 卷宗管理
      {
        path: '/dossier',
        name: 'dossier',
        meta: {
          keepAlive: true
        },
        component: Dossier
      },
      // 案件办理
      {
        path: '/caseHandle',
        name: 'caseHandle',
        component: CaseHandle,
        // 路由独享守卫
        beforeEnter: (to, from, next) => {
          step3SharedStore.setHasSaved(false)
          step3SharedStore.setStep3AllDatas([])
          next()
        },
        redirect: '/caseHandle/step1',
        children: caseHandleRouterConfig
      },
      // 规范性检查
      {
        path: '/normalizationCheck',
        name: 'normalizationCheck',
        component: NormalizationCheck
      }
    ],
    redirect: '/caseFileLibary'
  },
    // 开发文档
  {
    name: 'doc',
    path: '/doc',
    component: Doc,
    children: []
  }
  ]
})



