import { Base64 } from 'js-base64'
// 依赖 base64
window.localCache = {
  setItem: function(key, value) {
    value += ''
    try {
      if (Base64) {
        LocalCache.addStorage(key, Base64.encode(value))
      } else {
        LocalCache.addStorage(key, value)
      }
    } catch (error) {
      localStorage.setItem(key, value)
    }
  },
  getItem: function(key) {
    try {
      if (Base64) {
        return Base64.decode(LocalCache.getStorage(key))
      } else {
        return LocalCache.getStorage(key)
      }
    } catch (error) {
      return localStorage.getItem(key)
    }
  },
  removeItem: function(key) {
    try {
      LocalCache.removeStorage(key)
    } catch (error) {
      localStorage.removeItem(key)
    }
  }
}
window.fileAction = {
  UploadSuccess: function(str) {
    return str
  },
  fileList: function(str) {
    return str
  }
}
window.OpenCase = function(data) {
  return data
}



