import _ from 'lodash'
export let store = {
  debug: false,
  step3AllDatas: [],
  state: {
    currentFactId: '',
    currentEvidenceFixedClassifyId: '',
    currentEvidenceId: '',
    currentEvidenceSource: [
      //     {
      //   ocrFileId: '1',
      //   ocrFileName: 'test.png',
      //   sourceSort: 1,
      //   pageDes: ''
      // }
    ]
  },
  // 上次选择的是保存：true 还是 未保存/取消：false
  // 初始化的时候，上次的结果是保存过的
  hasSaved: false,
  // 证据标题缓存
  evidenceTitleCacheMap: {},

  // 固定分类数组
  caseMgtEvidenceClassifyVoList: [],
  setHasSaved(bool) {
    this.hasSaved = bool
  },
  setCaseMgtEvidenceClassifyVoList(arr) {
    this.caseMgtEvidenceClassifyVoList = _.clone(arr)
  },
  setEvidenceTitleCacheMapByKey(key, value) {
    this.evidenceTitleCacheMap[key] = value
  },
  setStep3AllDatas(arr) {
    if (this.debug) console.log('setStep3AllDatas triggered with', arr)
    this.step3AllDatas = _.cloneDeep(arr)
  },
  setCurrentFactIdAction(id) {
    if (this.debug) console.log('setCurrentFactIdAction triggered with', id)
    this.state.currentFactId = id
  },
  setCurrentEvidenceFixedClassifyIdAction(id) {
    if (this.debug) console.log('setCurrentEvidenceFixedClassifyIdAction triggered with', id)
    this.state.currentEvidenceFixedClassifyId = id
  },
  setCurrentEvidenceIdAction(id) {
    if (this.debug) console.log('setCurrentEvidenceIdAction triggered with', id)
    this.state.currentEvidenceId = id
  },
  addToCurrentEvidenceSource(obj) {
    if (this.debug) console.log('addToCurrentEvidenceSource triggered with', obj)
    this.state.currentEvidenceSource.push({
      ocrFileId: obj.ocrFileId,
      ocrFileName: obj.ocrFileName,
      sourceSort: obj.sourceSort,
      pageDes: obj.pageDes
    })
  },
  clearCaseNumberInfoAction() {
    if (this.debug) console.log('clearCaseNumberInfoAction triggered')
    this.state = {
      currentFactId: '',
      currentEvidenceId: '',
      currentEvidenceSource: []
    }
  }
}



