<template>
  <div ref="uploader-list" class="uploader-list dossier-uploader-list">
    <header v-drag class="uploader-list-header">
      <span>正在上传{{fileList.length}}项</span>
      <span class="uploader-list-header-btns">
        <span v-if="shrink" class="btn-shrink" title="收缩列表" @click="shrinkFileList"><i class="iconfont">&#xe7c1;</i></span>
        <span v-else class="btn-shrink" title="展开列表" @click="shrinkFileList"><i class="iconfont">&#xe7c2;</i></span>
        <span class="btn-close" title="关闭" @click="closeFileList"><i class="iconfont">&#xe6da;</i></span>
      </span>
    </header>
     <el-scrollbar>
      <slot :file-list="fileList">
        <ul  ref="uploader-list-content" class="uploader-list-scrollbar" v-show="!shrink">
          <li v-for="(file,index) in fileList" :key="file.id" v-if="index<100">
            <div class="uploader-file">
                <div class="uploader-file-progress" :class="progressingClass" :style="file.progressStyle"></div>
                <div class="uploader-file-info">
                    <div class="uploader-file-name">
                      <svg class="icon file-uploader-icon" aria-hidden="true">
                          <use v-bind:xlink:href="file.fileName | fileCategory"></use>
                      </svg>
                      {{file.fileName}}
                    </div>
                    <div class="uploader-file-size">{{file.formatedSize | renderSize}}</div>
                    <div class="uploader-file-meta"></div>
                    <div class="uploader-file-status">
                    <span>
                        <span  class="yofc-file-name">{{file.progress|progress}}</span>
                        <em>{{formatedAverageSpeed}}</em>
                    </span>
                    </div>
                    <div class="uploader-file-actions">
                      <span class="uploader-file-remove" @click="remove(file)"></span>
                      <span class="uploader-file-pause" ></span>
                      <span class="uploader-file-resume" >️</span>
                      <!-- <span class="uploader-file-retry" @click="retry"></span> -->
                    </div>
                </div>
            </div>
          </li>
        </ul>
      </slot>
     </el-scrollbar>
  </div>
</template>

<script>
export default {
  props: ['title', 'fileList'],
  data() {
    return {
      timer: '',
      paused: false,
      error: false,
      averageSpeed: 0,
      currentSpeed: 0,
      isComplete: false,
      isUploading: false,
      size: 0,
      formatedSize: '',
      uploadedSize: 0,
      timeRemaining: 0,
      type: '',
      extension: '',
      progressingClass: 'uploader-file-progressing',
      formatedAverageSpeed: '',
      shrink: false,
      height: 0,
      show: true
    }
  },
  components: {},
  filters: {
    renderSize(value) {
      if (value === null || value === '') {
        return '0 Bytes'
      }
      let unitArr = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
      let index = 0
      let srcsize = parseFloat(value)
      index = Math.floor(Math.log(srcsize) / Math.log(1024))
      var size = srcsize / Math.pow(1024, index)
      //  保留的小数位数
      size = size.toFixed(2)
      return size + unitArr[index]
    },
    progress(values, file) {
      let value = Number(values)
      switch (value) {
        case 1:
          return '失败'
        default:
          if (value === 0.99) {
            return '解析中..'
          } else {
            return Math.floor(value * 99) + '%'
          }
      }
    },
    fileCategory(fileName) {
      const iconMap = {
        image: 'icon-tupianwenjian',
        video: 'icon-shipinwenjian',
        audio: 'icon-yinpinwenjian',
        folder: 'icon-wenjianjia',
        doc: 'icon-wordwenjian',
        txt: 'icon-textwenjian',
        excel: 'icon-excelwenjian',
        pdf: 'icon-pdfwenjian',
        ppt: 'icon-pptwenjian',
        zip: 'icon-yasuobaowenjian'
      }
      const typeMap = {
        image: ['gif', 'jpg', 'jpeg', 'png', 'bmp', 'webp'],
        video: ['mp4', 'm3u8', 'rmvb', 'avi', 'swf', '3gp', 'mkv', 'flv'],
        audio: ['mp3', 'wav', 'wma', 'ogg', 'aac', 'flac'],
        doc: ['doc', 'docx'],
        txt: ['txt'],
        excel: ['xls', 'xlsx'],
        pdf: ['pdf', 'epub', 'PDF'],
        ppt: ['ppt', 'pptx'],
        zip: ['zip', 'rar', '7z']
      }
      const getExtension = function(name) {
        var startIndex = fileName.lastIndexOf('.')
        if (startIndex !== -1) {
          return name.substring(startIndex + 1, fileName.length).toLowerCase()
        } else {
          return ''
        }
      }
      const extension = getExtension(fileName)
      let type = 'unknow'
      Object.keys(typeMap).forEach(_type => {
        const extensions = typeMap[_type]
        if (extensions.indexOf(extension) > -1) {
          type = _type
        }
      })
      const icon = iconMap[type] || 'icon-qitawenjianhuoweizhiwenjian'
      return `#${icon}`
    }
  },
  directives: {
    drag(el, bindings) {
      el.onmousedown = function(e) {
        const container = el.offsetParent
        // 获取鼠标点击处分别与div左边和上边的距离：鼠标位置-div位置
        const divx = e.clientX - container.offsetLeft
        const divy = e.clientY - container.offsetTop
        const width = document.documentElement.clientWidth
        const height = document.documentElement.clientHeight
        // 包含在onmousedown里，表示点击后才移动，为防止鼠标移出div，使用document.onmousemove
        document.onmousemove = function(e) {
          // 获取移动后div的位置：鼠标位置-divx/divy
          var l = e.clientX - divx
          var t = e.clientY - divy
          if (l < 0) {
            l = 0
          }
          if (l + container.clientWidth > width) {
            l = width - container.clientWidth
          }
          if (t < 0) {
            t = 0
          }
          if (t + container.clientHeight > height) {
            t = height - container.clientHeight
          }
          container.style.left = l + 'px'
          container.style.top = t + 'px'
          // 清除移动div过程中选中的dom
          window.getSelection().removeAllRanges()
        }
        document.onmouseup = function(e) {
          document.onmousemove = null
          document.onmouseup = null
        }
      }
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      const container = this.$refs['uploader-list']
      const divx = document.documentElement.clientWidth - container.clientWidth
      const divy =
        document.documentElement.clientHeight - container.clientHeight
      container.style.left = divx + 'px'
      container.style.top = divy + 'px'
      this.height = container.clientHeight - 48
    })
    this.timer = setInterval(() => {
      for (let a of this.fileList) {
        this.$set(a, 'progressStyle', '')
        this.mySetInterval(a)
      }
    }, 1000)
  },
  destroyed() {
    clearInterval(this.timer)
  },
  methods: {
    mySetInterval(a) {
      if (a.progress === 1) {
        a.progressStyle = {
          progress: `0`,
          webkitTransform: 'translateX(0%)',
          mozTransform: 'translateX(0%)',
          msTransform: 'translateX(0%)',
          transform: 'translateX(0%)'
        }
        return false
      }
      let randomNum = Math.random(0, 1)
      if (a.progress + randomNum * 0.1 >= 0.99) {
        a.progress = 0.99
        a.progressStyle = {
          progress: `100%`,
          webkitTransform: 'translateX(0%)',
          mozTransform: 'translateX(0%)',
          msTransform: 'translateX(0%)',
          transform: 'translateX(0%)'
        }
        return false
      }
      a.progress = a.progress + randomNum * 0.1
      let progress = Math.floor(a.progress * 99)
      let style = `translateX(${Math.floor(progress - 99)}%)`
      a.progressStyle = {
        progress: `${progress}%`,
        webkitTransform: style,
        mozTransform: style,
        msTransform: style,
        transform: style
      }
    },
    remove(file) {
      this.$emit('del', file.fileName)
      // file
    },
    shrinkFileList() {
      this.shrink = !this.shrink
      this.refreshListPosition()
    },
    refreshListPosition() {
      const container = this.$refs['uploader-list']
      const contentDom = this.$refs['uploader-list-content']
      let height = 0
      // 由展开转收缩
      if (this.shrink) {
        height = contentDom.clientHeight
      }
      this.$nextTick(function() {
        if (!this.shrink) {
          height = -contentDom.clientHeight
        }
        container.style.top = container.offsetTop + height + 'px'
      })
    },
    closeFileList() {
      if (this.fileList.length > 0) {
        this.$confirm(
          `当前还有上传任务未完成，退出将取消上传，是否继续？`,
          '',
          {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            cancelButtonClass: 'cancel',
            confirmButtonClass: 'danger',
            center: true,
            showClose: false,
            customClass: 'arDialog',
            closeOnClickModal: false
          }
        )
          .then(() => {
            this.$root.eventHub.$emit('clearFileList')
          })
          .catch(() => {})
      } else {
        this.$root.eventHub.$emit('clearFileList')
      }
    }
  }
}
</script>
<style>
.dossier-uploader-list .el-scrollbar__bar.is-vertical {
  height: 0 !important;
}
</style>

<style lang="scss" scoped>
.uploader-list {
  position: fixed;
  width: 480px;
  border-radius: 4px;
  box-shadow: -2px 4px 8px rgba(0, 0, 0, 0.15);
  z-index: 999;

  .uploader-list-header {
    height: 48px;
    line-height: 48px;
    background: #5d6469;
    color: #fff;
    padding-left: 20px;
    border-radius: 4px 4px 0 0;
    cursor: move;

    .uploader-list-header-btns {
      float: right;

      .btn-shrink {
        margin-right: 16px;
        cursor: pointer;
      }

      .btn-clear {
        margin-right: 16px;
        cursor: pointer;
      }

      .btn-close {
        margin-right: 20px;
        cursor: pointer;
      }
    }
  }

  .uploader-list-scrollbar {
    list-style: none;
    padding: 0;
    margin: 0;
    background: #fff;
    min-height: 300px;
    max-height: 300px !important;
    list-style: none;
    margin: 0;
    li {
      background: #fff;
    }

    .uploader-file {
      position: relative;
      height: 49px;
      line-height: 49px;
      overflow: hidden;
      border-bottom: 1px solid #cdcdcd;
    }
  }

  .uploader-file[status='waiting'] .uploader-file-pause,
  .uploader-file[status='uploading'] .uploader-file-pause {
    display: block;
  }
  .uploader-file[status='paused'] .uploader-file-resume {
    display: block;
  }
  .uploader-file[status='error'] .uploader-file-retry {
    display: block;
  }
  .uploader-file[status='success'] .uploader-file-remove {
    display: none;
  }
  .uploader-file[status='error'] .uploader-file-progress {
    background: #ffe0e0;
  }
  .uploader-file-progress {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #e2eeff;
    transform: translateX(-100%);
  }
  .uploader-file-progressing {
    transition: all 0.4s linear;
  }
  .uploader-file-info {
    position: relative;
    z-index: 1;
    height: 100%;
    overflow: hidden;
  }
  .uploader-file-info:hover {
    background-color: rgba(240, 240, 240, 0.2);
  }
  .uploader-file-info i,
  .uploader-file-info em {
    font-style: normal;
  }
  .uploader-file-name,
  .uploader-file-size,
  .uploader-file-meta,
  .uploader-file-status,
  .uploader-file-actions {
    float: left;
    position: relative;
    height: 100%;
  }
  .uploader-file-name {
    width: 45%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-indent: 14px;

    svg {
      width: 24px;
      height: 24px;
      position: relative;
      top: 2px;
    }
  }
  .uploader-file-size {
    width: 13%;
    text-indent: 10px;
  }
  .uploader-file-meta {
    width: 8%;
  }
  .uploader-file-status {
    width: 24%;
    text-indent: 20px;
  }
  .uploader-file-actions {
    width: 10%;
  }
  .uploader-file-actions > span {
    display: none;
    float: left;
    width: 16px;
    height: 16px;
    margin-top: 16px;
    margin-right: 10px;
    cursor: pointer;
    background: url('data:image/png;base64,')
      no-repeat 0 0;
  }
  .uploader-file-actions > span:hover {
    background-position-x: -21px;
  }
  .uploader-file-actions .uploader-file-pause {
    background-position-y: 0;
  }
  .uploader-file-actions .uploader-file-resume {
    background-position-y: -17px;
  }
  .uploader-file-actions .uploader-file-retry {
    background-position-y: -53px;
  }
  .uploader-file-actions .uploader-file-remove {
    display: block;
    background-position-y: -34px;
  }
  .yofc-file-name {
    white-space: nowrap;
    overflow: hidden;
  }
}
</style>



