// 案件办理模块路由配置
export default [{
  path: '/',
  redirect: 'step1'
},
  // 嫌疑人基本信息采集
{
  path: 'step1',
  name: 'step1',
  meta: {
    // index数据比较重要
    index: 0,
    requiresAuth: true,
    keepAlive: false
  },
  // 这句的写法还不是很明白？？
  component: resolve => require(['@/modules/caseHandle/step1/step1'], resolve)
},
  // 案件基本信息采集
{
  path: 'step2',
  name: 'step2',
  meta: {
    index: 1,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step2/step2'], resolve)
},
  // 事实认定
{
  path: 'step3',
  name: 'step3',
  props: true,
  meta: {
    index: 2,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step3/step3'], resolve)
},
  // 证据分析
{
  path: 'step4',
  name: 'step4',
  meta: {
    index: 3,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step4/step4'], resolve)
},

  // 处理意见
{
  path: 'step5',
  name: 'step5',
  meta: {
    index: 4,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step5/step5'], resolve)
},

  // 生成文书
{
  path: 'step6',
  name: 'step6',
  meta: {
    index: 5,
    requiresAuth: true,
    keepAlive: false
  },
  component: resolve => require(['@/modules/caseHandle/step6/step6'], resolve)
}
]



