<template>
<div class="custom-tree-container">
  <el-card class="box-card" shadow="never" body-style="padding:0">
    <div slot="header" class="clearfix">
      <span>
        <el-button size="mini" :class="{active:showPersonalRepo}" @click="switchFilsRepo(true)">个人资料</el-button>
        <el-button size="mini" class="depart-btn" :class="{active:!showPersonalRepo}" @click="switchFilsRepo(false)">部门资料</el-button>
      </span>
      <span style="float:right">
        <span class="tab-btn-icon" v-show="showPersonalRepo" >
          <i class="iconfont icon-tianjia" @click="insertRepo()" title="新建"></i>
        </span>
        <span class="tab-btn-icon">
          <i class="iconfont icon-tongbuhuoshuaxin" @click="syncData" title="同步数据"></i>
        </span>
      </span>
    </div>
    <uploader v-loading="loading" class="files-repot-uploader" :options="options" ref="uploader" :file-status-text="statusText" :autoStart="autoStart" @files-added="onFileAdded" @file-success="fileSuccess"  @file-complete="fileComplete"  @file-error="fileError">
      <!-- <div class="file-scrollbar"> -->
      <el-scrollbar class="file-scrollbar">
        <uploader-unsupport></uploader-unsupport>
        <el-tree v-show="showPersonalRepo" :data="personalData" ref="personalTree" highlight-current lazy :load="loadChildData" :props="defaultProps" empty-text="">
          <span class="custom-tree-node" slot-scope="{ node, data }" @dblclick.native.stop>
            <span class="custom-tree-node-label">
              <span class="custom-tree-node-icon"><svg class="icon" aria-hidden="true"><use v-bind:xlink:href="data.address"></use></svg></span>
              <span class="custom-tree-node-name" :title="node.label">{{ node.label }}</span>
            </span>
            <span v-if="data.type==='repo'" class="tree-tool" @click.stop="setCurrentNode(node, data)">
              <uploader-btn class="iconfont icon-shangchuanci tree-tool-btn" title="上传" @click.native="getUrl(node, data)"></uploader-btn>
              <span class="iconfont icon-shanchu tree-tool-btn" type="text" size="mini" title="删除" @click="() => remove(node, data)"></span>
              <el-dropdown trigger="click" :ivided="true">
                <span class="el-dropdown-link">
                  <span class="iconfont icon-gengduo tree-tool-btn" type="text" size="mini" title="更多"></span>
                </span>
                <el-dropdown-menu class="custom-tree-menu" slot="dropdown">
                  <el-dropdown-item class="custom-tree-menu-uploader"><uploader-btn :directory="true" @click.native="getUrl(node, data)">上传文件夹</uploader-btn></el-dropdown-item>
                  <el-dropdown-item @click.native="insertDir(node, data)">新建文件夹</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </span>
            <span v-else-if="data.type==='file'" class="tree-tool leaf" @click.stop="setCurrentNode(node, data)">
              <span class="iconfont icon-xiazai tree-tool-btn" type="text" size="mini" title="下载" @click="download(node, data)"></span>
              <span class="iconfont icon-shanchu tree-tool-btn" type="text" size="mini" title="删除" @click="() => remove(node, data)"></span>
            </span>
            <span v-else class="tree-tool" @click.stop="setCurrentNode(node, data)">
              <uploader-btn class="iconfont icon-shangchuanci tree-tool-btn" title="上传" @click.native="getUrl(node, data)"></uploader-btn>
              <span class="iconfont icon-xiazai tree-tool-btn" type="text" size="mini" title="下载" @click="() => download(node, data)"></span>
              <el-dropdown trigger="click" divided="true">
                <span class="el-dropdown-link">
                  <span class="iconfont icon-gengduo tree-tool-btn" type="text" size="mini" title="更多"></span>
                </span>
                <el-dropdown-menu class="custom-tree-menu" slot="dropdown">
                  <el-dropdown-item class="custom-tree-menu-uploader"><uploader-btn :directory="true" @click.native="getUrl(node, data)">上传文件夹</uploader-btn></el-dropdown-item>
                  <el-dropdown-item @click.native="insertDir(node, data)">新建文件夹</el-dropdown-item>
                  <el-dropdown-item @click.native="remove(node, data)">删除</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </span>
          </span>
        </el-tree>
        <el-tree v-show="!showPersonalRepo" :data="departData" highlight-current ref="departTree" lazy :load="loadChildData" :props="defaultProps" empty-text="">
          <span class="custom-tree-node" slot-scope="{ node, data }">
            <span class="custom-tree-node-label">
              <span class="custom-tree-node-icon"><svg class="icon" aria-hidden="true"><use v-bind:xlink:href="data.address"></use></svg></span>
              <span class="custom-tree-node-name" :title="node.label">{{ node.label }}</span>
            </span>
            <span v-if="data.type==='repo'" class="tree-tool" @click.stop="setCurrentNode(node, data)">
              <uploader-btn v-if="data.hide || (!data.hide && data.ownerRepo)" class="iconfont icon-shangchuanci tree-tool-btn" title="上传" @click.native="getUrl(node, data)"></uploader-btn>
            </span>
            <span v-else-if="data.type==='file'" class="tree-tool leaf" @click.stop="setCurrentNode(node, data)">
              <span class="iconfont icon-xiazai tree-tool-btn" type="text" size="mini" title="下载" @click="download(node, data)"></span>
              <span v-if="data.ownerRepo || (isStaff && data.hide)" class="iconfont icon-shanchu tree-tool-btn" type="text" size="mini" title="删除" @click="() => remove(node, data)"></span>
            </span>
            <span v-show="!data.groupId" v-else class="tree-tool" @click.stop="setCurrentNode(node, data)">
              <uploader-btn v-if="data.hide || (!data.hide && data.ownerRepo)" class="iconfont icon-shangchuanci tree-tool-btn" title="上传" @click.native="getUrl(node, data)"></uploader-btn>
              <span class="iconfont icon-xiazai tree-tool-btn" type="text" size="mini" title="下载" @click="() => download(node, data)"></span>
              <span v-if="data.ownerRepo || (isStaff && data.hide)" class="iconfont icon-shanchu tree-tool-btn" type="text" size="mini" title="删除" @click="() => remove(node, data)"></span>
            </span>
          </span>
        </el-tree>
        <div v-if="(showPersonalRepo && personalData.length=== 0) || (!showPersonalRepo && departData.length===0)" class="no-result">
          <img src="../assets/img/no_result.png" alt="">
          <p style="text-align: center;">该列表暂无数据！</p>
        </div>
      </el-scrollbar>
      <!-- </div> -->
    </uploader>
  </el-card>
</div>
</template>

<script>
import { Message } from 'element-ui'
import YmList from '../components/YmList'
import {
  fetchRepoList,
  fetchChildrenList,
  getDownloadUrl,
  getDeleteResult,
  getAjaxPromise,
  savelog,
  fileUploadSuccess,
  getUrl,
  getLeftStorage
} from '@/api/admin'

export default {
  components: {
    YmList
  },
  data() {
    return {
      showPersonalRepo: true,
      personalData: [],
      departData: [],
      defaultProps: {
        label(data, node) {
          return data.name
        },
        isLeaf(data, node) {
          return data.type === 'file'
        }
      },
      autoStart: false,
      options: {
        allowDuplicateUploads: true,
        target: '',
        relativePath: '/',
        // query: {
        //   upload_token: localCache.getItem('token'),
        //   parent_dir: '/',
        //   retJson: '1'
        // },
        query: (file, xhr) => {
          // console.log({
          //   parent_dir: file.queryOpts.parent_dir,
          //   retJson: file.queryOpts.retJson,
          //   upload_token: file.queryOpts.retJson
          // })
          return file.queryOpts
        },
        testChunks: false,
        chunkSize: 1024 * 1024 * 1024 * 20
      },
      statusText: {
        success: '成功',
        error: '上传失败',
        uploading: '上传中',
        paused: '暂停中',
        waiting: '等待中'
      },
      isUploaderList: false,
      fileList: [],
      currentNode: null,
      loading: false,
      isStaff: parseInt(localCache.getItem('isStaff'))
    }
  },

  mounted: function() {
    this.$nextTick(function() {
      this.loadRootData()
    })
  },

  methods: {
    // 同步资料库数据
    syncData() {
      this.loadRootData(true)
      this.loadRootData(false)
    },
    // 设置当前高亮节点
    setCurrentNode(node) {
      node.store.setCurrentNode(node)
    },
    // 切换资料库
    switchFilsRepo(showPersonalRepo) {
      this.showPersonalRepo = showPersonalRepo
      const data = this.showPersonalRepo ? this.personalData : this.departData
      if (data.length === 0) {
        this.loadRootData()
      }
    },
    // 加载根节点数据
    loadRootData(showPersonalRepo) {
      const isPersonal =
        showPersonalRepo === undefined
          ? this.showPersonalRepo
          : showPersonalRepo
      let url
      let params
      if (isPersonal) {
        url = '/yofcloudDisk/api/repos'
        params = { type: 'mine' }
      } else {
        url = '/yofcloudDisk/api/group/queryGroupsByUser'
        params = {}
      }
      this.loading = true
      const repoPromise = fetchRepoList(url, params)

      repoPromise
        .then(res => {
          this.loading = false
          const treeData = []
          res.data.data.forEach(node => {
            // 群组中给数据添加name,id属性，为统一生成树做准备
            if (!isPersonal) {
              node.name = node.groupName
              node.id = node.groupId
            }
            if (node.type !== 'file') {
              node.children = []
              // 给文件库添加repoName属性，后续传递给子节点
              if (node.type === 'repo') {
                node.repoName = node.name
              }
            }
            // 加密文件库不添加到树列表中
            if (!node.encrypted) {
              treeData.push(node)
            }
          })

          if (isPersonal) {
            this.personalData = treeData
          } else {
            this.departData = treeData
          }

          if (
            showPersonalRepo !== undefined &&
            showPersonalRepo === this.showPersonalRepo
          ) {
            this.$message({
              type: 'success',
              message: '同步成功'
            })
          }
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 子节点数据处理
    childNodesDataFilter(parentNode, children) {
      if (!parentNode) {
        return children
      }
      const nodesData = []
      for (var i = 0, l = children.length; i < l; i++) {
        children[i].name = children[i].name.replace(/\.n/g, '.')
        if (children[i].type !== 'file') {
          children[i].children = []
          // 给文件库添加repoName属性，后续传递给子节点
          if (children[i].type === 'repo') {
            children[i].repoName = children[i].name
          }
        } else {
          children[i].leaf = true
        }
        // 从父节点获取是否为库拥有者属性
        if (parentNode.data.ownerRepo) {
          children[i].ownerRepo = true
        }
        // 从父节点获取文件库是否为群组所有属性
        if (parentNode.data.hide) {
          children[i].hide = true
        }
        // 从父节点获取文件库名称属性
        if (parentNode.repoName !== undefined) {
          children[i].repoName = parentNode.repoName
        }
        // 加密文件库不添加到树列表
        if (!children[i].encrypted) {
          nodesData.push(children[i])
        }
      }
      return nodesData
    },
    // 加载子节点数据
    loadChildData(node, resolve) {
      if (node.level === 0) {
        return resolve([])
      }

      let url
      let params
      if (this.showPersonalRepo) {
        url = '/yofcloudDisk/api/repos/dirent'
        params = {
          repoId: node.data.repoId,
          p: node.data.path
        }
      } else {
        url = '/yofcloudDisk/api/group/queryGroupFile'
        params = {
          groupId: node.data.groupId,
          repoId: node.data.repoId,
          p: node.data.path
        }
      }

      const childPromise = fetchChildrenList(url, params)
      childPromise
        .then(res => {
          if (!res.data.data) return []
          var children = res.data.data
          const nodesData = this.childNodesDataFilter(node, children)
          resolve(nodesData)
        })
        .catch(error => {
          if (error.response.data.code === 400) {
            this.syncData()
          }
        })
    },
    // 刷新节点
    refreshNode(node, callback) {
      if (!node) {
        return
      }
      let url
      let params
      if (this.showPersonalRepo) {
        url = '/yofcloudDisk/api/repos/dirent'
        params = {
          repoId: node.data.repoId,
          p: node.data.path
        }
      } else {
        url = '/yofcloudDisk/api/group/queryGroupFile'
        params = {
          groupId: node.data.groupId,
          repoId: node.data.repoId,
          p: node.data.path
        }
      }

      const childPromise = fetchChildrenList(url, params)
      childPromise.then(res => {
        var children = res.data.data
        const nodesData = this.childNodesDataFilter(node, children)
        node.childNodes.forEach((childNode, index) => {
          node.removeChild(childNode)
        })
        node.data.children = nodesData
        node.setData(node.data)
        callback && callback()
      })
    },
    getUrl(node, nodeData) {
      this.currentNode = node

      // if (/^\/{0,1}$/.test(nodeData.path)) {
      //   this.$refs.uploader.uploader.opts.query.parent_dir = nodeData.path
      // } else {
      //   this.$refs.uploader.uploader.opts.query.parent_dir = nodeData.path + '/'
      // }
    },
    onFileAdded(files, fileList) {
      if (!this.currentNode) {
        return
      }

      let self = this
      let validate = true
      let leftOver = 0
      let memory = 0
      let parentDir = ''
      const nodeData = this.currentNode.data
      if (/^\/{0,1}$/.test(nodeData.path)) {
        parentDir = nodeData.path
      } else {
        parentDir = nodeData.path + '/'
      }
      for (let a of files) {
        memory += parseInt(a.size)
        a.queryOpts = {
          upload_token: localCache.getItem('token'),
          parent_dir: parentDir,
          retJson: '1'
        }
        a.currentNode = this.currentNode
      }

      getUrl({
        p: nodeData.path,
        repoId: nodeData.repoId
      })
        .then(res => {
          if (res.data.code === 200) {
            this.$refs.uploader.uploader.opts.target =
              '/seafhttp/upload-aj/' + res.data.data
          } else {
            this.$message({
              type: 'warning',
              message: res.data.message
            })
          }
          return getLeftStorage()
        })
        .then(res => {
          if (res.data.code === 200) {
            leftOver = res.data.data
            if (memory < leftOver) {
              this.fullscreenLoading = true
              for (let i = 0; i < files.length; i++) {
                // eslint-disable-next-line
                const regex = /^(?=[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\s\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]$).{1,40}$|^[\u4e00-\u9fa5a-zA-Z0-9\-\、\[\]\.\_\(\)]{1,40}$/
                if (
                  files[i].parent != null &&
                  !regex.test(files[i].parent.name)
                ) {
                  validate = false
                  self.fullscreenLoading = false
                  self.$refs.uploader.uploader.cancel()
                  self.$message({
                    type: 'warning',
                    message: `<span class="message-long">格式不正确，不能为空，只能输入数字、字母、中文和、[].-_()，长度在40个字符以内！</span>`,
                    dangerouslyUseHTMLString: true,
                    center: true
                  })
                } else if (files[i].name.length > 80) {
                  validate = false
                  self.fullscreenLoading = false
                  self.$refs.uploader.uploader.cancel()
                  self.$message({
                    type: 'warning',
                    message: '文件名长度在80个字符以内！'
                  })
                }
              }
              if (validate) {
                self.$refs.uploader.uploader.resume()
                self.fullscreenLoading = false
                this.$root.eventHub.$emit('addItems2UploadFileList', fileList)
              }
            } else {
              self.$refs.uploader.uploader.cancel()
              self.$message({
                type: 'warning',
                message: '当前用户存储空间不足！'
              })
            }
          }
        })
        .catch(error => {
          if (error.response && error.response.data.code === 400) {
            this.syncData()
          }
        })
    },
    closeUpload(done) {
      this.$refs.uploader.uploader.cancel()
      done()
    },
    start() {
      this.$refs.uploader.autoStart = true
    },
    fileSuccess(rootFile, file, message, chunk) {
      this.refreshNode(file.currentNode, () => {
        this.currentNode.expand()
      })
      this.$root.eventHub.$emit('fileUploadSuccess', rootFile)
    },
    fileComplete(rootFile) {
      const currentNode = rootFile.currentNode || this.currentNode
      if (!currentNode) {
        return
      }
      fileUploadSuccess({
        repoId: currentNode.data.repoId,
        ppath: currentNode.data.path,
        libName: currentNode.data.repoName
      }).then(res => {
        if (res.data.code === 200) {
        }
      })
      // 上传成功记录日志
      savelog({ logKey: '172', operationResult: '1' })
    },
    fileError(rootFile, file, message, chunk) {
      this.$message({
        type: 'error',
        message: '上传失败'
      })
      // 上传失败记录日志
      savelog({ logKey: '172', operationResult: '0' })
    },
    download(node, nodeData) {
      const dirVos = {
        p: nodeData.ppath,
        repoId: nodeData.repoId,
        name: nodeData.name
      }
      let url
      let param
      if (nodeData.type === 'file') {
        url = '/yofcloudDisk/api/repos/oneFile'
        param = {
          p: nodeData.path,
          repoId: nodeData.repoId
        }
      } else {
        url = '/yofcloudDisk/api/repos/filezips'
        param = [dirVos]
      }

      this.loading = true
      const downloadPromise = getDownloadUrl(url, param)
      downloadPromise
        .then(res => {
          this.loading = false
          if (res.data.code === 200) {
            var seafileUrl = localCache.getItem('seafileUrl')
            let downloadUrl = ''
            if (nodeData.type === 'file') {
              downloadUrl = seafileUrl + res.data.data
              this.$root.eventHub.$emit('addItems2DownloadList', [
                { url: downloadUrl, fileName: nodeData.name }
              ])
            } else {
              downloadUrl = '/seafhttp/zip/' + res.data.data.zip_token
              this.$root.eventHub.$emit('addItems2DownloadList', [
                { url: downloadUrl, fileName: nodeData.name + '.zip' }
              ])
            }
          }
        })
        .catch(error => {
          this.loading = false
          if (error.respose.data.code === 400) {
            this.syncData()
          }
        })
    },
    // 删除
    remove(node, nodeData) {
      let url
      let params
      if (nodeData.type === 'repo') {
        url = '/yofcloudDisk/api/repos/delRepo'
        params = [
          {
            id: nodeData.id
          }
        ]
      } else {
        url = '/yofcloudDisk/api/repos/dels'
        params = [
          {
            p: nodeData.ppath,
            repoId: nodeData.repoId,
            name: nodeData.name,
            type: nodeData.type
          }
        ]
      }

      this.$confirm('是否删除?', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        cancelButtonClass: 'cancel',
        confirmButtonClass: 'danger',
        center: true,
        showClose: false,
        customClass: 'arDialog'
      })
        .then(() => {
          this.loading = true
          const delPromise = getDeleteResult(url, params)
          delPromise
            .then(res => {
              this.loading = false
              const parent = node.parent
              if (parent) {
                parent.removeChild(node)
              } else {
                node.remove()
              }

              this.$message({
                type: 'success',
                message: '删除成功!'
              })
            })
            .catch(error => {
              this.loading = false
              if (error.response.data.code === 400) {
                this.syncData()
              }
            })
        })
        .catch(res => {
          this.loading = false
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 获取新建输入框prompt
    getInsertPrompt(name) {
      // eslint-disable-next-line
      const regex = /^(?=[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\s\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]$).{1,40}$|^[\u4e00-\u9fa5a-zA-Z0-9\-\、\[\]\.\_\(\)]{1,40}$/
      const msg =
        '格式不正确，不能为空，只能输入数字、字母、中文和、[].-_()，长度在40个字符以内！'
      return this.$prompt(`请输入${name}名称`, `新建${name}`, {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        // inputValue: '',
        customClass: 'add-file',
        confirmButtonClass: 'loading',
        inputPattern: regex,
        inputType: 'text',
        inputErrorMessage: `${name}${msg}`
      })
    },
    // 新建文件夹
    insertDir(node, nodeData) {
      const insertPrompt = this.getInsertPrompt('文件夹')
      insertPrompt
        .then(({ value }) => {
          let p = `${nodeData.path + value}`
          if (nodeData.path !== '/') {
            p = `${nodeData.path}/${value}`
          }
          const params = {
            operation: 'mkdir',
            p: p,
            repoId: nodeData.repoId
          }
          const url = '/yofcloudDisk/api/repos/insertDir'

          this.loading = true
          const insertPromise = getAjaxPromise({
            method: 'put',
            url,
            data: params
          })
          insertPromise
            .then(res => {
              this.loading = false
              this.refreshNode(node, () => {
                node.expand()
              })
              Message({
                message: '新建成功',
                type: 'success'
              })
            })
            .catch(error => {
              this.loading = false
              if (error.response.data.code === 400) {
                this.syncData()
              }
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          })
        })
    },
    // 新建文件库
    insertRepo() {
      const insertPrompt = this.getInsertPrompt('文件库')
      insertPrompt
        .then(({ value }) => {
          this.loading = true
          const queryRepoPromise = getAjaxPromise({
            method: 'get',
            url: '/yofcloudDisk/api/queryRePos/' + value
          })

          queryRepoPromise
            .then(res => {
              if (res.data.code === 200) {
                if (res.data.data === false) {
                  const insertPromise = getAjaxPromise({
                    method: 'put',
                    url: '/yofcloudDisk/api/repos/insertRepo',
                    data: {
                      name: value
                    }
                  })

                  insertPromise
                    .then(res => {
                      this.loading = false
                      if (res.data.code === 200) {
                        this.loadRootData()
                        Message({
                          message: '新建个人库成功',
                          type: 'success'
                        })
                      }
                    })
                    .catch(() => {
                      this.loading = false
                    })
                } else {
                  this.loading = false
                  Message({
                    message: '文件库已经存在,无法重复创建',
                    type: 'warning'
                  })
                }
              }
            })
            .catch(() => {
              this.loading = false
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          })
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.custom-tree-container {
  .clearfix {
    height: 60px;
    line-height: 60px;
    padding-left: 16px;
    padding-right: 10px;
  }
  .file-scrollbar {
    width: 100%;
    height: calc(100vh - 102px);
    position: relative;

    .el-scrollbar__wrap {
      overflow-x: hidden;
    }
  }

  .el-card {
    border: none;
  }
  .el-card__header {
    padding: 0;
  }
  .el-button {
    border-color: transparent;
    border-color: #ddd;
    font-size: 100%;
  }
  .el-button.depart-btn {
    margin-left: 32px;
  }
  .el-button:hover {
    border-color: #42a0f8;
    color: #42a0f8;
    background-color: transparent;
  }
  .el-button.active {
    color: #fff;
    border-color: #42a0f8;
    background-color: #42a0f8;
  }
  .el-tree-node__content {
    height: 38px;

    .el-tree-node__expand-icon {
      margin-left: 10px;
      font-size: 14px;
    }
  }
}

.custom-tree-menu-uploader {
  padding: 0;
}
.custom-tree-menu-uploader .uploader-btn {
  height: 36px;
  line-height: 36px;
  width: 100%;
  font-size: 15px;
  display: inline-block;
  text-align: center;
}

.el-message__content {
  span.message-long {
    word-break: normal;
    width: 350px;
    display: block;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
</style>

<style scoped>
.tab-btn-icon {
  cursor: pointer;
  padding-right: 6px;
}
.tab-btn-icon:hover {
  color: #42a0f8;
}
.custom-tree-menu.el-dropdown-menu {
  margin: 0;
  padding: 0;
}
.custom-tree-menu.el-dropdown-menu .uploader-btn {
  width: 100%;
  height: 100%;
  font-size: 15px;
  background-color: transparent;
  border-color: transparent;
  padding: 0;
}
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 8px;
}

.custom-tree-node {
  color: #666;
  user-select: none;
}

.custom-tree-node .el-button--text {
  color: #666;
}

.files-repot-uploader .uploader-btn {
  background-color: transparent;
  width: 12px;
  font-size: 15px;
  height: 12px;
  border: none;
}

.tree-tool {
  display: none;
  position: absolute;
  right: 16px;
  margin-top: -2px;
}

.tree-tool.leaf {
  margin-top: 0;
}

.custom-tree-node:hover .tree-tool {
  display: inline-block;
}

.custom-tree-node-label {
  position: relative;
  height: 100%;
  line-height: 1;
  display: inline-block;
  max-width: 160px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.custom-tree-node-icon svg {
  font-size: 18px;
}

.tree-tool-btn:hover {
  color: #42a0f8;
}
/* .file-scrollbar {
  width: 100%;
  height: calc(100vh - 115px);
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
} */
</style>



