<template>
  <el-container class="caseFileLibrary" v-loading="fullscreenLoading">
    <uploader :options="options" ref="uploader" :autoStart="false" @files-added="onFileAdded"  class="uploader-example" :file-status-text="statusText"  @file-success="fileSuccess" @file-complete="fileComplete" @file-error="fileError">
    <el-header class="yofc-header">
      <el-breadcrumb separator="/" class="">
          <el-breadcrumb-item>案件资料库</el-breadcrumb-item>
          <el-breadcrumb-item v-for="(item,index) in breadcrumbs"  :to="{ path: '/caseFileLibary',query:{repoId:item.repoId,p:item.p,name:item.name} }" :key="index">{{item.name}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <el-main>
      <uploader-unsupport></uploader-unsupport>
          <el-row>
            <el-col :span="12">
              <el-dropdown  size="medium" class="ml10 mr10">
                  <el-button size="mini">
                      <i class="iconfont mr5">&#xe6e2;</i>上传<i class="el-icon-arrow-down el-icon--right"></i>
                  </el-button>
                  <el-dropdown-menu slot="dropdown" class="el-dropdown-menu-css">
                          <uploader-drop class="uploader-drop-css">
                              <el-dropdown-item  class="cloudDisk-dropdown-item"><uploader-btn>上传文件</uploader-btn></el-dropdown-item>
                              <el-dropdown-item  class="cloudDisk-dropdown-item"><uploader-btn :directory="true">上传文件夹</uploader-btn></el-dropdown-item>
                          </uploader-drop>
                  </el-dropdown-menu>
              </el-dropdown>
              <el-button :disabled="tableSelection.length==0" size="mini" @click="download"><i class="iconfont mr5 mt5">&#xe6fe;</i>下载</el-button>
              <el-button :disabled="tableSelection.length==0" size="mini" @click="deleteFile"><i class="iconfont mr5 mt5">&#xe6df;</i>删除</el-button>
              <el-button  size="mini" @click="addMyFile"><i class="iconfont mr5 mt5">&#xe6ed;</i>新建文件夹</el-button>
            </el-col>
            <el-col :span="12" class="file-search">
                <!-- <ym-search @search="searchData" :searchPlaceholder="'请输入关键字搜索文件'"/> -->
            </el-col>
          </el-row>
          <div class="data-tables-server-css">
              <data-tables-server :data="tableData"  :total="total" @selection-change="handleSelectionChange" :loading="loading" @query-change="loadData" :page-size="0" :pagination-props="{ background: true,  pageSizes: [10,30,50,100] }" :current-page.sync="params.pageNumber">
                <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column label="名称" min-width="100px">
                  <template slot-scope="scope">
                    <a class="a-text" :class="{'yofc-table-botton':scope.row.type !== 'file'}" @click="getNextDir(scope.row)"><svg class="icon" aria-hidden="true"><use :xlink:href="scope.row.address"></use></svg>
                    {{scope.row.name}}</a>
                    </template>
                </el-table-column>
              <el-table-column v-for="title in titles" :prop="title.prop" :label="title.label" :key="title.prop">
                </el-table-column>
              </data-tables-server>
          </div>
    </el-main>
    </uploader>
    <!-- 文件上传 -->
    <ym-list v-if="isUploaderList" :fileLists="fileList"/>
  </el-container>
</template>

<script>
import YmList from '@/components/YmList'

import { getCurrentTime } from '@/utils/filter'
import YmSearch from '@/components/YmSearch'
import {
  fetchChildrenList,
  getUrl,
  getLeftStorage,
  addActions,
  revertFile,
  savelog,
  fileUploadSuccess,
  getAjaxPromise,
  queryTrashByRepoId
} from '@/api/admin'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'caseFileLibrary',
  components: {
    YmList,
    YmSearch
  },
  data() {
    return {
      msg: '案件文件库',
      dataSign: '',
      total: 0,
      tableSelection: [],
      loading: false,
      tableData: [],
      breadcrumbs: [],
      titles: [
        {
          prop: 'size_formatted',
          label: '大小'
        },
        {
          prop: 'typeValue',
          label: '文件类型'
        },
        {
          prop: 'time',
          label: '更新时间'
        }
      ],
      params: {
        p: `/${this.caseObj.caseName}`,
        page: 1,
        limit: 10,
        repoId: ''
      },
      options: {
        allowDuplicateUploads: true,
        target: '',
        relativePath: '/',
        query: (file, xhr) => {
          // console.log({
          //   parent_dir: file.queryOpts.parent_dir,
          //   retJson: file.queryOpts.retJson,
          //   upload_token: file.queryOpts.retJson
          // })
          return file.queryOpts
        },
        testChunks: false,
        chunkSize: 1024 * 1024 * 1024 * 20
      },
      statusText: {
        success: '成功',
        error: '上传失败',
        uploading: '上传中',
        paused: '暂停中',
        waiting: '等待中'
      },
      fileList: [],
      isUploaderList: false,
      fullscreenLoading: false,
      isUpdateSuccess: false
    }
  },
  created() {
    this.breadcrumbs.push({
      p: `/${this.caseObj.caseName}`,
      repoId: this.params.repoId,
      name: this.caseObj.caseName
    })
    // this.showData(this.params)
    // 移除上传项上传列表
    this.$root.eventHub.$on('removeItemFromYmFileList', file => {
      const index = this.fileList.findIndex(item => item === file)
      this.fileList.splice(index, 1)
    })
    // 清空列表
    this.$root.eventHub.$on('clearYmFileList', file => {
      this.$refs.uploader.uploader.cancel()
      this.isUploaderList = false
      this.fileList = []
    })
  },
  activated() {
    this.loadData(this.params)
  },
  beforeRouteUpdate(to, from, next) {
    if (this.params.p !== to.query.p) {
      if (!to.query.p) {
        this.breadcrumbs.splice(1, this.breadcrumbs.length - 1)
      } else {
        let index = this.breadcrumbs.findIndex(item => item.p === to.query.p)
        this.breadcrumbs.splice(index + 1, this.breadcrumbs.length - 1)
      }
      this.params.p = to.query.p
      this.params.repoId = to.query.repoId
      // this.showData()
      this.params.pageNumber = 1
      this.loadData(this.params)
    }
  },
  methods: {
    downloadFile(url, fileName) {
      if (!url) {
        return
      }
      const filename = fileName
      const xhr = new XMLHttpRequest()
      xhr.open('get', url, true)
      // 监听进度事件
      xhr.responseType = 'blob'
      xhr.setRequestHeader(
        'Content-Type',
        'application/x-www-form-urlencoded; charset=UTF-8'
      )
      xhr.onreadystatechange = () => {
        if (xhr.readyState === 4 && xhr.status === 200) {
          // Chrome version
          var link = document.createElement('a')
          link.href = window.URL.createObjectURL(xhr.response)
          link.download = filename
          link.click()
        }
      }
      xhr.send()
    },
    async loadData(queryInfo) {
      this.params.page = queryInfo.page
      this.params.limit = queryInfo.pageSize
        ? queryInfo.pageSize
        : queryInfo.limit
      if (!this.params.repoId) {
        const caseRepoId = await getAjaxPromise({
          method: 'get',
          url: '/yofcloudDisk/api/user/getUserCaseRepoId'
        })
        this.params.repoId = caseRepoId.data.data.repoId
      }
      this.loading = true
      await fetchChildrenList('/yofcloudDisk/api/repos/dirent', this.params)
        .then(res => {
          this.loading = false
          if (!res.data.data) return []
          if (res.data.data.length > 0) {
            for (let a of res.data.data) {
              a.size_formatted = a.size_formatted ? a.size_formatted : ''
              a.typeValue = a.type === 'dir' ? '文件夹' : '文件'
            }
          }
          this.tableData = res.data.data
          this.total = res.data.count
        })
        .catch(respon => {
          this.loading = false
          if (respon.response.data.code === 400) {
            queryTrashByRepoId({ repoId: this.params.repoId })
              .then(o => {
                if (o.data.code === 200) {
                  let index = -1
                  if (o.data.data) {
                    index = o.data.data.findIndex(
                      item => item.objName === this.caseObj.caseName
                    )
                  }
                  if (index < 0) {
                    const insertDirPromise = getAjaxPromise({
                      method: 'put',
                      url: '/yofcloudDisk/api/repos/insertDir',
                      data: {
                        operation: 'mkdir',
                        p: `/${this.caseObj.caseName}`,
                        repoId: this.params.repoId
                      }
                    })
                    // 新建案件文件夹
                    insertDirPromise.then(res => {
                      if (res.data.code === 200) {
                        this.$router.go(0)
                      }
                    })
                  } else {
                    let param = [
                      {
                        repoId: this.params.repoId,
                        p: `/${this.caseObj.caseName}`,
                        type: '1'
                      }
                    ]
                    revertFile(param).then(res => {
                      if (res.data.code === 200) {
                        this.$router.go(0)
                      }
                    })
                  }
                }
              })
              .catch(() => {
                this.fullscreenLoading = false
              })
          }
        })
    },
    searchData(keyValue) {
      console.log(keyValue)
    },
    deleteFile() {
      let params = []
      for (let a of this.tableSelection) {
        params.push({
          p: a.ppath,
          repoId: a.repoId,
          name: a.name,
          type: a.type
        })
      }
      this.del(
        '文件或文件夹',
        '/yofcloudDisk/api/repos/dels',
        '删除',
        'post',
        params
      )
    },
    addMyFile() {
      this.add('文件夹', '/yofcloudDisk/api/repos/insertDir', '新建', 'put')
    },
    add(name, url, operation, type, keyValue, inputPattern, inputErrorMessage) {
      // eslint-disable-next-line
      let regex = /^(?=[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\s\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]$).{1,40}$|^[\u4e00-\u9fa5a-zA-Z0-9\-\、\[\]\.\_\(\)]{1,40}$/
      if (inputPattern != null) {
        regex = inputPattern
      }
      let msg =
        '不能为空，只能输入数字、字母、中文和、[].-_()，长度在40个字符以内！'
      if (inputErrorMessage != null) {
        msg = inputErrorMessage
      }
      this.$prompt(`请输入${name}`, `${operation}${name}`, {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: keyValue,
        confirmButtonClass: 'loading',
        inputPattern: regex,
        inputType: 'text',
        customClass: 'add-file',
        inputErrorMessage: `${name}格式不正确，${msg}`
      })
        .then(({ value }) => {
          this.fullscreenLoading = true
          let data
          if (name === '文件夹' && operation === '新建') {
            data = {
              repoId: this.params.repoId,
              p: this.params.p + '/' + value,
              operation: 'mkdir'
            }
          }
          addActions(type, url, data)
            .then(res => {
              if (res.data.code === 200) {
                this.tableSelection = []
                this.$message({
                  type: 'success',
                  message: `${name}${operation}成功`
                })
                this.fullscreenLoading = false
                this.params.pageNumber = 1
                this.loadData(this.params)
              }
            })
            .catch(() => {
              this.fullscreenLoading = false
            })
        })
        .catch(() => {
          this.fullscreenLoading = false
          this.$message({
            type: 'info',
            message: '取消输入'
          })
        })
    },
    del(name, url, operation, type, params) {
      this.$confirm(`确定要${operation}此${name}吗？`, '', {
        confirmButtonText: '删除',
        confirmButtonClass: 'danger',
        cancelButtonText: '取消',
        center: true,
        showClose: false
      })
        .then(() => {
          this.fullscreenLoading = true
          addActions(type, url, params)
            .then(res => {
              if (res.data.code === 200) {
                this.fullscreenLoading = false
                this.$message({
                  type: 'success',
                  message: `${name}${operation}成功!`
                })
                this.tableSelection = []
                this.loadData(this.params)
              }
            })
            .catch(() => {
              this.fullscreenLoading = false
            })
        })
        .catch(() => {
          this.fullscreenLoading = false
          this.$message({
            type: 'info',
            message: `已取消${operation}`
          })
        })
    },
    getNextDir(row) {
      if (row.type === 'file') {
        return false
      }
      this.breadcrumbs.push({
        repoId: row.repoId,
        p: row.path,
        name: row.name
      })
      this.params.page = 1
      this.params.p = row.path
      this.params.repoId = row.repoId
      // this.showData()
      // if (/^\/{0,1}$/.test(row.path)) {
      //   this.$refs.uploader.uploader.opts.query.parent_dir = row.path
      // } else {
      //   this.$refs.uploader.uploader.opts.query.parent_dir = row.path + '/'
      // }
      this.params.pageNumber = 1
      this.loadData(this.params)
    },
    handleSelectionChange(val) {
      this.tableSelection = val
    },
    showData() {
      // this.$refs.uploader.uploader.cancel()
      // let params = {
      //   p: this.params.p,
      //   repoId: this.params.repoId
      // }
      // getUrl(params).then(res => {
      //   if (res.data.code === 200) {
      //     if (/^\/{0,1}$/.test(this.params.p)) {
      //       this.$refs.uploader.uploader.opts.query.parent_dir = this.params.p
      //     } else {
      //       this.$refs.uploader.uploader.opts.query.parent_dir =
      //         this.params.p + '/'
      //     }
      //     this.$refs.uploader.uploader.opts.target =
      //       '/seafhttp/upload-aj/' + res.data.data
      //   } else {
      //     this.$message({
      //       type: 'warning',
      //       message: res.data.message
      //     })
      //   }
      // })
    },
    onFileAdded(files, fileList) {
      if (fileList.length === 0 && files.length > 0) {
        this.$message({
          type: 'warning',
          message: '改文件夹正在上传中，请勿重复上传！'
        })
        return
      }
      // let self = this
      let validate = true
      let leftOver = 0
      let memory = 0
      let parentDir = ''
      if (/^\/{0,1}$/.test(this.params.p)) {
        parentDir = this.params.p
      } else {
        parentDir = this.params.p + '/'
      }
      for (let a of files) {
        memory += parseInt(a.size)
        a.queryOpts = {
          upload_token: localCache.getItem('token'),
          parent_dir: parentDir,
          retJson: '1'
        }
      }

      getUrl({
        p: this.params.p,
        repoId: this.params.repoId
      })
        .then(res => {
          if (res.data.code === 200) {
            // if (/^\/{0,1}$/.test(this.params.p)) {
            //   this.$refs.uploader.uploader.opts.query.parent_dir = this.params.p
            // } else {
            //   this.$refs.uploader.uploader.opts.query.parent_dir =
            //     this.params.p + '/'
            // }
            this.$refs.uploader.uploader.opts.target =
              '/seafhttp/upload-aj/' + res.data.data
          } else {
            this.$message({
              type: 'warning',
              message: res.data.message
            })
          }
          return getLeftStorage()
        })
        .then(res => {
          if (res.data.code === 200) {
            leftOver = res.data.data
            if (memory < leftOver) {
              this.fullscreenLoading = true
              for (let i = 0; i < files.length; i++) {
                // eslint-disable-next-line
                const regex = /^(?=[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\s\_\(\)]+[\u4e00-\u9fa5a-zA-Z0-9\.\-\、\[\]\_\(\)]$).{1,40}$|^[\u4e00-\u9fa5a-zA-Z0-9\-\、\[\]\.\_\(\)]{1,40}$/
                if (
                  files[i].parent != null &&
                  !regex.test(files[i].parent.name)
                ) {
                  validate = false
                  this.fullscreenLoading = false
                  this.$refs.uploader.uploader.cancel()
                  this.$message({
                    type: 'warning',
                    message:
                      '格式不正确，不能为空，只能输入数字、字母、中文和、[].-_()，长度在40个字符以内！'
                  })
                } else if (files[i].name.length > 80) {
                  validate = false
                  this.fullscreenLoading = false
                  this.$refs.uploader.uploader.cancel()
                  this.$message({
                    type: 'warning',
                    message: '文件长度在80个字符以内！'
                  })
                }
              }
              if (validate) {
                this.$refs.uploader.uploader.resume()
                this.fullscreenLoading = false
                fileList.forEach(file => {
                  this.fileList.push(file)
                })
                // this.fileList = fileList
                this.isUploaderList = true
              }
            } else {
              this.$refs.uploader.uploader.cancel()
              this.$message({
                type: 'warning',
                message: '当前用户存储空间不足！'
              })
            }
          }
        })
    },
    closeUpload(done) {
      this.$confirm('是否关闭?', '', {
        confirmButtonText: '关闭',
        cancelButtonText: '取消',
        center: true,
        showClose: false
      })
        .then(() => {
          // 取消文件上传记录日志
          if (!this.isUpdateSuccess) {
            savelog({ logKey: '173', operationResult: '1' })
          }
          this.isUpdateSuccess = false
          this.$refs.uploader.uploader.cancel()
          done()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: `已取消关闭`
          })
        })
    },
    start() {
      this.$refs.uploader.autoStart = true
    },
    fileSuccess(rootFile, file, message, chunk) {
      this.tableSelection = []
      this.params.pageNumber = 1
      this.loadData(this.params)
    },
    fileComplete(rootFile) {
      this.isUpdateSuccess = true
      // 全部上传成功后同步数据
      fileUploadSuccess({
        repoId: this.breadcrumbs[this.breadcrumbs.length - 1].repoId,
        ppath:
          this.breadcrumbs.length >= 1
            ? this.breadcrumbs[this.breadcrumbs.length - 1].p
            : '/',
        libName: '案件库'
      }).then(res => {
        if (res.data.code === 200) {
        }
      })
      this.tableSelection = []
      this.params.pageNumber = 1
      this.loadData(this.params)
      // 上传成功记录日志
      savelog({ logKey: '172', operationResult: '1' })
    },
    fileError() {
      // 上传失败记录日志
      savelog({ logKey: '172', operationResult: '0' })
    },
    download() {
      let dirVos = []
      for (let a of this.tableSelection) {
        dirVos.push({
          p: a.ppath,
          repoId: a.repoId,
          name: a.name
        })
      }
      this.fullscreenLoading = true
      if (dirVos.length === 1 && this.tableSelection[0].type === 'file') {
        addActions('post', `/yofcloudDisk/api/repos/oneFile`, {
          p: this.tableSelection[0].path,
          repoId: this.tableSelection[0].repoId
        })
          .then(res => {
            this.fullscreenLoading = false
            if (res.data.code === 200) {
              let seafileUrl = localCache.getItem('seafileUrl')
              window.location = `${seafileUrl}${res.data.data}`
            }
          })
          .catch(() => {
            this.fullscreenLoading = false
            this.tableSelection = []
            this.params.pageNumber = 1
            this.loadData(this.params)
          })
      } else {
        addActions('post', `/yofcloudDisk/api/repos/filezips`, dirVos)
          .then(res => {
            this.fullscreenLoading = false
            if (res.data.code === 200) {
              this.downloadFile(
                `/seafhttp/zip/${res.data.data.zip_token}`,
                `案件文件${getCurrentTime(new Date().getTime())}.zip`
              )
            }
          })
          .catch(() => {
            this.fullscreenLoading = false
            this.tableSelection = []
            this.params.pageNumber = 1
            this.loadData(this.params)
          })
      }
    },
    currentChange() {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mr10 {
  margin-right: 10px;
}
</style>

<style>
.mr5 {
  margin-right: 5px;
}
.caseFileLibrary {
  background: #fff;
}
.yofc-header {
  height: 48px !important;
  padding-top: 20px;
  color: #333;
  border-bottom: 1px solid #ddd;
  padding-bottom: 20px;
}
.yofc-header .el-breadcrumb__inner {
  color: #333;
  font-weight: 600;
}
.yofc-header el-breadcrumb__inner a,
.yofc-header .el-breadcrumb__inner.is-link {
  font-weight: 400;
  color: #999;
}
.yofc-header .el-breadcrumb__inner.is-link:hover {
  color: #42a0f8;
}
.yofc-header .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  color: #333;
  font-weight: 600;
}
.uploader-drop-css {
  padding: 0 !important;
  border: none !important;
  background-color: #fff !important;
}
.caseFileLibrary .icon {
  font-size: 18px !important;
}
.uploader-drop-css .uploader-btn {
  border: none;
  padding: 0 20px;
  box-sizing: border-box;
  display: inline-block;
  width: 100%;
  line-height: 30px;
}
.uploader-drop-css .uploader-drop-css {
  padding: 0;
}
.uploader-drop-css .uploader-btn:hover {
  background: #ecf5ff;
}
.caseFileLibrary .cd-crumbs-top {
  vertical-align: top;
}
.caseFileLibrary .cd-crumbs a {
  display: inline-block;
  max-width: 180px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 16px;
}
.caseFileLibrary .cloudDisk-dropdown-item {
  padding: 0 !important;
  padding: 0;
}
.uploader-drop-css .el-dropdown-menu__item {
  padding: 0 !important;
}
.uploader-drop-css .el-dropdown-menu--medium .cloudDisk-dropdown-item {
  padding: 0 !important;
}
.uploader-drop-css .el-dropdown-menu--medium .el-dropdown-menu__item {
  padding: 0 !important;
}
.caseFileLibrary .uploader-example {
  width: 100%;
}
.caseFileLibrary .uploader-example .uploader-file-status > span > i {
  display: none !important;
}
.caseFileLibrary .my-file-search .cd-search {
  width: 100% !important;
}
.caseFileLibrary .data-tables-server-css {
