<template>

<el-container id="root-app-wrap">
  <el-header id="root-app-header" height="87px">
    <span class="title" @contextmenu="openDevTool($event)">当前案件：
       <el-popover
    placement="bottom-start"
    trigger="hover"
    :content="caseObj.caseName">
    <em slot="reference">{{caseObj.caseTruncateName}}</em>
  </el-popover>
      </span>
    <div class="top-menu-wrapper">
    <ul class="top-menu">
      <li @click="changeRouter('/caseFileLibary')" :class="[currentRouterName === 'caseFileLibary'?'actived':'']"><span class="top-menu-item"><i class="iconfont" style="color:#7d62e7;" title="案件资料库">&#xe70a;</i></span><span class="text">案件资料库</span></li>
      <li @click="changeRouter('/caseHandle')" :class="[currentRouterName === 'caseHandle'?'actived':'']"><span class="top-menu-item"><i class="iconfont" style="color:#439ef7;" title="案件办理">&#xe70f;</i></span><span class="text">案件办理</span></li>
      <li @click="changeRouter('/dossier')" :class="[currentRouterName === 'dossier'?'actived':'']"><span class="top-menu-item"><i class="iconfont" style="color:#0dbb9a;" title="卷宗管理">&#xe70d;</i></span><span class="text">卷宗管理</span></li>
      <li @click="changeRouter('/normalizationCheck')" :class="[currentRouterName === 'normalizationCheck'?'actived':'']"><span class="top-menu-item"><i class="iconfont" style="color:#e96a37;" title="规范性检查">&#xe70c;</i></span><span class="text">规范性检查</span></li>
      <li><span class="top-menu-item"><i class="iconfont" style="color:#41a0fa;" title="类案分析">&#xe70e;</i></span><span class="text">类案分析</span></li>
      <li><span class="top-menu-item"><i class="iconfont" style="color:#4b6cf9;" title="智库">&#xe70b;</i></span><span class="text">智库</span></li>
    </ul>
    <div class="win-ops-wrap">
      <span class="iconfont" title="最小化" @click="minimizeWin()">&#xe6f1;</span>
      <span class="iconfont" title="关闭" @click="closeWin()">&#xe6cf;</span>
    </div>
    </div>
  </el-header>
  <el-container id="yofc-el-container">
  <el-main id="root-app-mian">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive" :caseObj="caseObjData" ></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive" :caseObj="caseObjData" ></router-view>
  </el-main>
  </el-container>
</el-container>
</template>

<script>
import _ from 'lodash'
let debug = false
// 工具函数
function getChromeVersion() {
  var arr = navigator.userAgent.split(' ')
  var chromeVersion = ''
  for (var i = 0; i < arr.length; i++) {
    if (/chrome/i.test(arr[i])) chromeVersion = arr[i]
  }
  if (chromeVersion) {
    return Number(chromeVersion.split('/')[1].split('.')[0])
  } else {
    return false
  }
}

export default {
  data() {
    if (debug) {
      return {
        caseObj: {
          caseNumber: '42011420180035600789',
          caseTruncateName: this.truncateTitle(
            '中文测试中文测试中文中文测试中文测试中文中文测试中文测试中文'
          ),
          caseName:
            '中文测试中文测试中文中文测试中文测试中文中文测试中文测试中文',
          caseFlowType: localCache.getItem('caseFlowType')
        }
      }
    } else {
      return {
        caseObj: {
          caseNumber: localCache.getItem('caseNumber'),
          caseTruncateName: this.truncateTitle(localCache.getItem('caseName')),
          caseName: localCache.getItem('caseName'),
          caseFlowType: localCache.getItem('caseFlowType')
        }
      }
    }
  },
  computed: {
    caseObjData() {
      return this.caseObj
    }
  },
  created() {
    window.getCaseData = caseObj => {}
    this.currentRouterName = this.$route.name
    let flag = /step[1-6]/.test(this.$route.name)
    if (flag) {
      this.currentRouterName = 'caseHandle'
    }
  },
  mounted() {
    let version = getChromeVersion()
    if (version < 50) {
      document.querySelector('#root-app-mian').style.height =
        'calc(100% - 87px)'
    }
  },
  methods: {
    // 根据屏幕分辨率截断显示标题
    truncateTitle(originTitle) {
      let width = window.screen.width
      if (width <= 1366) {
        return _.truncate(originTitle, { length: 7, separator: ' ' })
      } else if (width > 1366 && width <= 1440) {
        return _.truncate(originTitle, { length: 10, separator: ' ' })
      } else if (width > 1440 && width < 1920) {
        return _.truncate(originTitle, { length: 12, separator: ' ' })
      } else {
        return _.truncate(originTitle, { length: 18, separator: ' ' })
      }
    },
    openDevTool(e) {
      e.preventDefault()
      JSDebugging.ShowDevTools()
    },
    // 关闭窗口
    closeWin() {
      let flag = /step[1-6]/.test(this.$route.name)
      if (flag) {
        window.checkIsSave('/caseHandle', 'close')
      } else {
        if (eDossier) {
          eDossier.close()
        }
      }
    },
    // 最小化窗口
    minimizeWin() {
      if (eDossier) {
        eDossier.minimized()
      }
    },
    // 路由切换
    changeRouter(path) {
      this.currentRouterName = path.substring(1, path.length + 1)
      let flag = /step[1-6]/.test(this.$route.name)
      if (flag) {
        window.checkIsSave(path)
      } else {
        this.$router.push({ path: path })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
$brand-color: #42a0f8;
$aside-bg: #3e5366;
#yofc-el-container {
  height: 100%;
}
#root-app-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
}

#root-app-header {
  height: 86px;
  background: #42a0f8 url('../assets/img/win-title-bg.png') no-repeat 24px
    center;
  border-bottom: 1px solid #add3f7;
  box-sizing: border-box;
  //text-indent: 74px;
  text-align: center;
  position: relative;
  span.title {
    color: #fff;
    font-size: 16px;
    line-height: 86px;
    position: absolute;
    top: 0;
    left: 74px;
    em {
      color: #ffeb01;
      font-style: normal;
    }
  }
}

#root-app-aside {
  background-color: $aside-bg;
}

#root-app-mian {
  // height: calc(100% - 87px);
  height: 100%;
  background-color: #f2f2f2;
}

.top-menu-wrapper {
  display: inline-block;
  width: 57.291667%;
}
//右上角窗体操作按钮
.win-ops-wrap {
  position: absolute;
  right: 20px;
  top: 20px;
  span {
    display: inline-block;
    width: 40px;
    height: 40px;
    font-size: 40px;
    text-align: center;
    line-height: 40px;
    color: #fff;
    cursor: pointer;
  }
}
//顶部导航
ul.top-menu {
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
  float: left;
  text-indent: 0;
  display: flex;
  justify-content: space-between;
  li {
    display: inline-block;
    min-width: 87px;
    height: 87px;
    cursor: pointer;
    text-align: center;
    font-size: 14px;
    // padding-top: 14px;
    position: relative;
    transform: perspective(1px) translateZ(0);

    &:before {
      content: ' ';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      //background-color: #287eed;
      // background: linear-gradient(rgba(40, 126, 235, 0), rgba(40, 126, 235, 1));
      background: linear-gradient(rgba(66, 160, 248, 0), rgba(0, 72, 218, 1));
      // background:linear-gradient(180deg, rgba(66, 160, 248, 1) 0%,  rgba(0, 72, 218, 1) 100%;
      top: -1px;
      left: 0;
      bottom: 0;
      right: 0;
      opacity: 0.5;
      transform: scaleY(0);
      transform-origin: 50% 100%;
      transition-property: transform;
      transition-duration: 0.3s;
      transition-timing-function: ease-out;
    }
    &:hover:before,
    &.actived:before {
      transform: scaleY(1);
    }
    span {
      display: inline-block;
      position: relative;
      font-size: 16px;
      color: #fff;
      &.text {
        display: block;
        height: 35px;
        line-height: 35px;
        font-size: 14px;
      }
      &.top-menu-item {
        margin-top: 14px;
        height: 36px;
        width: 36px;
        border-radius: 18px;
        background-color: #fff;

        i {
          font-size: 26px;
          line-height: 36px;
        }
      }
    }
  }
}
</style>



