<template>
<div class="container-admin">
  <el-menu :default-active="$route.path" mode="horizontal" router @select="handleSelect">
    <el-menu-item index="/caseList"><span>案件列表</span></el-menu-item>
    <el-menu-item index="/filesRepot"><span>云资料库</span></el-menu-item>
    <el-menu-item index="/autoRecognition" style="line-height:46px;"><el-badge :value="fileLength" :hidden='fileLength < 1' class="item"><span>文字识别</span></el-badge></el-menu-item>
    <el-menu-item index="/tweakUp"><span>检务助手</span></el-menu-item>
  </el-menu>
  <!-- <div class="line"></div> -->
  <!--使用keep-alive会将页面缓存-->
  <keep-alive>
   <router-view v-if="$route.meta.keepAlive"></router-view>
  </keep-alive>
  <router-view v-if="!$route.meta.keepAlive"></router-view>

  <common-file-list  :fileListVisible.sync="fileListVisible" :uploadList.sync="uploadList" :downloadList.sync="downloadList"></common-file-list>
</div>
</template>

<script>
import { getAjaxPromise } from '@/api/admin'
import CommonFileList from '../components/CommonFileList'
import { Message } from 'element-ui'
export default {
  data() {
    return {
      ws: null,
      userId: localCache.getItem('userId') || '',
      timeout: 300000, // 5分钟
      timeoutObj: null,
      tt: null,
      lockReconnect: false,
      fileListVisible: false,
      uploadList: [],
      downloadList: [],
      fileLength: 0
    }
  },
  components: { CommonFileList },
  watch: {
    uploadList() {
      if (this.uploadList.length > 0 || this.downloadList.length > 0) {
        try {
          eDossier.UpDowning()
        } catch (err) {}
      } else {
        try {
          eDossier.UpDown()
        } catch (err) {}
      }
    },
    downloadList() {
      if (this.uploadList.length > 0 || this.downloadList.length > 0) {
        try {
          eDossier.UpDowning()
        } catch (err) {}
      } else {
        try {
          eDossier.UpDown()
        } catch (err) {}
      }
    }
  },
  mounted: function() {
    this.$nextTick(() => {
      // 获取可配置的IP
      this.getIp()
      // 自定义事件监听
      this.initWatchEvent()
      // 获取ocr文件识别中的文件数量
      this.getSendOcrFileCountByDocType()
      // 重置传输列表状态
      try {
        eDossier.UpDown()
      } catch (err) {}
    })
  },
  methods: {
    handleSelect(key, keyPath) {},
    getIp() {
      const param = {
        classify: 'system',
        code: 'webSocketServer'
      }

      const ipPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/system/dictionary/getByPara',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })

      ipPromise.then(res => {
        let cName = res.data.data[0].cName
        this.ip = `ws://${cName}/edossier_websocket/socketServer?userId=${
          this.userId
        }&connectType=admin`
        this.createWebSocket()
      })
    },
    createWebSocket() {
      try {
        this.ws = new WebSocket(this.ip)
        this.init(this)
      } catch (e) {
        console.log('catch')
        this.lockReconnect = false
        this.reconnect(this)
      }
    },
    init(that) {
      this.ws.onopen = () => {
        this.ws.send('hello')
        // 心跳检测
        this.heartCheckStart(that)
      }
      this.ws.onerror = function() {
        console.log('发生异常了')
      }
      this.ws.onmessage = evt => {
        console.log(evt)
        let data = JSON.parse(evt.data)
        // if (data.ocrStatus === 'OCR_COMPLETION' || data.ocrStatus === ' FAIL') {
        this.getSendOcrFileCountByDocType()
        // }
        if (evt) {
          if (this.$route.path === '/autoRecognition') {
            if (data.msgType === 'MSG_TYPE_OCR') {
              window.agReceive('MSG_TYPE_OCR')
            }
          }
        }
        console.log('数据已接收...')
        that.lockReconnect = true
        // this.tt = null
        clearInterval(this.tt)
        // this.tt && this.clearInterval(this.tt)
        // this.heartCheckSet()
      }
      this.ws.onclose = function() {
        // 关闭 websocket
        console.log('连接已关闭...')
        if (that) {
          that.lockReconnect = false
          that.reconnect(that)
        }
      }
      this.reconnect = that => {
        if (that.lockReconnect) {
          return
        }
        that.lockReconnect = true
        // 没连接上会一直重连，设置延迟避免请求过多
        clearInterval(this.tt)
        this.tt = setInterval(function() {
          that.createWebSocket()
          that.lockReconnect = false
        }, 3000)
      }
      this.heartCheckStart = that => {
        this.timeoutObj = setInterval(function() {
          that.ws.send('HeartBeat')
        }, that.timeout)
      }
      this.heartCheckSet = () => {
        clearInterval(this.timeoutObj)
        this.heartCheckStart(that)
      }
    },
    // 自定义事件监听
    initWatchEvent() {
      // 添加上传任务到上传列表
      this.$root.eventHub.$on('addItems2UploadFileList', items => {
        // this.fileListVisible = true
        items.forEach(item => {
          this.uploadList.push(item)
        })
        Message({
          message: '已添加至上传列表',
          type: 'success',
          duration: 2000
        })
      })
      // 移除上传项上传列表
      this.$root.eventHub.$on('removeItemFromUploadFileList', file => {
        const index = this.uploadList.findIndex(item => item === file)
        this.uploadList.splice(index, 1)
      })
      // 上传成功回调
      this.$root.eventHub.$on('fileUploadSuccess', file => {
        const index = this.uploadList.findIndex(item => item === file)
        this.uploadList.splice(index, 1)
      })

      // ocr文件上传
      this.$root.eventHub.$on('ocrFileUpload', () => {
        this.getSendOcrFileCountByDocType()
      })

      // 添加下载任务到下载列表
      this.$root.eventHub.$on('addItems2DownloadList', items => {
        // this.fileListVisible = true
        items.forEach(item => {
          this.downloadList.push(item)
        })
        Message({
          message: '已添加至下载列表',
          type: 'success',
          duration: 2000
        })
      })
      // 移除下载项
      this.$root.eventHub.$on('removeItemFromDownloadList', file => {
        const index = this.downloadList.findIndex(item => item === file)
        this.downloadList.splice(index, 1)
      })
      // 下载成功回调
      this.$root.eventHub.$on('downloadSuccess', file => {
        const index = this.downloadList.findIndex(item => item === file)
        this.downloadList.splice(index, 1)
      })

      // 显示文件列表
      this.$root.eventHub.$on('showFileList', () => {
        this.fileListVisible = !this.fileListVisible
      })
      // 关闭文件列表
      this.$root.eventHub.$on('closeCommonFileList', () => {
        this.fileListVisible = false
      })
    },
    // 获取ocr文件识别中的文件数量
    getSendOcrFileCountByDocType() {
      const param = {
        docType: 'OCR',
        caseNumber: '',
        userId: localCache.getItem('userId')
      }
      const sendOrcNumPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/case_file/getSendOcrFileCountByDocType',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })
      sendOrcNumPromise.then(res => {
        if (res.data.code === '200') {
          this.fileLength = res.data.data
        }
      })
    }
  },
  destroyed() {
    this.ws.onclose()
    clearInterval(this.tt)
    clearInterval(this.timeoutObj)
  }
}
</script>

<style lang="scss">
.container-admin {
  .el-badge__content.is-fixed {
    top: 14px;
    z-index:10;
  }
  height: 100%;

  .el-menu--horizontal > .el-menu-item {
    height: 47px;
    line-height: 47px;
    margin: 0;
    padding: 0;
    width: 25%;
    text-align: center;
    border: none;
    font-weight: 600;
    padding-bottom: 1px;

    span {
      height: calc(100% - 2px);
      padding: 0 8px;
      display: inline-block;
      border-bottom: 2px solid transparent;
    }
  }

  .el-menu--horizontal > .el-menu-item:hover {
    color: #42a0f8;
  }

  .el-menu--horizontal > .el-menu-item.is-active {
    color: #42a0f8;
    span {
      border-bottom: 2px solid #42a0f8;
    }
  }

  route-view {
    height: 100%;
    position: relative;
  }
}

.no-result {
  text-align: center;
  margin-top: 200px;
  img {
    margin-bottom: 10px;
  }
  p {
    margin: 0;
  }
}
</style>



