<template>
    <router-view style="height: 100%;"></router-view>
</template>

<script>
// 全局图标
import './sass/icons/iconfont.scss'
import './sass/icons/iconfont'
import './sass/default.scss'
import EE from 'events'

export default {
  components: {},
  methods: {},
  created() {
    // 事件总线
    if (window.eventEmitter) {
      this.ee = window.eventEmitter
      // 支持创建10000个listener
      this.ee.setMaxListeners(10000)
    } else {
      this.ee = window.eventEmitter = new EE()
      this.ee.setMaxListeners(10000)
    }
    document.addEventListener('keydown', function(e) {
      if (e.keyCode === 123) {
        try {
          JSDebugging.ShowDevTools()
        } catch (error) {}
      }
    })
  }
}
</script>



