<template>
  <div id="autoRecognition" style="height:100%">
      <div class="ed-search-group">
          <input type="text" placeholder="请输入文件名称搜索" v-model="searchKeyWord" maxlength="40" @keyup.enter="searchFileByKeyword()" @input="searchFileByKeyword()">
          <i class="icon iconfont ed-clear-button" @click="clearInput()" v-show="searchKeyWord.length>0">&#xe6cf;</i>
          <i class="icon iconfont ed-search-button">&#xe6e8;</i>
          <uploader :options="uploadOptions" ref="uploader" :file-status-text="statusText" :autoStart="false" @files-added="onFileAdded" @files-submitted="onFilesSubmitted" @file-success="onFileSuccess" @fileError="onFileError">
            <uploader-btn class="upload-btn" :attrs="attrs">
              <i class="icon iconfont" style="margin-right:8px;">&#xe6e2;</i><span style="position:relative;top:1px;">上传</span>
            </uploader-btn>
          </uploader>
      </div>
      <!-- accept=".bmp,.dcx,.pcx,.png,.jpg,.jpeg,.jp2,.jpc,.jfif,.pdf,.tif,.tiff,.gif,.djvu,.djv,.jb2" -->
      <div class="ed-ocrList"  v-loading="loading" style="height:calc(100% - 145px)">
          <ul>
              <li style="border-bottom:none;height:40px;line-height:55px;color:#999;">
                <div>
                  <input type="checkbox" v-model="checkAll"  @change="handleCheckAllChange" :disabled="ocrList.length === 0" :class="[ocrList.length === 0?'gray':'']">
                  <span style="vertical-align: middle;">文件名</span>
                  </div>
                <div><span style="color:#999;">识别进度</span></div>
                <div>
                    <i class="icon iconfont" title="下载" @click="getIds('download')" :class="[ocrList.length === 0?'gray':'']" :disabled="ocrList.length === 0">&#xe6fe;</i>
                    <i class="icon iconfont" title="删除" @click="getIds('delete')" :class="[ocrList.length === 0?'gray':'']" :disabled="ocrList.length === 0">&#xe6df;</i>
                </div>
              </li>
          </ul>
          <ul style="max-height: 98%;overflow-y: scroll;">
              <li v-for="item in ocrList" :key="item.id">
                  <div v-if="getFileSuffix(item.original_doc_name) === 'pdf'">
                      <input v-if="item.status === 'OCR_COMPLETION' || item.status ==='FAIL'" type="checkbox" v-model="item.isChecked"  @change="handleCheckOneChange(item)">
                      <em v-if="item.status !== 'OCR_COMPLETION' && item.status !=='FAIL'" style="display:inline-block;width:20px;height:14px"></em>
                      <svg class="icon" aria-hidden="true"><use xlink:href="#icon-pdfwenjian"></use></svg>
                      <a v-if="item.status == 'FAIL'" href='javascript:void(0)' v-bind:title="item.original_doc_name" style="cursor: text;opacity:1;color:#ccc;">{{item.original_doc_name}}</a>
                      <a v-if="item.status != 'FAIL'" href='javascript:void(0)' v-bind:title="item.original_doc_name" @click="showOcrFile(item)" style="cursor:pointer;">{{item.original_doc_name}}</a>
                  </div>
                  <div v-if="getFileSuffix(item.original_doc_name) !== 'pdf'">
                      <input v-if="item.status === 'OCR_COMPLETION' || item.status ==='FAIL'" type="checkbox" v-model="item.isChecked"  @change="handleCheckOneChange(item)">
                      <em v-if="item.status !== 'OCR_COMPLETION' && item.status !=='FAIL'" style="display:inline-block;width:17px;height:14px"></em>
                      <!-- <el-checkbox v-model="item.isChecked" style="vertical-align: middle;margin-right:6px;" @change="handleCheckOneChange(item)"></el-checkbox> -->
                      <svg class="icon" aria-hidden="true"><use xlink:href="#icon-tupianwenjian"></use></svg>
                      <a v-if="item.status == 'FAIL'" href='javascript:void(0)' v-bind:title="item.original_doc_name" style="cursor:text;opacity:1;color:#ccc;">{{item.original_doc_name}}</a>
                      <a v-if="item.status != 'FAIL'" href='javascript:void(0)' v-bind:title="item.original_doc_name" @click="showOcrFile(item)">{{item.original_doc_name}}</a>
                  </div>
                  <div v-if="item.status !== 'OCR_COMPLETION' && item.status !=='FAIL'">
                      <i class="el-icon-loading"></i>
                      <a>识别中</a>
                  </div>
                  <div v-if="item.status === 'OCR_COMPLETION'">
                      <i class="icon iconfont" style="color:#23ca85;">&#xe6cd;</i>
                      <a>识别成功</a>
                  </div>
                  <div v-if="item.status === 'FAIL' ">
                      <i class="icon iconfont" style="color:#f05468;">&#xe6e9;</i>
                      <a>识别失败</a>
                  </div>
                  <div v-if="item.status !== 'OCR_COMPLETION' && item.status !=='FAIL'">
                  </div>
                  <div v-if="item.status === 'OCR_COMPLETION'" class="ed-operator-button">
                      <i class="icon iconfont" title="下载" @click="doDownload(item.id,1)">&#xe6fe;</i>
                      <i class="icon iconfont" title="删除" @click="deleltFile(item.id,1)">&#xe6df;</i>
                  </div>
                    <div v-if="item.status === 'FAIL'" class="ed-operator-button">
                      <i class="icon iconfont" title="删除" @click="deleltFile(item.id) && num ++">&#xe6df;</i>
                  </div>
              </li>
              <div v-if="ocrList.length == 0" class="no-result">
                 <img src="../assets/img/no_result.png" alt="">
                 <p style="text-align: center;">该列表暂无数据！</p>
              </div>
              <div class="block" style="clear:both;margin-top:12px;" v-if="ocrList.length > 0">
                <span class="totalRow">共 <a href="javascript:void(0)" v-bind:title="totalRow">{{totalRow}}</a> 条</span>
                <el-pagination
                    small
                    background
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1"
                    :page-size="15"
                    :pager-count="5"
                    layout="prev, pager, next,jumper"
                    :total="totalRow">
                </el-pagination>
              </div>
          </ul>
      </div>
  </div>
</template>

<script>
import {
  fetchOcrList,
  deleteOcrList,
  getCaseFileById,
  getAjaxPromise
} from '@/api/admin'
export default {
  name: 'autoRecognition',
  data() {
    return {
      searchKeyWord: '',
      currentPage1: 1,
      ocrList: [],
      totalRow: 0,
      userId: localCache.getItem('userId') || 14,
      originalDocName: '',
      cur_page: 1,
      fileList: [],
      autoStart: false,
      errorMsgList: [],
      uploadOptions: {
        allowDuplicateUploads: true,
        target: '/api/4static/ocr_file/uploadOcrBusinessFile',
        testChunks: false,
        chunkSize: 1024 * 1024 * 1024 * 20,
        // query: {
        //   docType: 'OCR',
        //   docSubType: 'ZJJ',
        //   caseNumber: '',
        //   userId: localCache.getItem('userId') || 1001
        // }
        query: (file, xhr) => {
          return file.queryOpts
        }
      },
      statusText: {
        success: '成功',
        error: '上传失败',
        uploading: '上传中',
        paused: '暂停中',
        waiting: '等待中'
      },
      loading: false,
      // isIndeterminate: true,
      checkAll: false,
      num: 0,
      uploadSuccessFlag: true,
      attrs: {
        // accept: 'application/pdf,image/*'
      }
    }
  },
  mounted: function() {
    this.$nextTick(() => {
      window.agReceive = type => {
        if (type) {
          this.showData(type)
        }
      }
      this.showData()
      // 默认查询一次ocr识别中的文件数量
      this.$root.eventHub.$emit('ocrFileUpload', '')
    })
  },
  methods: {
    showData(type) {
      if (type === 'MSG_TYPE_OCR') {
        this.getOcrList(this.cur_page, '')
      } else {
        this.getOcrList(1, '')
      }
    },
    handleCheckAllChange(val) {
      if (this.checkAll) {
        this.ocrList.forEach((item, index) => {
          if (item.status === 'OCR_COMPLETION' || item.status === 'FAIL') {
            item.isChecked = true
          }
        })
      } else {
        this.ocrList.forEach((item, index) => {
          if (item.status === 'OCR_COMPLETION' || item.status === 'FAIL') {
            item.isChecked = false
          }
        })
      }
    },
    handleCheckOneChange(item) {
      this.ocrList.forEach((item1, index) => {
        if (item1.id === item.id) {
          item1.isChecked = item.isChecked
        }
      })
      if (item.isChecked) {
        let i = 0
        let i1 = 0
        this.ocrList.forEach((item, index) => {
          if (item.status === 'OCR_COMPLETION' || item.status === 'FAIL') {
            i = i + 1
            if (item.isChecked === true) {
              i1 = i1 + 1
            }
          }
        })
        if (i === i1) {
          this.checkAll = true
        }
      } else {
        let j = this.ocrList.length
        this.ocrList.forEach((item, index) => {
          if (!item.isChecked) {
            j = j - 1
          }
        })
        if (this.ocrList.length > j) {
          this.checkAll = false
        }
      }
    },
    onFileAdded(files, fileList) {},
    onFilesSubmitted(files, fileList) {
      this.errorMsgList = []
      let validate = true
      this.uploadSuccessFlag = true
      let great2gFlag = true
      for (let i = 0; i < files.length; i++) {
        // const file = files[i]
        // const types = [
        //   'image/jpeg',
        //   'image/gif',
        //   'image/gif',
        //   'image/png',
        //   'image/bmp',
        //   'application/pdf',
        //   'image/tiff',
        //   'image/jp2'
        // ]
        // if (
        //   types.indexOf(file.fileType) < 0 ||
        //   file.size / 1024 / 1024 / 1024 >= 2 ||
        //   file.size / 1024 / 1024 / 1024 === 0
        // ) {
        //   validate = false
        //   this.$refs.uploader.uploader.removeFile(file)
        //   this.$refs.uploader.uploader.cancel()
        //   if (file.size / 1024 / 1024 / 1024 >= 2) {
        //     this.$message({
        //       message: '上传的文件大小不能超过2G',
        //       type: 'error',
        //       duration: 3000
        //     })
        //   } else if (types.indexOf(file.fileType) < 0) {
        //     this.$message({
        //       message: '上传的文件格式只能为图片和pdf',
        //       type: 'error',
        //       duration: 3000
        //     })
        //   } else if (file.size / 1024 / 1024 / 1024 === 0) {
        //     this.$message({
        //       message: '上传的文件大小不能为0kb',
        //       type: 'error',
        //       duration: 3000
        //     })
        //   }
        //   continue
        // }
        const file = files[i]
        if (file.size / 1024 / 1024 / 1024 >= 0.5) {
          if (great2gFlag) {
            setTimeout(function() {
              window.vm.$message({
                message: '上传的文件大小不能超过500Mb',
                type: 'error',
                duration: 3000
              })
            }, 1500)
            great2gFlag = false
          }
          file.queryOpts = {
            docType: 'OCR',
            docSubType: 'overSize',
            caseNumber: '',
            userId: localCache.getItem('userId') || ''
          }
          file.serverError = true
        } else {
          file.queryOpts = {
            docType: 'OCR',
            // docSubType: 'ZJJ',
            caseNumber: '',
            userId: localCache.getItem('userId') || ''
          }
        }
        continue
      }

      if (validate) {
        this.$refs.uploader.uploader.resume()
        this.$root.eventHub.$emit('addItems2UploadFileList', fileList)
      }
    },
    onFileSuccess(rootFile, fileList, message, thunk) {
      if (JSON.parse(message).code === '500') {
        rootFile.serverError = true

        let isInArrayFlag = this.isInArray(
          this.errorMsgList,
          JSON.parse(message).msg
        )

        if (isInArrayFlag) {
          this.uploadSuccessFlag = false
        } else {
          this.uploadSuccessFlag = true
        }

        if (this.uploadSuccessFlag) {
          this.errorMsgList.push(JSON.parse(message).msg)
          setTimeout(function() {
            window.vm.$message({
              message: JSON.parse(message).msg,
              type: 'error',
              duration: 3000
            })
          }, 1000)
        }
      } else {
        this.searchKeyWord = ''
        this.getOcrList(1, '')
        this.$root.eventHub.$emit('fileUploadSuccess', rootFile)
      }
      this.$root.eventHub.$emit('ocrFileUpload', fileList)
    },
    onFileError() {
      if (this.uploadSuccessFlag) {
        this.$message({
          message: '文件上传失败',
          type: 'warning',
          duration: 3000
        })
        this.uploadSuccessFlag = false
        this.$root.eventHub.$emit('ocrFileUpload', '')
      }
    },
    handleCurrentChange(val) {
      this.checkAll = false
      this.getOcrList(val, this.searchKeyWord)
    },
    isInArray(arr, value) {
      for (var i = 0; i < arr.length; i++) {
        if (value === arr[i]) {
          return true
        }
      }
      return false
    },
    // 获取ocr列表
    getOcrList(currentPage, originalDocName) {
      this.cur_page = currentPage
      this.num = 0
      let param = {
        caseNumber: '',
        docType: 'OCR',
        docSubtype: '',
        originalDocName: originalDocName,
        currentPageIndex: currentPage,
        pageSize: 15,
        userId: this.userId
      }
      this.loading = true
      let ocrPromise = fetchOcrList(param)
      ocrPromise.then(res => {
        this.loading = false
        this.ocrList = res.data.list
        if (this.ocrList.length > 0) {
          this.ocrList.forEach((item, index) => {
            if (item.status === 'OCR_COMPLETION' || item.status === 'FAIL') {
              item.isChecked = false
            }
          })
        }
        this.totalRow = res.data.totalRow
      })
    },
    // 清楚input框
    clearInput() {
      this.searchKeyWord = ''
      this.getOcrList(1, this.searchKeyWord)
    },
    // 获取文件名后缀
    getFileSuffix(fileName) {
      return fileName.substring(fileName.lastIndexOf('.') + 1).toLowerCase()
    },
    // 删除文件
    deleltFile(ids, type) {
      if (type === 1) {
        this.num = this.num + 1
      }
      const jsonParam = { ids: ids }
      this.$confirm('确定要删除此文件？', '', {
        confirmButtonText: '删除',
        cancelButtonText: '取消',
        cancelButtonClass: 'cancel',
        confirmButtonClass: 'danger',
        center: true,
        showClose: false,
        customClass: 'arDialog'
      }).then(() => {
        this.loading = true
        const deletePromise = deleteOcrList(jsonParam)
        deletePromise.then(res => {
          this.loading = false
          if (res.data.status === 'success') {
            this.$message({
              message: '删除成功',
              type: 'success',
              duration: 2000
            })
            this.checkAll = false

            if (this.cur_page === 1) {
              this.getOcrList(1, this.searchKeyWord)
            } else {
              if (this.num === this.ocrList.length) {
                this.getOcrList(this.cur_page - 1, this.searchKeyWord)
              } else {
                this.getOcrList(this.cur_page, this.searchKeyWord)
              }
            }
          } else {
            if (res.data.msg === '文件已被其他用户删除') {
              this.$message({
                message: '删除成功',
                type: 'success',
                duration: 2000
              })
            } else {
              this.$message({
                message: res.data.msg,
                type: 'success',
                duration: 2000
              })
            }
            if (this.cur_page === 1) {
              this.getOcrList(1, this.searchKeyWord)
            } else {
              if (this.num === this.ocrList.length) {
                this.getOcrList(this.cur_page - 1, this.searchKeyWord)
              } else {
                this.getOcrList(this.cur_page, this.searchKeyWord)
              }
            }
          }
        })
      })
    },
    showOcrFile(item) {
      const filePromise = getCaseFileById({ id: item.id })
      filePromise.then(res => {
        if (res.data.status === 'success') {
          let data = res.data.data
          if (data === null || data === '') {
            this.$message({
              message: '文件已经被其他用户删除',
              type: 'warning'
            })
          } else {
            // 只有在文件识别成功后才能打开文件对比
            if (data.status === 'OCR_COMPLETION') {
              let param = {
                data: {
                  docId: item.id
                }
              }
              eDossier.openPdfCompare_WithOutBase64(JSON.stringify(param))
            } else {
              this.$message({
                message: '识别中，请稍后',
                type: 'warning'
              })
            }
          }
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error'
          })
        }
      })
    },
    // 下载
    doDownload(id) {
      const filePromise = getCaseFileById({ id: id })
      filePromise.then(res => {
        if (res.data.status === 'success') {
          if (res.data.data.wordGroup === '' || res.data.data.wordFile === '') {
            this.$notify.error({
              title: '错误',
              message:
                res.data.data.originalDocName +
                '识别后的文件在文件服务器中路径有误,请联系管理员'
            })
            return
          }
          let httpPath =
            '/' + res.data.data.wordGroup + '/' + res.data.data.wordFile
          // window.location = httpPath
          let originalDocName = res.data.data.originalDocName
          let fileName =
            originalDocName.substring(0, originalDocName.lastIndexOf('.')) +
            '.doc'
          this.local_download(httpPath, 'application/msword', fileName)
        } else {
          this.$notify.error({
            title: '错误',
            message: res.data.msg
          })
          this.getOcrList(1, '')
        }
      })
    },
    local_download(url, type, name) {
      this.$root.eventHub.$emit('addItems2DownloadList', [
        { url: url, fileName: name }
      ])
      // let link = document.createElement('a')
      // link.style.display = 'none'
      // link.href = url
      // link.setAttribute('download', name)
      // document.body.appendChild(link)
      // link.click()
    },
    searchFileByKeyword() {
      this.getOcrList(1, this.searchKeyWord)
    },
    getIds(type) {
      if (this.ocrList.length === 0) {
        return
      }
      this.num = 0
      let ids = ''
      this.ocrList.forEach((item, index) => {
        if (item.status === 'OCR_COMPLETION' || item.status === 'FAIL') {
          if (item.isChecked === true) {
            this.num = this.num + 1
            if (this.num === 1) {
              ids = item.id
            } else {
              ids += ',' + item.id
            }
          }
        }
      })
      if (type === 'delete') {
        if (ids === '') {
          this.$message({
            message: '请选择要删除的文件',
            type: 'warning',
            duration: 2000
          })
          return false
        } else {
          this.deleltFile(ids)
        }
      } else if (type === 'download') {
        if (ids === '') {
          this.$message({
            message: '请选择要下载的文件',
            type: 'warning',
            duration: 2000
          })
          return false
        } else {
          this.checkOcrFileWhenDownload(ids)
        }
      }
    },
    // 批量下载文件
    doManyDownload(ids) {
      let stime = this.getCurrentTime()
      this.$root.eventHub.$emit('addItems2DownloadList', [
        {
          url: `/api/4static/dossier/management/downLoadFileListInZip?ids=${ids}`,
          fileName: `OCR文件${stime}.zip`
        }
      ])
      // window.location = `/api/4static/dossier/management/downLoadFileListInZip?ids=${ids}`
    },
    getCurrentTime() {
      let date = new Date()
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate =
        date.getFullYear() +
        '' +
        month +
        '' +
        strDate +
        '' +
        date.getHours() +
        '' +
        date.getMinutes() +
        '' +
        date.getSeconds()
      return currentdate
    },
    // 当下载的时候检查ocr文件
    checkOcrFileWhenDownload(ids) {
      const params = {
        ids: ids
      }
      const checkOcrFileWhenDownloadPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/dossier/management/checkOcrFileWhenDownload',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        params
      })
      checkOcrFileWhenDownloadPromise.then(res => {
        if (res.data.code === '200') {
          if (res.data.data.msg) {
            this.$notify.error({
              title: '错误',
              message: res.data.data.msg
            })
          } else {
            if (this.num > 1) {
              // 批量下载文件
              this.doManyDownload(ids)
            } else {
              // 单个下载文件
              this.doDownload(ids)
            }
          }
        }
      })
    }
  }
}
</script>

<style lang="scss">
#autoRecognition {
  line-height: 1.5;
  letter-spacing: -0.15px;
  color: #666;
  margin: 0 16px;
  ::-webkit-scrollbar {
    width: 0px;
  }
  .el-upload-list {
    display: none !important;
  }
  .ed-search-group {
    margin-top: 20px;
    position: relative;
    input {
      float: left;
      width: calc(100% - 142px) !important;
      height: 30px;
      line-height: 30px;
      border: 1px solid #ddd;
      border-radius: 2px;
      color: #666;
      // font-size: 12px;
      padding-left: 30px;
      padding-right: 20px;
      &:focus {
        border-color: #42a0f8;
        outline: none;
      }
      &:hover {
        border-color: #42a0f8;
        outline: none;
      }
    }
    input::-webkit-input-placeholder {
      color: #ccc;
    }
    .ed-clear-button {
      font-size: 22px;
      position: absolute;
      top: 2px;
      right: 90px;
      cursor: pointer;
      &:hover {
        color: #42a0f8;
      }
    }
    .ed-search-button {
      height: 2em;
      position: absolute;
      left: 10px;
      top: 7px;
      font-size: 14px;
    }
    .upload-btn {
      float: right;
      color: #fff;
      background-color: #42a0f8;
      border-color: #42a0f8;
      border-radius: 3px;
      padding: 5px 12px;
      &:hover {
        background-color: #5fb1fe;
        border-color: #5fb1fe;
      }
    }
  }

  .gray {
    color: #cccccc !important;
    &:focus,
    &:hover,
    &:active {
      color: #cccccc !important;
      border-color: #cccccc !important;
    }
  }

  .ed-ocrList {
    clear: both;
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style: none;
        height: 48px;
        line-height: 48px;
        border-bottom: 1px solid #ddd;
        position: relative;
        .ed-operator-button {
          display: none;
        }
        &:hover {
          .ed-operator-button {
            display: inline-block;
          }
        }
        svg,
        i {
          vertical-align: middle;
          font-size: 18px;
        }
        a {
          vertical-align: middle;
          display: inline-block;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          text-decoration: none;
        }
        div {
          position: absolute;
          top: 0;
          &:nth-child(1) {
            width: calc(100% - 150px);
            left: 0;
            a {
              max-width: calc(100% - 46px);
              color: #666;
              &:hover {
                color: #42a0f8;
              }
            }
            > input {
              width: 14px;
              height: 14px;
              background-color: #fff;
              -webkit-appearance: none;
              border: 1px solid #c9c9c9;
              border-radius: 2px;
              outline: none;
              vertical-align: middle;
              margin-left: 0;
              &:focus,
              &:hover {
                border-color: #42a0f8;
              }
            }
            input[type='checkbox']:checked {
              background: url('../assets/img/gou.png') no-repeat center;
              background-color: #42a0f8;
              border: none;
            }
          }
          &:nth-child(2) {
            width: 78px;
            right: 60px;
            cursor: text;
            color: #666;
            i {
              font-size: 15px;
              cursor: text;
            }
            a {
              color: #666;
            }
          }
          &:nth-child(3) {
            text-align: right;
            width: 55px;
            right: 0;
            i {
              font-size: 15px;
              margin-left: 10px;
              color: #999;
              cursor: pointer;
              &:hover {
                color: #42a0f8;
              }
            }
          }
        }
      }
    }
  }
  .el-pagination {
    width: auto;
    float: right;
    font-weight: 400;
    font-size: 12px !important;
    span {
      font-size: 12px !important;
    }
  }
  .no-result {
    text-align: center;
    margin-top: 200px;
    img {
      margin-bottom: 10px;
    }
    p {
      margin: 0;
    }
  }
  .el-pagination__jump {
    margin-left: 5px;
  }
  .el-pager li {
    height: 22px !important;
    line-height: 22px !important;
  }
  .el-pagination__editor.el-input {
    width: 40px;
  }
  .totalRow {
    position: relative;
    top: 2px;
    a {
      display: inline-block;
      max-width: 30px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      vertical-align: middle;
      color: #666;
      cursor: text;
    }
  }
}
.arDialog {
  width: 320px !important;
  padding-bottom: 16px !important;
}

.arDialog {
  .el-message-box__content {
    border-bottom: 1px solid #ddd !important;
    padding-bottom: 15px !important;
    padding-top: 25px !important;
  }
  .el-message-box__header {
    padding-top: 15px !important;
    display: none;
  }
  .el-message-box__btns {
    padding-top: 8px !important;
  }
  .el-message-box__message p {
    padding-top: 10px;
  }
  .danger {
    background: #f05468 !important;
    border-color: #f05468 !important;
  }
  .el-message-box__btns button:nth-child(2) {
    margin-left: 30px !important;
  }
}
</style>



