<template>
  <el-container class="dossier" v-loading="fullscreenLoading">
    <el-header class="dossier-header">
  <el-tabs v-model="activeName" @tab-click="tabClick" class="yofc-tab">
    <el-tab-pane label="证据卷" name="first"></el-tab-pane>
    <el-tab-pane label="文书卷" name="second"></el-tab-pane>
  </el-tabs>
    </el-header>
    <el-main>
          <el-row>
            <el-col :span="12">
                <el-button size="mini"  @click="uploadFile()"><i class="iconfont mr5">&#xe6e2;</i>上传</el-button>
                <el-button :disabled="tableSelection.length==0" size="mini" @click="download"><i class="iconfont mr5 mt5">&#xe6fe;</i>下载</el-button>
                <el-button :disabled="tableSelection.length==0" size="mini" @click="deleteFile"><i class="iconfont mr5 mt5">&#xe6df;</i>删除</el-button>
            </el-col>
            <el-col :span="12" class="file-search">
                <ym-search @search="searchData" :searchPlaceholder="'请输入文件名搜索文件'"/>
            </el-col>
          </el-row>
          <div class="data-tables-server-css">
              <data-tables-server :data="tableData"  :total="total" @selection-change="handleSelectionChange" :loading="loading" @query-change="loadData" :page-size="0" :pagination-props="{background: true,  pageSizes: [10,30,50,100] }" :current-page.sync="params.pageNumber">
                <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column label="名称" min-width="100px">
                  <template slot-scope="scope">
                    <svg class="icon" aria-hidden="true"><use :xlink:href="scope.row.fileSuffix|fileSuffix"></use></svg>
                    {{scope.row.ocrFileName}}
                    </template>
                </el-table-column>
                <el-table-column label="状态" min-width="100px">
                  <template slot-scope="scope">
                    <span class="dot-status" :class="scope.row.status |dotStatus"></span>
                    {{scope.row.statusName}}
                    </template>
                </el-table-column>
                <el-table-column label="上传时间" min-width="100px">
                  <template slot-scope="scope">
                    {{scope.row.createTime}}
                    </template>
                </el-table-column>
              </data-tables-server>
          </div>
    </el-main>
            <!-- 文件上传 -->
    <!-- <el-dialog :title="'文件上传'" class="cd-size-lag yofc-upload-list" :close-on-click-modal="false" :visible.sync="isUploaderList" :before-close="closeUpload" append-to-body> -->
      <file-list @del="delFile" v-if="fileListVisible" :fileList="fileList"/>
    <!-- </el-dialog> -->
  </el-container>
</template>

<script>
import {
  getFileListByDocType,
  addActions,
  getAjaxPromise,
  checkOcrFileIsExist
} from '@/api/admin'
import YmSearch from '@/components/YmSearch'
import FileList from '@/components/FileList'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'dossier',
  components: { YmSearch, FileList },
  mounted() {
    this.$nextTick(() => {
      // 获取可配置的IP
      this.getIp()
      window.fileAction.UploadSuccess = str => {
        let index = this.fileList.findIndex(
          item => item.fileName === str.fileName
        )
        if (Number(str.state) === 0) {
          this.fileList[index].progress = 1
          this.fileList[index].progressStyle = {
            progress: `100%`,
            webkitTransform: 'translateX(0%)',
            mozTransform: 'translateX(0%)',
            msTransform: 'translateX(0%)',
            transform: 'translateX(0%)'
          }
          // this.$message({
          //   type: 'success',
          //   message: `上传成功`
          // })
          this.fileList.splice(index, 1)
          this.loadData(this.params)
        } else {
          this.fileList[index].progress = 1
          this.fileList[index].progressStyle = {
            progress: `100%`,
            webkitTransform: 'translateX(0%)',
            mozTransform: 'translateX(0%)',
            msTransform: 'translateX(0%)',
            transform: 'translateX(0%)',
            background: '#fff2f2'
          }
          this.$message({
            type: 'warning',
            message: `上传失败`
          })
        }
      }
      window.fileAction.fileList = str => {
        // debugger
        for (let a of str.fileList) {
          let Num = 0
          if (a.formatedSize) {
            Num = a.formatedSize.substr(a.formatedSize.length - 1, 1)
          } else {
            Num = 1
          }
          a.progress = 0.1 + Num * 0.01
          this.fileList.push(a)
        }
        this.fileListVisible = true
        // this.fileList = str.fileList
        // this.isUploaderList = true
      }

      // 移除上传项上传列表
      this.$root.eventHub.$on('removeItemFromFileList', file => {
        const index = this.fileList.findIndex(item => item === file)
        this.fileList.splice(index, 1)
      })
      // 清空列表
      this.$root.eventHub.$on('clearFileList', file => {
        this.fileListVisible = false
        this.fileList = []
        eDossier.clearFileList()
      })
    })
  },
  data() {
    return {
      msg: '卷宗',
      activeName: 'first',
      total: 0,
      fileList: [
        // { fileName: '1212121.pdf', progress: 1, formatedSize: '1222212' },
        // { fileName: '121212112122.pdf', progress: 0.22, formatedSize: '121212' }
      ],
      tableSelection: [],
      loading: false,
      tableData: [],
      params: {
        caseNumber: this.caseObj.caseNumber,
        docSubtype: '',
        docType: 'JZ',
        pageNumber: 1,
        pageSize: 10,
        originalDocName: ''
      },
      ws: null,
      userId: localCache.getItem('userId') || 1001,
      timeout: 300000, // 5分钟
      timeoutObj: null,
      tt: null,
      lockReconnect: false,
      fileListVisible: false,
      uploadList: [],
      downloadList: [],
      isUploaderList: false,
      fullscreenLoading: false
    }
  },
  created() {},
  methods: {
    async loadData(queryInfo) {
      this.params.docSubtype = this.activeName === 'first' ? 'ZJJ' : 'WSJ'
      this.params.pageNumber = queryInfo.page
        ? queryInfo.page
        : queryInfo.pageNumber
      this.params.pageSize = queryInfo.pageSize
      this.loading = true
      await getFileListByDocType(this.params).then(res => {
        this.loading = false
        if (!res.data.data) return []
        this.tableData = res.data.data.list
        this.total = res.data.data.totalRow
      })
    },
    tabClick(tab, event) {
      this.activeName = tab.name
      this.params.originalDocName = ''
      this.params.pageNumber = 1
      this.loadData(this.params)
    },
    delFile(name) {
      let index = this.fileList.findIndex(item => item.fileName === name)
      this.fileList.splice(index, 1)
    },
    getIp() {
      const param = {
        classify: 'system',
        code: 'webSocketServer'
      }

      const ipPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/system/dictionary/getByPara',
        headers: {
          contentType: 'application/json'
        },
        data: param
      })

      ipPromise.then(res => {
        let cName = res.data.data[0].cName
        this.ip = `ws://${cName}/edossier_websocket/socketServer?userId=${
          this.userId
        }&connectType=dossier`
        this.createWebSocket()
      })
    },
    createWebSocket() {
      try {
        this.ws = new WebSocket(this.ip)
        this.init(this)
      } catch (e) {
        console.log('catch')
        this.lockReconnect = false
        this.reconnect(this)
      }
    },
    init(that) {
      this.ws.onopen = () => {
        this.ws.send('hello')
        // 心跳检测
        this.heartCheckStart(that)
      }
      this.ws.onerror = function() {
        console.log('发生异常了')
      }
      this.ws.onmessage = evt => {
        console.log(evt)
        let data = JSON.parse(evt.data)
        if (evt) {
          if (data.msgType === 'MSG_TYPE_OCR') {
            this.params.pageNumber = 1
            this.loadData(this.params)
          }
          console.log(data)
        }
        console.log('数据已接收...')
        that.lockReconnect = true
        clearInterval(this.tt)
        // this.heartCheckSet()
      }
      this.ws.onclose = function() {
        // 关闭 websocket
        console.log('连接已关闭...')
        if (that) {
          that.lockReconnect = false
          that.reconnect(that)
        }
      }
      // 组件销毁时调用，中断websocket链接
      this.over = () => {
        this.ws.close()
      }
      this.reconnect = () => {
        if (that.lockReconnect) {
          return
        }
        that.lockReconnect = true
        // 没连接上会一直重连，设置延迟避免请求过多
        clearInterval(this.tt)
        this.tt = setInterval(function() {
          that.createWebSocket()
          that.lockReconnect = false
        }, 3000)
      }
      this.heartCheckStart = that => {
        this.timeoutObj = setInterval(function() {
          that.ws.send('HeartBeat')
        }, that.timeout)
      }
      this.heartCheckSet = () => {
        clearInterval(this.timeoutObj)
        this.heartCheckStart(that)
      }
    },
    closeUpload(done) {
      this.$confirm('是否关闭?', '', {
        confirmButtonText: '关闭',
        cancelButtonText: '取消',
        center: true,
        showClose: false
      })
        .then(() => {
          this.isUploaderList = false
          done()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: `已取消关闭`
          })
        })
    },
    uploadFile() {
      // this.isUploaderList = true
      let objData = {
        docType: 'JZ',
        docSubType: this.activeName === 'first' ? 'ZJJ' : 'WSJ',
        caseNumber: this.caseObj.caseNumber,
        userId: localCache.getItem('userId')
      }
      eDossier.SelectFilesToPDFAndUpload(objData)
    },
    deleteFile() {
      let delFlag = true
      this.tableSelection.forEach(item => {
        if (item.status === 'SEND_OCR') {
          this.$message({
            type: 'warning',
            message: '正在OCR识别中的文件不允许被删除'
          })
          delFlag = false
          return false
        }
      })
      if (delFlag) {
        this.del(
          '文件',
          '/api/4static/dossier/management/deleteOcrFile',
          '删除',
          'post',
          this.tableSelection.map(row => row.id)
        )
      }
    },
    searchData(keyValue) {
      this.params.originalDocName = keyValue
      this.params.pageNumber = 1
      this.loadData(this.params)
    },
    addMyFile() {
      this.add('文件夹', '/yofcloudDisk/api/repos/insertDir', '新建', 'put')
    },
    add(name, url, operation, type, keyValue, inputPattern, inputErrorMessage) {
      // eslint-disable-next-line
      let regex = /^[\u4e00-\u9fa5a-zA-Z0-9\-\_\(\)]{1,40}$/
      if (inputPattern != null) {
        regex = inputPattern
      }
      let msg = '只能输入数字、字母、中文和-_()，长度在40个字符以内！'
      if (inputErrorMessage != null) {
        msg = inputErrorMessage
      }
      this.$prompt(`请输入${name}`, `${operation}${name}`, {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: keyValue,
        confirmButtonClass: 'loading',
        inputPattern: regex,
        inputType: 'text',
        inputErrorMessage: `${name}格式不正确，${msg}`
      })
        .then(({ value }) => {
          this.fullscreenLoading = true
          let data
          if (name === '文件夹' && operation === '新建') {
            data = {
              repoId: this.params.repoId,
              p: this.params.p + '/' + value,
              operation: 'mkdir'
            }
          }
          addActions(type, url, data)
            .then(res => {
              if (res.data.code === '200') {
                this.tableSelection = []
                this.$message({
                  type: 'success',
                  message: `${name}${operation}成功`
                })
                this.fullscreenLoading = false
                this.loadData(this.params)
              }
            })
            .catch(() => {
              this.fullscreenLoading = false
            })
        })
        .catch(() => {
          this.fullscreenLoading = false
          this.$message({
            type: 'info',
            message: '取消输入'
          })
        })
    },
    del(name, url, operation, type, params) {
      this.$confirm(`确定要${operation}此${name}吗？`, '', {
        confirmButtonText: '删除',
        confirmButtonClass: 'danger',
        cancelButtonText: '取消',
        center: true,
        showClose: false
      })
        .then(() => {
          this.fullscreenLoading = true
          addActions(type, url, params)
            .then(res => {
              if (res.data.code === '200') {
                this.fullscreenLoading = false
                this.$message({
                  type: 'success',
                  message: `${name}${operation}成功!`
                })
                this.tableSelection = []
                this.loadData(this.params)
              }
            })
            .catch(() => {
              this.fullscreenLoading = false
            })
        })
        .catch(() => {
          this.fullscreenLoading = false
          this.$message({
            type: 'info',
            message: `已取消${operation}`
          })
        })
    },
    handleSelectionChange(val) {
      this.tableSelection = val
    },
    download() {
      checkOcrFileIsExist(
        this.tableSelection.map(row => row.id).join(';') + ''
      ).then(res => {
        if (res.data.code === '200') {
          if (res.data.data.isExist) {
            if (this.tableSelection.length === 1) {
              let link = document.createElement('a')
              link.style.display = 'none'
              link.href = `/${this.tableSelection[0].doubleDeckGroup}/${
                this.tableSelection[0].doubleDeckFile
              }`
              link.setAttribute(
                'download',
                `${this.tableSelection[0].ocrFileName}.pdf`
              )
              document.body.appendChild(link)
              link.click()
            } else {
              window.location = `/api/4static/dossier/management/downLoadFileListInZip?ids=${this.tableSelection
                .map(row => row.id)
                .join(',')}`
            }
          } else {
            this.$message({
              type: 'warning',
              message: res.data.msg
            })
          }
        } else {
          this.$message({
            type: 'warning',
            message: res.data.msg
          })
        }
      })
    }
  },
  filters: {
    dotStatus(value) {
      switch (value) {
        case 'DOCUMENT_ANALYSIS_COMPLETION':
          return 'success'
        case 'FAIL':
          return 'danger'
      }
    },
    fileSuffix(value) {
      switch (value) {
        case 'pdf':
          return '#icon-pdfwenjian'
        case 'PDF':
          return '#icon-pdfwenjian'
        case 'docx':
          return '#icon-wordwenjian'
        case 'doc':
          return '#icon-wordwenjian'
        case 'JPG':
          return '#icon-tupianwenjian'
        case 'jpg':
          return '#icon-tupianwenjian'
        default:
          return '#icon-tupianwenjian'
      }
    }
  },
  destroyed() {
    this.ws.onclose()
    clearInterval(this.tt)
    clearInterval(this.timeoutObj)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.dossier {
  background: #fff;
}
.dossier-header {
  height: 48px !important;
  padding: 0;
}
.dossier-header .el-tabs__item {
  height: 48px;
  /* color: #333; */
  line-height: 48px;
  font-weight: 600;
}
.dossier-header .el-tabs__nav-wrap::after {
  height: 1px;
}
.yofc-tab .el-tabs__nav {
  transform: translateX(20px) !important;
}
.data-tables-server-css {
  margin-top: 20px;
}
.data-tables-server-css .el-pagination__sizes {
  margin: 0;
}
.data-tables-server-css .el-table th {
  background: #f2f2f2;
}
.data-tables-server-css .el-pagination__total {
  float: left;
}
.data-tables-server-css .el-pagination {
  padding: 10px 0;
}
.data-tables-server-css .icon {
  font-size: 18px !important;
}
.data-tables-server-css .sc-table .pagination-wrap {
  text-align: right;
}
.data-tables-server-css .el-pagination .el-select .el-input {
  margin: 0 0 0 5px;
}
.dot-status {
  display: inline-block;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #e6a23c;
  vertical-align: top;
  margin-top: 8px;
  margin-right: 3px;
}
.dot-status.success {
  background: #67c23a;
}
.dot-status.danger {
  background: #f56c6c;
}
.file-search {
  text-align: right;
}
</style>



