<template>
    <div class="download-list">
        <ul>
          <li v-for="(item, index) in downloadList" :key="index">
              <download :options="item"></download>
          </li>
        </ul>
        <div v-if="downloadList.length == 0" class="no-result">
            <img src="../assets/img/no_result.png" alt="">
            <p style="text-align: center;">该列表暂无数据！</p>
        </div>
    </div>
</template>
<script>
import Download from './Download'
export default {
  props: ['downloadList'],
  components: { Download }
}
</script>
<style scoped>
.download-list {
  position: relative;
  padding: 8px 16px;
}
.download-list > ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.download-list ul li {
  background-color: #f5f5f5;
  margin-top: 8px;
}
</style>



