import axios from 'axios'
import store from '../store/store'
import { Base64 } from 'js-base64'
import { Message, MessageBox } from 'element-ui'

function _login() {
  var yofcCloudUser = localCache.getItem('yofcCloudUser')
  var cloudUser = JSON.parse(yofcCloudUser)
  if (!cloudUser) {
    const h = window.vm.$createElement
    let userName, password
    MessageBox({
      title: '请重新登录',
      message: h('div', null, [
        h('h4', null, '用户名'),
        h('el-input', {
          attrs: {
            placeholder: '请输入用户名'
          },
          on: {
            input: function (value) {
              userName = value
            }
          }
        }),
        h('h4', null, '密码'),
        h('el-input', {
          attrs: {
            placeholder: '请输入密码'
          },
          on: {
            input: function (value) {
              password = value
            }
          }
        })
      ]),
      showCancelButton: true,
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      beforeClose: (action, instance, done) => {
        if (action === 'confirm') {
          instance.confirmButtonLoading = true
          instance.confirmButtonText = '执行中...'
          setTimeout(() => {
            done()
            setTimeout(() => {
              instance.confirmButtonLoading = false
            }, 300)
          }, 3000)
        } else {
          done()
        }
      }
    }).then(action => {
      let params = {
        anotherName: userName,
        password: Base64.encode('cloudDisk' + password)
      }
      axios
        .post('/yofcloudDisk/api/login/toLogin', params)
        .then(res => {
          if (res.data.code === 200) {
            Message({
              type: 'success',
              message: '登录成功'
            })
            localCache.setItem(
              'yofcCloudUser',
              JSON.stringify({
                userName: userName,
                password: Base64.encode('cloudDisk' + password)
              })
            )
            localCache.setItem('sessionId', res.data.data.sessionId)
            localCache.setItem('token', res.data.data.token)
            localCache.setItem('userId', res.data.data.id)
            localCache.setItem('seafileUrl', res.data.data.seafileUrl)
            localCache.setItem('isStaff', res.data.data.isStaff)
            store.commit('LoginOut')
            window.vm.$router.go(0)
          } else {
            store.commit('LoginOut')
          }
        })
        .catch(res => {
          store.commit('LoginOut')
        })
    })
    return false
  } else {
    let isMsg = Message({
      type: 'error',
      message: '正在重连...'
    })
    axios
      .post('/yofcloudDisk/api/login/toLogin', {
        anotherName: cloudUser.userName,
        password: cloudUser.password
      })
      .then(res => {
        if (res.data.code === 200) {
          isMsg.close()
          Message({
            type: 'success',
            message: '重连成功'
          })
          localCache.setItem('sessionId', res.data.data.sessionId)
          localCache.setItem('token', res.data.data.token)
          localCache.setItem('userId', res.data.data.id)
          localCache.setItem('seafileUrl', res.data.data.seafileUrl)
          localCache.setItem('isStaff', res.data.data.isStaff)
          store.commit('LoginOut')
          window.vm.$router.go(0)
        } else {
          isMsg.close()
          store.commit('LoginOut')
        }
      })
      .catch(res => {
        store.commit('LoginOut')
        isMsg.close()
        if (res.response.data.code === 400) {
          MessageBox.alert('密码已被修改，请重新登录', '', {
            confirmButtonText: '确定',
            showClose: false,
            center: true,
            callback: action => {
              try {
                eDossier.ClientMainForm_ExchangeNo()
              } catch (error) { }
            }
          })
        }
      })
  }
}
// import store from '@/store'
// import { getToken } from '@/utils/auth'

// create an axios instance
const service = axios.create({
  // baseURL: process.env.BASE_API // api的base_url
  // timeout: 5000 // request timeout
})

// request interceptor
service.interceptors.request.use(
  config => {
    // Do something before request is sent
    // if (store.getters.token) {
    // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
    //   config.headers['X-Token'] = getToken()
    // }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// respone interceptor
service.interceptors.response.use(
  response => response,
  error => {
    if (error.response.status === 401) {
      if (!store.state.isLogin) {
        store.commit('LoginIn')
        _login()
      }
      return Promise.reject(error)
    }
    console.log('err' + error) // for debug
    const defaultErrorMsg = error.config.url.indexOf('yofcloudDisk') >= 0 ? '与数据交互平台服务中断' : '与服务器连接中断，请联系管理员'
    Message({
      message: error.response.data
        ? error.response.data.message || error.response.data.msg || defaultErrorMsg : defaultErrorMsg,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



