import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    isLogin: false
  },
  mutations: {
    LoginIn(state) {
      state.isLogin = true
    },
    LoginOut(state) {
      state.isLogin = false
    }
  }
})



