<template>
  <div ref="uploader-list" class="ym-uploader-list">
    <header v-drag class="uploader-list-header">
      <span>正在上传{{fileLists.length}}项</span>
      <span class="uploader-list-header-btns">
        <span v-if="shrink" class="btn-shrink" title="收缩列表" @click="shrinkFileList"><i class="iconfont">&#xe7c1;</i></span>
        <span v-else class="btn-shrink" title="展开列表" @click="shrinkFileList"><i class="iconfont">&#xe7c2;</i></span>
        <span class="btn-close" title="关闭" @click="closeFileList"><i class="iconfont">&#xe6da;</i></span>
      </span>
    </header>
    <el-scrollbar>
      <slot :file-list="fileList">
        <ul ref="uploader-list-content" class="uploader-list-scrollbar" v-show="!shrink">
          <li v-for="file in fileList" :key="file.id">
            <uploader-file :file="file" :list="true"></uploader-file>
          </li>
        </ul>
      </slot>
    </el-scrollbar>
  </div>
</template>

<script>
import UploaderFile from './file.vue'

const COMPONENT_NAME = 'uploader-list'

export default {
  props: ['title', 'fileLists'],
  data() {
    return {
      shrink: false,
      height: 0,
      show: true
    }
  },
  name: COMPONENT_NAME,
  directives: {
    drag(el, bindings) {
      el.onmousedown = function(e) {
        const container = el.offsetParent
        // 获取鼠标点击处分别与div左边和上边的距离：鼠标位置-div位置
        const divx = e.clientX - container.offsetLeft
        const divy = e.clientY - container.offsetTop
        const width = document.documentElement.clientWidth
        const height = document.documentElement.clientHeight
        // 包含在onmousedown里，表示点击后才移动，为防止鼠标移出div，使用document.onmousemove
        document.onmousemove = function(e) {
          // 获取移动后div的位置：鼠标位置-divx/divy
          var l = e.clientX - divx
          var t = e.clientY - divy
          if (l < 0) {
            l = 0
          }
          if (l + container.clientWidth > width) {
            l = width - container.clientWidth
          }
          if (t < 0) {
            t = 0
          }
          if (t + container.clientHeight > height) {
            t = height - container.clientHeight
          }
          container.style.left = l + 'px'
          container.style.top = t + 'px'
          // 清除移动div过程中选中的dom
          window.getSelection().removeAllRanges()
        }
        document.onmouseup = function(e) {
          document.onmousemove = null
          document.onmouseup = null
        }
      }
    }
  },
  computed: {
    fileList() {
      return this.fileLists
    }
  },
  components: {
    UploaderFile
  },
  mounted: function() {
    this.$nextTick(function() {
      const container = this.$refs['uploader-list']
      const divx = document.documentElement.clientWidth - container.clientWidth
      const divy =
        document.documentElement.clientHeight - container.clientHeight
      container.style.left = divx + 'px'
      container.style.top = divy + 'px'
      this.height = container.clientHeight - 48
    })
  },
  methods: {
    shrinkFileList() {
      this.shrink = !this.shrink
      this.refreshListPosition()
    },
    refreshListPosition() {
      const container = this.$refs['uploader-list']
      const contentDom = this.$refs['uploader-list-content']
      let height = 0
      // 由展开转收缩
      if (this.shrink) {
        height = contentDom.clientHeight
      }
      this.$nextTick(function() {
        if (!this.shrink) {
          height = -contentDom.clientHeight
        }
        container.style.top = container.offsetTop + height + 'px'
      })
    },
    closeFileList() {
      if (this.fileLists.length > 0) {
        this.$confirm(
          `当前还有上传任务未完成，退出将取消上传，是否继续？`,
          '',
          {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            cancelButtonClass: 'cancel',
            confirmButtonClass: 'danger',
            center: true,
            showClose: false,
            customClass: 'arDialog',
            closeOnClickModal: false
          }
        )
          .then(() => {
            this.$root.eventHub.$emit('clearYmFileList')
          })
          .catch(() => {})
      } else {
        this.$root.eventHub.$emit('clearYmFileList')
      }
    }
  }
}
</script>

<style lang="scss">
.ym-uploader-list {
  position: fixed;
  width: 480px;
  border-radius: 4px;
  box-shadow: -2px 4px 8px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  z-index: 999;

  .uploader-list-header {
    height: 48px;
    line-height: 48px;
    background: #5d6469;
    color: #fff;
    padding-left: 20px;
    border-radius: 4px 4px 0 0;
    cursor: move;

    .uploader-list-header-btns {
      float: right;

      .btn-shrink {
        margin-right: 16px;
        cursor: pointer;
      }

      .btn-clear {
        margin-right: 16px;
        cursor: pointer;
      }

      .btn-close {
        margin-right: 20px;
        cursor: pointer;
      }
    }
  }

  .uploader-list-scrollbar {
    list-style: none;
    padding: 0;
    margin: 0;
    height: 300px;
    background-color: #fff;
  }
}
</style>



