<template>
  <div class="el-tabs-wrap">
    <el-tabs v-model="activeName">
        <el-tab-pane label="unicode引用" name="first">
          <iframe class="iconfont-doc-wrap" src="static/iconfont-doc/demo_unicode.html"></iframe>
        </el-tab-pane>
        <el-tab-pane label="font-class引用" name="second">
          <iframe class="iconfont-doc-wrap" src="static/iconfont-doc/demo_fontclass.html"></iframe>
        </el-tab-pane>
        <el-tab-pane label="symbol引用" name="third">
          <iframe class="iconfont-doc-wrap" src="static/iconfont-doc/demo_symbol.html"></iframe>
        </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: 'Doc',
  data() {
    return {
      activeName: 'first'
    }
  }
}
</script>

<style lang="scss" scoped>
.iconfont-doc-wrap {
  border: none;
  display: block;
  width: 100%;
  height: 700px;
}
</style>



