<template>
  <div id="tweakUp">
    <el-row>
      <el-col :span="12">
          <!-- <div class="grid-content bg-purple ed-group" @click="jump(1)" :class="[lxFlag?'':'disable']"> -->
          <div class="grid-content bg-purple ed-group" @click="jump(1)">
             <img src="../assets/img/icon-lxjy.png" alt="">
             <p>量刑建议系统</p>
          </div>
      </el-col>
      <el-col :span="12">
          <!-- <div class="grid-content bg-purple-light ed-group" @click="jump(2)" :class="[ypFlag?'':'disable']"> -->
          <div class="grid-content bg-purple-light ed-group" @click="jump(2)">
             <img src="../assets/img/icon-sjjhpt.png" alt="">
              <p>数据交换平台</p>
          </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
          <div class="grid-content bg-purple ed-group" @click="jump(3)">
             <img src="../assets/img/icon-zdscjz.png" alt="">
              <p>卷宗自动编目</p>
          </div>
      </el-col>
      <el-col :span="12">
          <div class="grid-content bg-purple-light ed-group" @click="jump(4)">
             <img src="../assets/img/icon-fltwss.png" alt="">
              <p>法律条文搜索</p>
          </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
          <div class="grid-content bg-purple ed-group" @click="jump(5)">
             <img src="../assets/img/icon-znjc.png" alt="">
              <p>卷宗智能检测</p>
          </div>
      </el-col>
      <el-col :span="12">
          <div class="grid-content bg-purple" style="height:223px;cursor:text;border-bottom:1px solid #ddd;">
              <!-- <img src="../assets/img/11111.png" alt="">
              <p>智能检测图片清晰度</p> -->
          </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { fetchRepoList } from '@/api/admin'

export default {
  name: 'tweakUp',
  data() {
    return {
      msg: '全能助手'
      // 量刑建议flag
      // lxFlag: true,
      // 云盘plag
      // ypFlag: true
    }
  },
  mounted: function() {
    this.$nextTick(() => {
      window.receive = type => {
        this.showData(type)
      }
    })
  },
  methods: {
    showData(type) {
      if (type === 1) {
        // this.lxFlag = true
      } else if (type === 2) {
        // this.ypFlag = true
      }
    },
    jump(type) {
      if (type === 1) {
        // if (this.lxFlag) {
        eDossier.ShowForm_SentencingRecommendation()
        // this.lxFlag = !this.lxFlag
        // }
      } else if (type === 2) {
        // if (this.ypFlag) {
        // 当用户密码被改变后，登录失效，跳转云盘界面会白屏，为处理这种情况，跳转前先发一个到云盘的请求，判断登录是否失效，失效则跳转到登录界面
        const repoPromise = fetchRepoList(
          '/yofcloudDisk/api/group/queryGroupsByUser',
          {}
        )
        repoPromise
          .then(res => {
            eDossier.ShowForm_CloudDisk()
            // this.ypFlag = !this.ypFlag
          })
          .catch(error => {
            // 401时走重新登录过程
            if (error.response.status !== 401) {
              // this.ypFlag = !this.ypFlag
              eDossier.ShowForm_CloudDisk()
            }
          })
        // }
      } else if (type === 3) {
        eDossier.ShowForm_DossierCreate()
      } else if (type === 4) {
        eDossier.ShowForm_LawSearch()
      } else if (type === 5) {
        eDossier.ShowForm_DetecBlur()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#tweakUp {
  line-height: 1.5;
  letter-spacing: -0.15px;
  color: #666;
  .ed-group {
    text-align: center;
    border-bottom: 1px solid #ddd;
    cursor: pointer;
    height: 223px;
    &:hover {
      background-color: #f1f8ff;
    }
    p {
      margin: 0 0 20px;
      color: #666;
    }
    img {
      margin-top: 60px;
    }
    &.disable {
      cursor: text;
      background-color: white;
    }
  }

  .el-col:nth-child(odd) {
    text-align: center;
    border-right: 1px solid #ddd;
  }
}
</style>



