<template>
  <div class="common-file-list" v-bind:class="{active: fileListVisible}">
    <el-card class="box-card" shadow="never" body-style="padding:0">
      <div slot="header" class="clearfix">
        <span>
          <el-button size="mini" :class="{active:showUpload}" @click="showUpload=true,showUploadError = false">正在上传</el-button>
          <el-button size="mini" :class="{active:!showUpload && !showUploadError}" @click="showUpload=false,showUploadError = false">正在下载</el-button>
          <!-- <el-button size="mini" :class="{active:showUploadError}" @click="showUploadError=true,showUpload=false">上传失败</el-button> -->
        </span>
        <span style="float:right">
          <span class="tab-btn-icon" @click="close">
            <i class="iconfont icon-guanbizhu" title="关闭"></i>
          </span>
        </span>
      </div>
      <div>
        <upload-file-list :uploadList="uploadList" v-show="showUpload"/>
        <download-list :downloadList="downloadList" v-show="!showUpload  && !showUploadError"></download-list>
        <!-- <upload-error-list :uploaderrorList="uploaderrorList" v-show="!showUpload && showUploadError"></upload-error-list> -->
      </div>
    </el-card>
  </div>
</template>

<script>
import UploadFileList from '../components/UploadFileList'
import DownloadList from '../components/DownloadList'
// import UploadErrorList from '../components/UploadErrorList'
export default {
  props: ['fileListVisible', 'uploadList', 'downloadList'],
  name: 'common-file-list',
  components: { UploadFileList, DownloadList },
  data() {
    return {
      showUpload: true
    }
  },
  methods: {
    close() {
      this.$root.eventHub.$emit('closeCommonFileList')
    }
  }
}
</script>

<style lang="scss">
body {
  overflow: hidden;
  position: fixed;
}
html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.common-file-list {
  position: absolute;
  left: 0;
  top: 100%;
  height: 100%;
  width: 100%;
  background: #fff;
  transition: top 0.5s;
  z-index: 12;

  .clearfix {
    height: 60px;
    line-height: 60px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .el-card {
    border: none;
  }

  .el-card__header {
    padding: 0;
  }

  .el-button:hover {
    border-color: #42a0f8;
    color: #42a0f8;
    background-color: transparent;
  }

  .el-button.active {
    color: #fff;
    border-color: #42a0f8;
    background-color: #42a0f8;
  }

  .tab-btn-icon {
    cursor: pointer;
    padding-right: 6px;
  }
  .tab-btn-icon:hover {
    color: #42a0f8;
  }
  .tab-btn-icon i {
    font-size: 26px;
  }
}

.common-file-list.active {
  top: 0;
}
</style>



