<template>
  <div class="upload-list">
      <slot :file-list="uploadList">
      <ul>
        <li v-for="(file, index) in uploadList" :key="index">
          <upload-file :file="file" :list="true"></upload-file>
        </li>
      </ul>
      </slot>
      <div v-if="uploadList.length == 0" class="no-result">
          <img src="../assets/img/no_result.png" alt="">
          <p style="text-align: center;">该列表暂无数据！</p>
      </div>
  </div>
</template>

<script>
import UploadFile from './UploadFile.vue'

const COMPONENT_NAME = 'upload-file-list'

export default {
  props: ['title', 'uploadList'],
  name: COMPONENT_NAME,
  components: {
    UploadFile
  }
}
</script>

<style scoped>
.upload-list {
  position: relative;
  padding: 8px 16px;
}
.upload-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.upload-list ul li {
  background-color: #f8f8f8;
  margin-top: 8px;
}
</style>



