<template>
  <div class="case-list-container" v-loading="loading">
     <div class="ed-search-group">
        <input type="text" placeholder="输入案件名称搜索" v-model="searchKeyWord" maxlength="40" @keyup.enter="getCaseList(1, searchKeyWord)" @input="getCaseList(1, searchKeyWord)">
        <i class="icon iconfont ed-clear-button" @click="clearInput()" v-show="searchKeyWord.length>0">&#xe6cf;</i>
        <i class="icon iconfont ed-search-button">&#xe6e8;</i>
        <el-button size="small" type="primary" class="sync-btn" @click="syncCase"><i class="iconfont">&#xe6ee;</i><span>同步</span></el-button>
    </div>
    <el-scrollbar class="case-list-scrollbar">
    <!-- <div class="case-list-scrollbar"> -->
      <div class="search-result">
        <el-row class="case-list-item" :case-index="index" v-for="(item, index) in caseList" :key="item.case_info_id"  @click.native="openCaseManage(item)">
          <div class="item-col left case-list-item-time">
            <div class="item-col-two date year">{{item.case_acceptance_time | getYear}}</div>
            <div class="item-col-two date">{{item.case_acceptance_time | getDate}}</div>
          </div>
          <div class="item-col left case-list-item-content">
            <div class="item-col-two case-name" :title="item.case_name">{{item.case_name}}</div>
            <div class="item-col-two case-type">
              <label>{{item.case_type | caseTypeFilter}}:</label>
              <span :title="item.case_crime_type">{{item.case_crime_type}}</span>
            </div>
          </div>
          <div v-show="false" class="item-col right">案件受理中</div>
        </el-row>
        <div class="block" style="clear:both;margin-top:20px;" v-if="caseList.length > 0">
          <span>共 {{totalRow}} 条</span>
          <el-pagination
              small
              background
              @current-change="getCaseList(currentPageIndex, searchKeyWord)"
              :current-page.sync="currentPageIndex"
              :page-size="pageSize"
              layout="prev, pager, next,jumper"
              :total="totalRow">
          </el-pagination>
        </div>
        <div v-if="caseList.length == 0" class="no-result">
            <img src="../assets/img/no_result.png" alt="">
            <p style="text-align: center;">该列表暂无数据！</p>
        </div>
      </div>
    <!-- </div> -->
    </el-scrollbar>
  </div>
</template>

<script>
import { getAjaxPromise } from '../api/admin'
import { Base64 } from 'js-base64'
import { Message } from 'element-ui'
function padDate(value) {
  return value < 10 ? '0' + value : value
}
export default {
  name: 'caseList',
  data() {
    return {
      msg: '案件列表',
      searchKeyWord: '',
      currentPageIndex: 1,
      pageSize: 10,
      totalRow: 0,
      caseList: [],
      loading: false
    }
  },
  created() {
    this.getCaseList(1, '')
    window.caseListReceive = str => {
      this.caseListReceive(str)
    }
    this.$root.eventHub.$on('showUploadList', this.log)
  },
  filters: {
    getYear(value) {
      const date = new Date(value)
      const year = padDate(date.getFullYear())
      return year
    },
    getDate(value) {
      const date = new Date(value)
      const month = padDate(date.getMonth() + 1)
      const day = padDate(date.getDate())
      return `${month}.${day}`
    },
    caseTypeFilter(value) {
      return value === '审查逮捕案件' ? '移送案由' : '移诉案由'
    }
  },
  methods: {
    log(params) {
      console.log(params)
    },
    // 刷新案件列表
    refreshCaseList() {
      this.currentPageIndex = 1
      this.searchKeyWord = ''
      this.getCaseList(this.currentPageIndex, this.searchKeyWord)
    },
    // 获取案件列表
    getCaseList(currentPageIndex, caseName) {
      this.loading = true
      const getCaseList = getAjaxPromise({
        method: 'post',
        url: '/api/4static/caseList/getCasePageList',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded'
        },
        params: {
          currentPageIndex,
          pageSize: this.pageSize,
          userId: localCache.getItem('userId'),
          caseName
        }
      })
      getCaseList
        .then(res => {
          this.loading = false
          if (res.data.code === 0) {
            this.caseList = res.data.list
            this.currentPageIndex = res.data.pageNumber
            this.totalRow = res.data.totalRow
          }
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 重置input框
    clearInput() {
      this.searchKeyWord = ''
      this.getCaseList(1, this.searchKeyWord)
    },
    openCaseManage(caseInfo) {
      // const caseRepoId = getAjaxPromise({
      //   method: 'get',
      //   url: '/yofcloudDisk/api/user/getUserCaseRepoId'
      // })
      // caseRepoId.then(res => {
      let data = {
        caseNumber: caseInfo.case_number,
        caseName: caseInfo.case_name
        // repoId: res.data.data.repoId
      }
      localCache.setItem('caseNumber', caseInfo.case_number)
      localCache.setItem('caseName', caseInfo.case_name)
      // localCache.setItem('repoId', res.data.data.repoId)
      localCache.setItem('caseFlowType', caseInfo.case_type)
      eDossier.OpenCase(data)
      // })
    },
    // 同步案件
    syncCase() {
      eDossier.synchronizationSynchronization()
      // this.caseListReceive()
    },
    // 同步案件回调
    caseListReceive(str) {
      // str =
      //   'eyJjdXJDYXNlTnVtYmVyIjoiNDIwMTE0MjAxODAwMzUzMzMzIiwiY3VyQ2FzZU5hbWUiOiJmeHfmtYvor5XmoYjku7YiLCJjYXNlRmxvd1R5cGUiOiLlrqHmn6XpgK7mjZXmoYjku7YiLCJwbGF0Rm9ybUlzUnVuIjp0cnVlfQ=='
      const result = Base64.decode(str)
      const json = JSON.parse(result)
      let curCaseName = json.curCaseName // 案件名称
      const curCaseNumber = json.curCaseNumber // 受案号
      const curCaseType = json.caseFlowType // 案件类型
      const platFormIsRun = json.platFormIsRun // 统一平台运行状态
      const caseAcceptanceTime = json.strCaseAcceptDate
      const caseCrimeType = json.strCaseCrimeType
      if (!(curCaseNumber && curCaseName)) {
        Message({
          message: '在统一业务平台进入案卡详情时才可以同步案卡数据',
          type: 'warning'
        })
        return false
      } else {
        if (!curCaseType) {
          Message({
            message: '该案件类型未配置',
            type: 'warning'
          })
          return false
        }
      }
      if (!platFormIsRun) {
        Message({
          message:
            '小助手启动和统一平台从案件列表进入到案卡详情时，才可以同步数据',
          type: 'warning'
        })
        return false
      }

      curCaseName = curCaseName.substring(0, 80)
      // 查询案件类型是否受支持
      this.getCaseFlowType(
        curCaseName,
        curCaseNumber,
        curCaseType,
        caseAcceptanceTime,
        caseCrimeType
      )
      console.log(json)
    },
    // 获取支持的案件类型
    getCaseFlowType(
      curCaseName,
      curCaseNumber,
      curCaseType,
      caseAcceptanceTime,
      caseCrimeType
    ) {
      this.loading = true
      var data = {
        classify: 'ws',
        code: 'case_flow_type'
      }
      const caseFlowPromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/seaFileCaseNumberRelation/getCaseFlowType',
        data
      })

      caseFlowPromise
        .then(res => {
          if (res.data.status === 'success') {
            if (res.data.data) {
              const getCaseFlowTypeList = []
              Object.keys(res.data.data).forEach(key => {
                getCaseFlowTypeList.push(res.data.data[key].cName)
              })

              if (getCaseFlowTypeList.indexOf(curCaseType) === -1) {
                this.loading = false
                Message({
                  message: '不支持当前案件类型',
                  type: 'warm'
                })
              } else {
                // 案件类型支持时进行同步案件
                this.caseSynchronize(
                  curCaseName,
                  curCaseNumber,
                  curCaseType,
                  caseAcceptanceTime,
                  caseCrimeType
                )
              }
            }
          } else {
            this.loading = false
            Message({
              message: '查询案件类型失败',
              type: 'warm'
            })
          }
        })
        .catch(res => {
          this.loading = false
          Message({
            message: res.response.statusText,
            type: 'error'
          })
        })
    },
    // 案件同步
    caseSynchronize(
      curCaseName,
      curCaseNumber,
      curCaseType,
      caseAcceptanceTime,
      caseCrimeType
    ) {
      // 同步案件
      const caseSynchronizePromise = getAjaxPromise({
        method: 'post',
        url: '/api/4static/caseList/caseSynchronize',
        headers: {
          'Content-type': 'application/json'
        },
        data: JSON.stringify({
          userId: localCache.getItem('userId'),
          caseNumber: curCaseNumber,
          caseName: curCaseName,
          caseType: curCaseType,
          caseAcceptanceTime,
          caseCrimeType
        })
      })

      caseSynchronizePromise
        .then(res => {
          if (res.data.status === 'success') {
            // 向云盘获取案件文件夹列表
            const getCaseFilePromise = getAjaxPromise({
              url: '/yofcloudDisk/api/repos?type=case'
            })
            return getCaseFilePromise
          } else {
            this.loading = false
            Message({
              message: res.data.msg,
              type: 'warm'
            })
            return false
          }
        })
        .then(res => {
          const index = res.data.data.findIndex(
            caseDir => caseDir.name === curCaseName
          )
          if (index < 0) {
            // 案件文件夹不存在，需要新建
            const dirPathPromise = getAjaxPromise({
              method: 'get',
              url: '/yofcloudDisk/api/user/getUserCaseRepoId'
            })
            // 获取案件库路径
            return dirPathPromise
          } else {
            this.loading = false
            // 案件文件夹存在，不需要新建
            Message({
              message: '同步成功',
              type: 'success'
            })
            // 刷新案件列表
            this.refreshCaseList()
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            // 获取案件库路径成功，可以新建案件文件夹
            const insertDirPromise = getAjaxPromise({
              method: 'put',
              url: '/yofcloudDisk/api/repos/insertDir',
              data: {
                operation: 'mkdir',
                p: `/${curCaseName}`,
                repoId: res.data.data.repoId
              }
            })
            // 新建案件文件夹
            return insertDirPromise
          } else {
            this.loading = false
          }
        })
        .then(res => {
          this.loading = false
          if (res.data.code === 200) {
            Message({
              message: '同步成功',
              type: 'success'
            })
            // 刷新案件列表
            this.refreshCaseList()
          } else {
            this.loading = false
            Message({
              message: res.data.message,
              type: 'warm'
            })
          }
        })
        .catch(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style>
.case-list-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.case-list-container {
  height: calc(100% - 48px);
  line-height: 1.5;
  letter-spacing: -0.15px;
  background: #f5f5f5;
  color: #666;

  .el-upload-list {
    display: none !important;
  }

  .ed-search-group {
    position: relative;
    padding: 20px 16px 0 16px;

    input {
      float: left;
      width: calc(100% - 142px) !important;
      height: 30px;
      line-height: 30px;
      padding-left: 30px;
      padding-right: 20px;
      background-color: transparent;
      border: 1px solid #ddd;
      border-radius: 2px;
      color: #666;
      &:focus {
        border-color: #42a0f8;
        outline: none;
      }
      &:hover {
        border-color: #42a0f8;
        outline: none;
      }
    }

    input::-webkit-input-placeholder {
      color: #ccc;
    }

    .ed-clear-button {
      font-size: 22px;
      position: absolute;
      top: 22px;
      right: 108px;
      cursor: pointer;
      &:hover {
        color: #42a0f8;
      }
    }

    .ed-search-button {
      height: 2em;
      position: absolute;
      left: 26px;
      top: 26px;
      font-size: 14px;
    }

    .sync-btn {
      float: right;
      padding: 8px 12px;
      font-size: 100%;

      .iconfont {
        padding-right: 8px;
      }
    }
  }

  .case-list-scrollbar {
    width: 100%;
    position: relative;
    height: calc(100vh - 115px);

    .search-result {
      clear: both;
      padding: 16px 16px 0 16px;

      div[case-index='0'] {
        margin-top: 0;
      }

      .case-list-item {
        margin-top: 16px;
        background-color: #fff;
        position: relative;
        cursor: pointer;

        .item-col {
          height: 100%;
          height: 64px;
          line-height: 64px;

          .item-col-two {
            height: 32px;
            line-height: 32px;
          }

          .item-col-two.case-type {
            color: #999;
            vertical-align: middle;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            max-width: 260px;
            line-height: 26px;
          }

          .item-col-two.case-name {
            cursor: pointer;
            vertical-align: middle;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            max-width: 260px;
            color: #333;
            line-height: 39px;
          }

          .item-col-two.date {
            background-color: #409eff;
            color: #fff;
            text-align: center;
          }

          .item-col-two.date.year {
            height: 32px;
            line-height: 32px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.35);
          }
        }

        .case-list-item-time {
          width: 64px;
        }

        .item-col.left {
          float: left;
        }

        .item-col.right {
          float: right;
        }

        .case-list-item-content {
          padding-left: 10px;
        }
      }

      .case-list-item:hover {
        box-shadow: 4px 4px 8px rgba(157, 157, 157, 0.35);
      }

      .el-pagination {
        width: auto;
        float: right;
        font-weight: 400;
      }
      .no-result {
        text-align: center;
        margin-top: 200px;
        img {
          margin-bottom: 10px;
        }
        p {
          margin: 0;
        }
      }
    }
  }
  .el-loading-mask {
    top: 100px;
  }
}
</style>



