<template>
    <div class="download-file" :status="status">
        <div class="download-file-progress" :style="progressStyle"></div>
        <div class="download-file-info">
          <div class="download-file-img">
            <svg class="icon" aria-hidden="true">
              <use v-bind:xlink:href="fileCategory"></use>
            </svg>
          </div>
          <div class="download-file-content">
            <div class="download-file-name">{{options.fileName}}</div>
            <div class="download-file-size">{{getFileSize}}</div>
          </div>
          <div class="download-file-percent">
            <span v-show="status === 'error'">下载失败</span>
            <span v-show="status !== 'error'">{{progressStyle.progress}}</span>
          </div>
          <div class="download-file-actions">
            <span class="download-file-remove" title="取消下载" @click="remove()"></span>
          </div>
        </div>
    </div>
</template>
<script>
export default {
  props: ['options'],
  data() {
    return {
      status: 'downloading',
      formatedSize: '',
      progress: 1,
      total: 0,
      xhr: null,
      extension: ''
    }
  },
  computed: {
    progressStyle() {
      const progress = Math.floor(this.progress * 99)
      const style = `translateX(${Math.floor(progress - 99)}%)`
      return {
        progress: `${progress}%`,
        webkitTransform: style,
        mozTransform: style,
        msTransform: style,
        transform: style
      }
    },
    getFileSize() {
      const total = this.total
      let currentSize
      let size
      let unit
      if (total < 1024) {
        unit = 'Kb'
        size = total / 1024
        currentSize = (total * this.progress) / 1024
      } else {
        unit = 'Mb'
        size = total / (1024 * 1024)
        currentSize = (total * this.progress) / (1024 * 1024)
      }
      currentSize = currentSize.toFixed(1)
      size = size.toFixed(1)
      return `${currentSize} / ${size} ${unit}`
    },
    fileCategory() {
      const iconMap = {
        image: 'icon-tupianwenjian',
        video: 'icon-shipinwenjian',
        audio: 'icon-yinpinwenjian',
        folder: 'icon-wenjianjia',
        doc: 'icon-wordwenjian',
        txt: 'icon-textwenjian',
        excel: 'icon-excelwenjian',
        pdf: 'icon-pdfwenjian',
        ppt: 'icon-pptwenjian',
        zip: 'icon-yasuobaowenjian'
      }
      const extension = this.getExtension()
      let type = 'unknown'
      const typeMap = {
        image: ['gif', 'jpg', 'jpeg', 'png', 'bmp', 'webp'],
        video: ['mp4', 'm3u8', 'rmvb', 'avi', 'swf', '3gp', 'mkv', 'flv'],
        audio: ['mp3', 'wav', 'wma', 'ogg', 'aac', 'flac'],
        doc: ['doc', 'docx'],
        txt: ['txt'],
        excel: ['xls', 'xlsx'],
        pdf: ['pdf', 'epub'],
        ppt: ['ppt', 'pptx'],
        zip: ['zip', 'rar', '7z']
      }
      Object.keys(typeMap).forEach(_type => {
        const extensions = typeMap[_type]
        if (extensions.indexOf(extension) > -1) {
          type = _type
        }
      })
      const icon = iconMap[type] || 'icon-qitawenjianhuoweizhiwenjian'
      return `#${icon}`
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.download()
    })
    this.$root.eventHub.$on('downloadReStart', this.download)
  },
  methods: {
    getExtension() {
      const filename = this.options.fileName
      var startIndex = filename.lastIndexOf('.')
      if (startIndex !== -1) {
        return filename.substring(startIndex + 1, filename.length).toLowerCase()
      } else {
        return ''
      }
    },
    download() {
      const url = this.options.url
      if (!url) {
        return
      }
      const filename = this.options.fileName
      const xhr = new XMLHttpRequest()
      xhr.open('get', url, true)
      // 监听进度事件
      xhr.addEventListener('progress', this.onProgress, false)
      xhr.addEventListener('error', this.onError, false)

      xhr.responseType = 'blob'
      xhr.setRequestHeader(
        'Content-Type',
        'application/x-www-form-urlencoded; charset=UTF-8'
      )
      xhr.onreadystatechange = () => {
        if (xhr.readyState === 4 && xhr.status === 200) {
          // Chrome version
          var link = document.createElement('a')
          link.href = window.URL.createObjectURL(xhr.response)
          link.download = filename
          link.click()
          this.onSuccess()
        }
      }

      xhr.send()
      this.xhr = xhr
    },
    onProgress(progressEvent) {
      this.total = progressEvent.total || progressEvent.loaded
      this.progress = progressEvent.loaded / this.total
    },
    onError() {
      this.status = 'error'
      this.progress = 1
      this.$message({
        message: '下载失败',
        type: 'error',
        duration: 3 * 1000
      })
    },
    onSuccess() {
      this.status = 'success'
      this.$root.eventHub.$emit('downloadSuccess', this.options)
    },
    pause() {},
    resume() {},
    remove() {
      const message = this.error
        ? '是否删除？'
        : '当前任务未完成，是否取消下载？'
      this.$confirm(message, '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        cancelButtonClass: 'cancel',
        confirmButtonClass: 'danger',
        center: true,
        showClose: false,
        customClass: 'arDialog'
      })
        .then(() => {
          this.xhr.abort()
          this.$root.eventHub.$emit('removeItemFromDownloadList', this.options)
        })
        .catch(() => {})
    }
  }
}
</script>
<style lang="scss" scoped>
.download-file {
  position: relative;
  height: 60px;
  line-height: 60px;
  overflow: hidden;

  .download-file-progress {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #e2eeff;
    transform: translateX(-100%);
  }
  &[status='error'] {
    .download-file-progress {
      background: #ffe0e0;
    }
  }
  .download-file-progressing {
    transition: all 0.4s linear;
  }
  .download-file-info {
    position: relative;
    z-index: 1;
    height: 100%;
    overflow: hidden;
  }
  .download-file-info:hover {
    background-color: rgba(240, 240, 240, 0.2);
  }
  .download-file-info i,
  .download-file-info em {
    font-style: normal;
  }
  .download-file-name,
  .download-file-size,
  .download-file-meta,
  .download-file-status,
  .download-file-actions {
    float: left;
    position: relative;
    height: 100%;
  }
  .download-file-size {
    width: 13%;
    text-indent: 10px;
  }
  .download-file-name {
    width: 45%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-indent: 14px;
  }
  .download-file-img {
    position: relative;
    float: left;

    svg {
      width: 46px;
      height: 46px;
      position: relative;
      top: 8px;
    }
  }

  .download-file-content {
    float: left;
    position: relative;
    height: 100%;
    width: 60%;
    left: -5px;
  }

  .download-file-name {
    width: 100%;
    height: 20px;
    line-height: 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-indent: 14px;
    margin-top: 14px;
  }

  .download-file-size {
    width: 100%;
    height: 20px;
    line-height: 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-indent: 14px;
  }
  .download-file-percent {
    float: left;
    position: relative;
    width: 15%;
  }
  .download-file-actions {
    float: right;
    position: relative;
    width: 12%;

    span {
      float: right;
      position: relative;
      width: 16px;
      height: 16px;
      margin-top: 22px;
      margin-right: 8px;
      cursor: pointer;
      background: url('data:image/png;base64,')
        no-repeat 0 0;

      &:hover {
        background-position-x: -21px;
      }
    }

    .download-file-remove {
      background-position-y: -34px;
    }
  }
}
</style>



