<template>
<div class="pdf-row">
  <div class="pdf-col-20">
      <div class="pdf-content">
          <div class="pdf-title" v-if="this.pdfList.length>0">
            <span v-show="this.pdfList.length>0">{{jzRoot}}</span>
            <div class="pad-action" :disabled="this.pdfList.length <=1">
                <el-button type="text"  :disabled="this.pdfList.length <=1" @click="padAction('up')">上一文件</el-button>
                <el-button type="text"  :disabled="this.pdfList.length  <=1" @click="padAction('down')">下一文件</el-button>
            </div>
          </div>
          <iframe v-if="this.pdfList.length>0" id="padView"  :src="pdfUrl">
          </iframe>
          <div v-if="this.pdfList.length===0" class="pdf-no-div">
            <div class="pdf-no"> <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-pdfwenjian"></use>
          </svg>
          <div class="pdf-no-text">暂无PDF数据</div>
          </div></div>
      </div>
      </div>
  <div  class="pdf-col-4"><div class="jz-content" @mouseleave="getLeave()">
    <ul class="jz-list">
        <li v-for="(item,index) in jsList" :key="item.id" @click="getTreeData(item,index)" :class="{'isActive':isActive == index}">
        <span>
          <i :title="item.name">
          <svg class="icon" aria-hidden="true">
          <use :xlink:href="item.icon"></use>
          </svg>
          </i>
          <span class="icon-name">{{item.name}}</span>
        </span>
      </li>
    </ul>
    <transition :name="'vux-pop-' + (isActive !=-1&&isPopover ? 'in' : 'out')">
      <div class="jz-popover pdf-body-wrap" v-show="isActive !=-1&&isPopover" v-loading="fullscreenLoading">
                <el-tree v-if="treeData.length>0" ref="tree"  :data="treeData" :props="defaultProps" @node-click="handleNodeClick" node-key="id"
              default-expand-all
              highlight-current
             :render-content="renderContent">
                </el-tree>
            <div v-if="treeData.length==0" class="pdf-no-result">
              <img src="../assets/img/no_result.png" alt="">
              <p style="text-align: center;">该列表暂无数据！</p>
            </div>
      </div>
    </transition>
    </div></div>

    <!-- 标记弹窗 begin-->
    <el-dialog
      v-on:close="dialogCloseEvent"
      :visible.sync="dialogVisible"
      width="550px"
      center
      :before-close="handleClose"
      @open="initEvidenceForm">
      <div class="yofc-dialog-header" slot="title">
        <h1>{{dialogTitle}}</h1>
      </div>
      <el-form label-position="top" ref="evidenceForm" :rules="evidenceFormRules" class="evidence-mark-form" :model="evidenceForm">
        <el-alert v-if="evidenceForm.errorMessage"
          :title="evidenceForm.errorMessage"
          type="error"
          @close="evidenceForm.errorMessage=''"
          show-icon>
        </el-alert>
        <!--瑕疵或非法证据-->
        <el-form-item label="">
          <p>非法或瑕疵证据</p>
          <el-radio-group v-model="evidenceForm.type">
            <el-radio :title="item.cName" v-for="item in evidenceForm.preTypeList" :label="item.cName" :key="item.id" @change="changeEvidenceType(item)"></el-radio>
          </el-radio-group>
        </el-form-item>
        <!--证据非法描述-->
        <el-form-item label="" v-if="evidenceForm.preDescList.length" class="custom-el-form-item__content">
          <el-checkbox-group v-model="evidenceForm.desc">
            <div class="label-wrap" v-for="item in evidenceForm.preDescList" :key="item.improperEvidenceCheckboxId">
            <!-- <el-tooltip :content="item.improperEvidenceCheckboxName" placement="right"> -->
              <el-checkbox  :label="item.improperEvidenceCheckboxName"  name="evidenceDesc" :title="item.improperEvidenceCheckboxName"></el-checkbox>
            <!-- </el-tooltip> -->
            </div>
          </el-checkbox-group>
        </el-form-item>

        <el-form-item label="证据内容" prop="supplement" style="margin-bottom:0">
          <el-input type="textarea" rows="3" v-model="evidenceForm.supplement"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false;evidenceForm.errorMessage=''">取 消</el-button>
        <el-button size="small" type="primary"  @click="addEvidenceMark()">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 标记弹窗 end-->
</div>
</template>
<script>
import {
  getDossierFileListByCaseNumber,
  FilesExist,
  getImproperEvidenceType,
  getEvidenceDescByType,
  addAndModifyImproperEvidence
} from '@/api/root'
import EE from 'events'
import _ from 'lodash'

let switchPdfByOcrfileIdEventCb
let doEditAsyncCb
let doCopyAsyncCb
let doMarksAsyncCb

export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  computed: {
    pdfUrl() {
      if (this.pdfList.length === 0) {
        return `${this.hostname}static/generic/web/viewer.html?file=`
      }
      return `${this.hostname}static/generic/web/viewer.html?file=${
        this.defaultUrl
      }`
    }
  },
  name: 'previewPdf',
  data() {
    return {
      hostname:
        location.hostname !== 'localhost'
          ? '/edossier_h5_case_manage/'
          : '../../',
      defaultUrl: '',
      defaultId: '',
      jzRoot: '',
      docSubtype: 'ZJJ',
      fullscreenLoading: false,
      isPadAction: false,
      jsList: [
        {
          name: '原始卷宗',
          id: 0,
          eName: 'originalFileMap',
          icon: '#icon-yuanshijuanzong'
        },
        {
          name: '卷宗智能分类',
          id: 1,
          eName: 'evidenceClassifyFileMap',
          icon: '#icon-juanzongzhinengfenlei'
        },
        {
          name: '内部文书',
          id: 2,
          eName: 'uploadedWSFileMap',
          icon: '#icon-neibuwenshu'
        }
      ],
      pdfList: [],
      isPopover: false,
      isActive: -1,
      treeData: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      // 证据标记弹窗是否显示
      dialogVisible: false,
      // 证据标记弹窗标题
      dialogTitle: '证据标记',
      // 证据标记表单相关
      evidenceForm: {
        errorMessage: '',
        // 证据类型供选择数据
        preTypeList: null,
        // 证据类型
        type: '1',
        // 证据描述供选择的数据
        preDescList: [],
        // 证据描述,复选框选择，因此必须用一个数组
        desc: [],
        treeName: 'originalFileMap',
        // 补充说明
        supplement: ''
      },
      // 证据标记表单验证规则
      evidenceFormRules: {
        supplement: [
          {
            max: 2048,
            message: '补充说明内容超出长度2048个字符限制',
            trigger: 'blur'
          },
          { required: true, message: '请输入证据内容', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    dialogCloseEvent() {
      // console.log('close')
      this.evidenceForm = {
        errorMessage: '',
        // 证据类型供选择数据
        preTypeList: null,
        // 证据类型
        type: '1',
        // 证据描述供选择的数据
        preDescList: [],
        // 证据描述,复选框选择，因此必须用一个数组
        desc: [],
        treeName: 'originalFileMap',
        // 补充说明
        supplement: ''
      }
      // this.initEvidenceForm()
    },
    // 接受ocrFileId,展示对应的pdf
    // 场景一：事实认定中，某个证据的摘录来源（可以有多个），查看操作需要连接到具体的pdf
    // 场景二: 证据分析中，风险提示里列出的每条风险，可能是因为缺少卷宗的情况产生的风险；一般是根据
    // 具体卷宗分析出的风险，及风险来源对应具体pdf（可以有多个），查看操作时需要连接到具体的pdf
    // 场景三：证据分析中，瑕疵或非法证据（事先根据pdf文档做的摘录，因此一定来自于左侧的pdf列表中的某一个）,
    // 查看操作需要连接到具体的pdf
    showExistPdfByOcrfileId(ocrFileIds) {
      let ocrFileId = Number(ocrFileIds)
      if (ocrFileId === this.defaultId) {
        if (this.pdfList.length === 0) {
          this.$message.error('未找到对应的pdf文件')
        } else {
          this.$message('查看的pdf即为当前显示的pdf')
        }
        return false
      }
      let index = this.pdfList.findIndex(item => item.id === ocrFileId)

      if (index < 0) {
        let indexs
        let flg = false
        if (this.docSubtype === 'ZJJ') {
          indexs = this.pdfListWSJ.findIndex(item => item.id === ocrFileId)
          flg = true
        } else {
          indexs = this.pdfListZJJ.findIndex(item => item.id === ocrFileId)
          flg = false
        }
        if (indexs < 0) {
          this.$message.error('未找到对应的pdf文件')
          return false
        } else {
          if (flg) {
            this.docSubtype = 'WSJ'
            this.pdfList = this.copyArr(this.pdfListWSJ)
          } else {
            this.docSubtype = 'ZJJ'
            this.pdfList = this.copyArr(this.pdfListZJJ)
          }
          this.filesExist(ocrFileId, indexs)
        }
        return false
      }
      this.filesExist(ocrFileId, index)
    },
    // 新增证据标记
    addEvidenceMark() {
      // 收集数据
      let caseMgtImproperEvidenceCheckboxVoList = []
      let that = this
      _.forEach(
        _.filter(this.evidenceForm.preDescList, o => {
          return _.includes(
            this.evidenceForm.desc,
            o.improperEvidenceCheckboxName
          )
        }),
        item => {
          let temp = {}
          temp.improperEvidenceCheckboxId = item.improperEvidenceCheckboxId
          temp.improperEvidenceCheckboxName = item.improperEvidenceCheckboxName
          temp.improperEvidenceType =
            this.evidenceForm.type === '1'
              ? undefined
              : caseMgtImproperEvidenceCheckboxVoList.length === 0
                ? undefined
                : this.improperEvidenceType
          caseMgtImproperEvidenceCheckboxVoList.push(temp)
        }
      )
      // 提交数据
      addAndModifyImproperEvidence({
        caseMgtImproperEvidenceCheckboxVoList:
          caseMgtImproperEvidenceCheckboxVoList.length === 0
            ? undefined
            : caseMgtImproperEvidenceCheckboxVoList,
        caseNumber: this.caseObj.caseNumber,
        evidenceAnalysisImproperId: this.evidenceForm
          .evidenceAnalysisImproperId,
        improperEvidenceContent: this.evidenceForm.supplement, // ---必填项
        improperEvidenceType:
          this.evidenceForm.type === '1'
            ? undefined
            : this.improperEvidenceType,
        ocrFileId: this.defaultId
      }).then(res => {
        if (res.data.code === '200') {
          // 通知列表刷新
          this.ee.emit('ImproperEvidenceAdded', this.improperEvidenceType)
          this.$message({
            type: 'success',
            message: '标记成功!'
          })
          this.evidenceForm.errorMessage = ''
          that.dialogVisible = false
        } else {
          this.evidenceForm.errorMessage = res.data.msg
        }
      })
    },
    // 初始化证据标记弹窗中的表单
    initEvidenceForm() {
      getImproperEvidenceType({
        code: 'improper',
        classify: 'evidence'
      }).then(res => {
        if (res.data.code === '200') {
          this.evidenceForm.preTypeList = res.data.data
        }
      })
    },

    // 选择不同的非正常证据类型
    changeEvidenceType(item) {
      this.improperEvidenceType = item.eName
      getEvidenceDescByType({
        improperEvidenceType: item.eName
      }).then(res => {
        if (res.data.code === '200') {
          this.evidenceForm.preDescList = res.data.data
        }
      })
    },
    getTreeData(item, index) {
      this.isActive = index
      this.isPopover = true
      this.treeName = item.eName
      this.treeData = this.treeDatas[item.eName].children
      if (item.eName === 'uploadedWSFileMap') {
        this.loadTreeData('uploadedWSFileMap')
      }
    },
    loadTreeData(eName) {
      if (eName !== 'uploadedWSFileMap') {
        this.pdfList = []
        this.pdfListWSJ = []
        this.pdfListZJJ = []
      }
      getDossierFileListByCaseNumber({
        caseNumber: this.caseObj.caseNumber
      }).then(res => {
        if (res.data.code === '200') {
          this.treeDatas = res.data.data
          this.treeData = res.data.data[eName].children
          this.traverseTree(res.data.data[eName])
          this.treeName = eName
        }
      })
    },
    traverseTree(node) {
      if (!node) {
        return
      }
      this.traverseNode(node)
      if (node.children && node.children.length > 0) {
        for (let i of node.children) {
          this.traverseTree(i)
        }
      }
    },
    traverseNode(node) {
      if (node.docSubtype === 'ZJJ' || node.docSubtype === 'WSJ') {
        if (node.docSubtype === 'ZJJ') {
          this.pdfListZJJ.push({
            url: `/${node.doubleDeckGroup}/${node.doubleDeckFile}`,
            label: node.label,
            id: node.id,
            docSubtype: node.docSubtype,
            originalType: node.fileSuffix
          })
        } else {
          this.pdfListWSJ.push({
            url: `/${node.doubleDeckGroup}/${node.doubleDeckFile}`,
            label: node.label,
            id: node.id,
            docSubtype: node.docSubtype,
            originalType: node.fileSuffix
          })
        }
        if (this.docSubtype === 'ZJJ') {
          if (this.pdfListZJJ.length > 0) {
            this.pdfList = this.copyArr(this.pdfListZJJ)
          } else {
            this.docSubtype = 'WSJ'
            this.pdfList = this.copyArr(this.pdfListWSJ)
          }
        } else {
          this.pdfList = this.copyArr(this.pdfListWSJ)
        }
        if (this.isPadAction) {
          this.padAction(this.isPadAction)
        }
        if (!this.defaultUrl) {
          this.defaultUrl = this.pdfList[0].url
          this.defaultId = this.pdfList[0].id
          this.jzRoot = this.pdfList[0].label
          window.jzRoot = this.pdfList[0].label
        }
      }
    },
    padAction(value) {
      if (this.pdfList.length === 0) {
        return false
      }
      this.isPadAction = value
      let index = this.pdfList.findIndex(item => item.id === this.defaultId)
      if (value === 'up') {
        //  上一页
        if (index === 0) {
          this.filesExist(
            this.pdfList[this.pdfList.length - 1].id,
            this.pdfList.length - 1
          )
        } else {
          this.filesExist(this.pdfList[index - 1].id, index - 1)
        }
      } else {
        //  下一页
        if (index >= this.pdfList.length - 1) {
          this.filesExist(this.pdfList[0].id, 0)
        } else {
          this.filesExist(this.pdfList[index + 1].id, index + 1)
        }
      }
    },
    filesExist(id, index) {
      this.fullscreenLoading = true
      FilesExist(id)
        .then(res => {
          this.fullscreenLoading = false
          if (res.data.code === '200') {
            if (res.data.data.isExist) {
              this.defaultUrl = this.pdfList[index].url
              this.defaultId = id
              this.isPadAction = null
              this.jzRoot = this.pdfList[index].label
            } else {
              if (this.isPadAction) {
                this.loadTreeData(this.treeName)
              } else {
                this.defaultUrl = ''
                this.isPadAction = null
                this.$message({
                  type: 'warning',
                  message: '选中的文件中已经不存在，将刷新，请再次选择'
                })
                this.loadTreeData(this.treeName)
              }
            }
          }
        })
        .catch(() => {
          this.fullscreenLoading = false
        })
    },
    renderContent(h, { node, data, store }) {
      if (data.fileSuffix) {
        let icon = this.$options.filters.fileSuffix(data.fileSuffix)
        return (
          <span title={node.label}>
            <span class="mr5">
              <svg class="icon" aria-hidden="true">
                <use xlinkHref={icon} />
              </svg>
            </span>
            <span>{node.label}</span>
          </span>
        )
      } else {
        return (
          <span title={node.label}>
            <span>{node.label}</span>
          </span>
        )
      }
    },
    copyArr(arr) {
      let newArr = []
      for (let a of arr) {
        newArr.push(a)
      }
      return newArr
    },
    handleNodeClick(data) {
      if (data.docSubtype === 'ZJJ' || data.docSubtype === 'WSJ') {
        let index = this.pdfList.findIndex(item => item.id === data.id)
        if (index >= 0) {
          this.filesExist(data.id, index)
        } else {
          this.docSubtype = data.docSubtype
          if (this.docSubtype === 'ZJJ') {
            this.pdfList = this.copyArr(this.pdfListZJJ)
          } else {
            this.pdfList = this.copyArr(this.pdfListWSJ)
          }
          let indexs = this.pdfList.findIndex(item => item.id === data.id)
          this.filesExist(data.id, indexs)
        }
      } else if (data.fileSuffix === 'docx' || data.fileSuffix === 'doc') {
        eDossier.openWord_WithOutBase64(
          JSON.stringify({ data: { docId: data.id + '' } })
        )
      }
    },
    getLeave() {
      this.isPopover = false
      this.isActive = -1
    },
    // 关闭证据标记弹窗处理器
    handleClose(done) {
      this.evidenceForm.errorMessage = ''
      done()
      // this.initEvidenceForm()
    }
  },
  watch: {
    jzRoot(newVal, oldVal) {
      window.jzRoot = this.jzRoot
    }
    // pdfList(newVal, oldVal) {
    //   if (newVal.length > 0) {
    //     if (!this.defaultUrl) {
    //       this.defaultUrl = newVal[0].url
    //       this.defaultId = newVal[0].id
    //       this.jzRoot = newVal[0].label
    //       window.jzRoot = newVal[0].label
    //     } else {
    //       let index = this.pdfList.findIndex(item => item.id === this.defaultId)
    //       if (index >= 0) {
    //         this.padAction(this.isPadAction)
    //       } else {
    //         this.defaultUrl = newVal[0].url
    //         this.defaultId = newVal[0].id
    //         this.jzRoot = newVal[0].label
    //         window.jzRoot = newVal[0].label
    //       }
    //     }
    //   }
    // }
  },
  mounted() {
    this.loadTreeData('originalFileMap')
    this.$nextTick(() => {
      if (window.eventEmitter) {
        this.ee = window.eventEmitter
      } else {
        this.ee = window.eventEmitter = new EE()
      }
      doMarksAsyncCb = data => {
        this.dialogTitle = '证据标记'
        this.dialogVisible = true
        this.evidenceForm.supplement = data
      }
      this.ee.addListener('doMarksAsync', doMarksAsyncCb)

      doCopyAsyncCb = data => {
        let originalType = this.pdfList[
          this.pdfList.findIndex(item => item.id === this.defaultId)
        ].originalType

        let docSubtype = this.pdfList[
          this.pdfList.findIndex(item => item.id === this.defaultId)
        ].docSubtype
        // 传递事件到案件处理step3中evidenceForm组件中
        this.ee.emit('getPdfNameFromCopyAction', {
          ocrFileName: this.jzRoot,
          ocrFileId: this.defaultId,
          originalFile: originalType,
          originalCateType: docSubtype,
          originalType: originalType,
          contentTxt: data
        })
      }
      this.ee.addListener('doCopyAsync', doCopyAsyncCb)

      doEditAsyncCb = data => {
        let tmpArr = []
        this.dialogTitle = '证据编辑'
        this.dialogVisible = true
        this.evidenceForm.type = data.improperEvidenceName
        this.changeEvidenceType({ eName: data.improperEvidenceType })
        _.forEach(data.caseMgtImproperEvidenceCheckboxVoList, o => {
          tmpArr.push(o.improperEvidenceCheckboxName)
        })
        this.evidenceForm.evidenceAnalysisImproperId =
          data.evidenceAnalysisImproperId
        this.evidenceForm.desc = tmpArr
        this.evidenceForm.supplement = data.improperEvidenceContent
      }
      this.ee.addListener('doEditAsync', doEditAsyncCb)

      switchPdfByOcrfileIdEventCb = data => {
        this.showExistPdfByOcrfileId(data)
      }
      // 监听事件：根据传入不同的ocrFileId,切换对应的pdf
      this.ee.addListener(
        'switchPdfByOcrfileIdEvent',
        switchPdfByOcrfileIdEventCb
      )
    })
  },
  components: {},
  filters: {
    fileSuffix(value) {
      switch (value) {
        case 'pdf':
          return '#icon-pdfwenjian'
        case 'PDF':
          return '#icon-pdfwenjian'
        case 'docx':
          return '#icon-wordwenjian'
        case 'doc':
          return '#icon-wordwenjian'
        case 'JPG':
          return '#icon-tupianwenjian'
        case 'jpg':
          return '#icon-tupianwenjian'
        case 'tif':
          return '#icon-tupianwenjian'
        case 'png':
          return '#icon-tupianwenjian'
        default:
          return '#icon-tupianwenjian'
      }
    }
  },
  destroyed() {
    this.ee.removeListener('doMarksAsync', doMarksAsyncCb)
    this.ee.removeListener('doCopyAsync', doCopyAsyncCb)
    this.ee.removeListener('doEditAsync', doEditAsyncCb)
    this.ee.removeListener(
      'switchPdfByOcrfileIdEvent',
      switchPdfByOcrfileIdEventCb
    )
  }
}
</script>

<style>
.pdf-no-result {
  text-align: center;
  margin: 100px auto;
}
.pdf-no {
  width: 100px;
  height: 100px;
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-left: -40px;
  margin-top: -40px; /* negative half of the height */
}
.pdf-no .icon {
  width: 60px;
  height: 60px;
}
.pdf-no .pdf-no-text {
  color: #333;
}
.pdf-no-div {
  height: 100%;
  width: 100%;
  border: 24px solid #fff;
  background: #eff3f6;
  padding: 10px;
  box-sizing: border-box;
  /* display: table; */
  position: relative;
  /* text-align: center; */
  /* margin: 100px auto; */
}
.pdf-body-wrap {
  height: 100%;
  box-sizing: border-box;
  overflow: auto;
}
.pdf-row {
  height: 100%;
  clear: both;
}
.pdf-col-4 {
  float: right;
  height: 100%;
  width: 119px;
}
.pdf-col-20 {
  width: calc(100% - 119px);
  float: left;
  height: 100%;
}
#padView {
  box-sizing: border-box !important;
  border-width: 0;
  width: 100%;
  height: calc(100% - 48px);
}
.pdf-title {
  line-height: 48px;
  box-sizing: border-box;
  height: 48px;
  padding-left: 20px;
  box-sizing: border-box;
  border-bottom: 1px solid #ddd;
  font-weight: 600;
  font-size: 14px;
  color: #333 !important;
}
.pdf-content {
  height: 100%;
  border-right: 1px solid #ddd;
  box-sizing: border-box;
}
.pad-action {
  float: right;
  margin-right: 20px;
}
.pad-action .el-button--text:hover {
  color: #66b1ff !important;
  /* text-decoration: underline; */
}
.pad-action .el-icon-arrow-up {
  margin-right: 10px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
}
.pad-action .el-icon-arrow-down {
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
}
.jz-list {
  overflow: hidden;
  list-style: none;
  padding: 0;
  margin: 0;
  border-bottom: 1px solid #ddd;
}
.jz-list li {
  text-align: center;
  height: 120px;
  line-height: 120px;
  color: #666;
  font-size: 14px;
  transition: color 0.15s linear;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin-right: -1px;
  margin-bottom: -1px;
  cursor: pointer;
}
.jz-list li:hover {
  background: #eff3f6;
}

.jz-list li span {
  display: inline-block;
  line-height: normal;
  vertical-align: middle;
  color: #666;
}
.jz-list li i {
  display: block;
  font-size: 32px;
  margin-bottom: 10px;
  color: #666;
}
.jz-list li .icon-name {
  display: inline-block;
  padding: 0 3px;
  color: #666;
}
.jz-list .isActive {
  background: rgb(66, 160, 248, 0.3) !important;
}
.jz-popover {
  position: absolute;
  left: 100%;
  top: 0;
  font-size: 14px;
  width: auto;
  height: 100%;
  max-height: 100%;
  min-width: 200px;
  background: #fff;
  z-index: 3;
  border: 1px solid #ddd;
  border-top: none;
  border-bottom: none;
  box-shadow: 4px 2px 10px 1px rgba(0, 0, 0, 0.1);
}
.jz-popover .icon {
  font-size: 18px !important;
}
.jz-popover .el-tree-node__content {
  height: 38px;
  padding-right: 10px;
}
.jz-content {
  position: relative;
  height: 100%;
}
.text-hover:hover {
  color: #42a0f8;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  transition: all 250ms;
  height: 100%;
  top: 0;
  position: absolute;
  backface-visibility: hidden;
  perspective: 1000;
}

.vux-pop-out-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}

.vux-pop-out-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}

.vux-pop-in-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}

.vux-pop-in-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
</style>
<style lang="scss" scoped>
.custom-el-form-item__content {
  //background-color: red;
  border: 1px solid #ddd;
  padding: 10px;
  max-height: 200px;
  overflow: auto;
  overflow-x: hidden;

  .el-checkbox-group {
    .label-wrap {
      height: 40px;
      line-height: 40px;
      label {
        margin: 0;
        display: inline-block;
        position: relative;
        max-width: calc(100% - 20px);
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }
  }
}
.evidence-mark-form {
  .el-form-item__content {
    p {
      margin-bottom: 5px;
    }
  }
  .el-form-item__content {
    line-height: 10px;
  }
  .el-form-item {
    margin-bottom: 10px;
  }
}
</style>



