<template>
    <el-input :placeholder="searchPlaceholder" v-model="keyValue" @keyup.enter.native="doEnterSearch" @keyup.8.native="backspaceSearch"  class="input-with-select cd-search" size="small">
        <i slot="suffix" title="搜索" class="el-input__icon el-icon-search cs-p" @click="doSearch()"></i>
    </el-input>
</template>
<style  lang="scss">
.el-select .el-input {
  width: 130px;
}
.cs-p {
  cursor: pointer;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.cd-search {
  .el-input__inner {
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    border-bottom-left-radius: 25px;
  }
}

.cd-search {
  width: 30% !important;
  float: right;
  margin-right: 20px;
}
</style>
<script>
export default {
  props: ['searchPlaceholder'],
  data() {
    return {
      keyValue: ''
    }
  },
  methods: {
    doSearch() {
      this.$emit('search', this.keyValue)
    },
    doEnterSearch(e) {
      this.$emit('search', this.keyValue)
    },
    backspaceSearch(e) {
      this.$emit('search', this.keyValue)
    }
  },
  created() {}
}
</script>



